/* harmony default export */ __webpack_exports__["a"] = ({"Spells":[{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Ablativum.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Antimagie","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Der Spruch erschafft eine unsichtbare Kuppel um den Magier herum, die sich mit ihm bewegt und darin Befindliches vor Zaubern schützt. Die Kuppel ist eigentlich eine Kugel, da jedoch der Magier meistens auf dem Boden steht, ist die Kugelform nur selten relevant. Die Kuppel verfügt über einen Radius von 3 Schritt und eine Schildstärke in Höhe von QS x 3. Wird ein freundlicher oder feindlicher Zauber gegen ein unter der Kuppel befindliches Ziel gerichtet, vermindert die Schildstärke dessen QS. Sinkt diese dadurch auf 0, tritt kein Effekt ein. Die Schildstärke wird jedoch im Rahmen dieses Prozesses verbraucht (–1 QS je aufgehaltener QS), und der Ablativum endet vorzeitig, sobald seine QS unter 1 fällt. Sollte ein Flächenzauber, beispielsweise der Ignisphaero, teilweise den Schild des Ablativum berühren, wird die Schildstärke um die QS des Flächenzaubers vermindert. Wirkt ein Zauber über mehrere KR, so wird die Schildstärke jede KR um die QS vermindert. Sollte ein Zauber mehr QS aufweisen als der Ablativum, dringt der Zauber mit den überzähligen QS durch den Schild. Die gesamten QS des Zaubers werden aber wie üblich von der Schildstärke abgezogen.","Publikation":"Aventurische Magie 114","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"1 Aktion","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Ablativum","spellextensions":{"0":["Stärkerer Schild ","(FW 8, 4 AP): Die Schildstärke beträgt QS x 4."],"1":["Großer Radius ","(FW 12, 8 AP): Der Radius beträgt 5 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP, die jedoch nicht die Schildstärke erhöhen."],"2":["Reichweite Berührung ","(FW 16, 12 AP): Als Reichweite gilt Berührung. Die Schildstärke beträgt nur QS x 2. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Blindheit.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"KL/IN/CH (modifiziert um SK)","Wirkung":"Das Ziel verliert schlagartig die Fähigkeit, seine Umgebung optisch wahrzunehmen, und es erhält dadurch den Status <em>Blind</em>.","Publikation":"Aventurische Magie 122","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"16 Aktionen","Verbreitung":"Hexen, Scharlatane"},"spellclass":"Zauberspruch","name":"Blindheit","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 3 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."],"1":["Sehschwierigkeiten ","(FW 12, 6 AP): Das Ziel hat auch noch QS Stunden nach dem Ende der Wirkungsdauer des Zaubers Sehschwierigkeiten. Alle Proben, die Augenlicht erfordern, sind um 1 erschwert."],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Es entstehen zusätzliche Kosten in Höhe von 8 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Zwingtanz.html","properties":{"Wirkungsdauer":"QS in KR","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"MU/KL/CH (modifiziert um SK)","Wirkung":"Durch den Zauberspruch ist das Opfer dazu gezwungen, unkontrolliert zu tanzen. Es bewegt sich dabei nicht weiter als QS Schritt vom Ort seiner Verzauberung weg. Dabei vermeidet es Stürze in Spalten, Abgründe oder ähnliche Gefahren. Während des Tanzeserhält es den Status Handlungsunfähig. Allerdings gilt das Opfer nicht als Liegend und es verfügt noch über seine normale GS, die allerdings ausschließlich beim Tanzen zum Einsatz kommt. Alle Attacken und Fernkampfangriffe gegen das Opfer sind aufgrund der unvorhersehbaren Bewegungen um 4 Punkte erschwert.","Publikation":"Die Streitenden Königreiche Seite 168","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Zwingtanz","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Zauberklinge.html","properties":{"Wirkungsdauer":"FP in Tage","Merkmal":"Objekt","Zielkategorie":"Objekt","Reichweite":"8 Schritt","Probe":"KL/IN/FF","Wirkung":"Mit diesem Ritual lassen sich arkane Kräfte in eine Waffe binden. Diese gilt dann als magische Waffe. Die Kosten des Rituals sind abhängig von der Größe der Waffe. Die Wirkungsdauer lässt sich auf QS x 30 Jahre erhöhen. In diesem Falle verfünffachen sich die AsP-Kosten, wobei 1/20 hiervon permanent verloren gehen.","AsP-Kosten":"abhängig von der Waffe (nicht modifizierbar)","Publikation":"Regelwerk Seite 301","Steigerungsfaktor":"C","Verbreitung":"allgemein","Ritualdauer":"8 Stunden"},"spellclass":"Ritual","name":"Zauberklinge Geisterspeer","spellextensions":{"0":["Verkürzte Ritualdauer ","(FW 8, 3 AP): Die Ritualdauer beträgt 4 Stunden."],"1":["Trägerwahl ","(FW 12, 6 AP): Die Waffe gilt nur dann als magisch, wenn sie von Personen geführt wird, die der Zauberer bestimmt hat."],"2":["Hass auf … ","(FW 16, 9 AP): Gegen eine bestimmte Art von Wesen richtet die Waffe +1 TP an. Der Typus des Wesens muss vor der Verzauberung ausgewählt werden (Dämonen, Untote, Menschen usw.). Die Kosten des Rituals steigen dadurch um 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_BlickaufsWesen.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Zielkategorie":"Wesen","Reichweite":"8 Schritt","Probe":"KL/IN/CH (modifiziert um SK)","Wirkung":"Dem Zauberer werden die Werte des Ziels offenbart. Er kann pro erreichter QS eine Kategorie auswählen, die für ihn offengelegt wird: Körperliche Eigenschaften, LE und ZK, Kampftechniken, Körperliche Vor- und Nachteile, Körperliche Sonderfertigkeiten, Kampfsonderfertigkeiten, aktuell vorhandene Zustände und Status, Körpertalente.","Publikation":"Aventurische Magie 121","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Elfen, Scharlatane"},"spellclass":"Zauberspruch","name":"Blick aufs Wesen","spellextensions":{"0":["Geistige Eigenschaften ","(FW 8, 1 AP): Als weitere Kategorien stehen Geistige Eigenschaften, AE und SK, Zauber, Geistige Vor- und Nachteile, Geistige Sonderfertigkeiten und Magische Sonderfertigkeiten sowie Wissenstalente zur Auswahl."],"1":["Zwei Ziele ","(FW 12, 2 AP): Der Zauberer kann bis zu 2 Ziele gleichzeitig einschätzen. Das zweite Ziel kostet 2 AsP extra."],"2":["Vier Ziele ","(FW 16, 3 AP): Der Zauberer kann bis zu 4 Ziele gleichzeitig einschätzen. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_Zaubermelodie.html","properties":{"Merkmal":"Heilung","Steigerungsfaktor":"B","Talent":"Musizieren oder Singen","Probe":"MU/IN/CH","Wirkung":"Spielt der Elf ungestört eine Stunde lang diese Melodie, kann er sich in einen tranceartigen Zustand versetzen, in dem er sich in Einklang mit den astralen Strömungen der Umgebung bringen kann. Er erwacht nach einer halben Stunde aus der Trance. Wenn er aus der Trance erwacht, sind für ihn alle Proben auf Zaubersprüche und Rituale bis zum nächsten Sonnenaufgang erleichtert. Bei QS 1-3 um 1, bei QS 4-6 um 2","Publikation":"Regelwerk Seite 284","AsP-Kosten":"8 AsP","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Zaubermelodie","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/fgregregreg.html","properties":{"Merkmal":"Keines","Schutzkreis":"Niedere Dämonen können den Kreis nicht betreten.","AP-Wert":"10 Abenteuerpunkte","Publikation":"Regelwerk Seite 266","AsP-Kosten":"4 AsP","Bannkreis":"Der erste niedere Dämon, der sich dem Kreis auf 7 Schritt nähert, wird in ihn hineingezogen und kann ihn während der Wirkungsdauer nicht mehr verlassen.","Verbreitung":"Gildenmagier"},"spellclass":"Bann und Schutzkreis","name":"Bann- und Schutzkreis gegen niedere Dämonen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Zungelaehmen.html","properties":{"Wirkungsdauer":"QS x 3 in Stunden","Merkmal":"Einfluss","Probe":"KL/IN/CH (modifiziert durch ZK)","Wirkung":"Zunge und Lippen des Opfers sind gelähmt. Es kann daher nicht mehr artikuliert sprechen. Dies beeinflusst auch Zaubersprüche. Außerdem neigen so Verfluchte zu Speichelfluss. Der Verfluchte erhält den Status Stumm.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Zunge lähmen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_BandundFessel.html","properties":{"Wirkungsdauer":"QS x 2 Stunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"KL/CH/KK (modifiziert um SK)","Wirkung":"Das Ziel ist während der Wirkungsdauer nicht dazu in der Lage, sich weiter als 7 Schritt von dem Ort zu entfernen, an dem es sich am Ende der Zauberdauer aufhält. Wird es mit Gewalt herausgedrängt, erhält es solange 1 Stufe des Zustands <em>Schmerz </em>pro KR, bis es wieder in den Wirkungsbereich zurückkehrt.","Publikation":"Aventurische Magie 121","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"16 Aktionen","Verbreitung":"Druiden, Elfen, Hexen"},"spellclass":"Zauberspruch","name":"Band und Fessel","spellextensions":{"0":["Zwei Personen ","(FW 8, 2 AP): Der Zauber betrifft bis zu 2 Personen gleichzeitig. Für die zweite Person entstehen nach der ersten entstehen zusätzliche Kosten in Höhe von 2 AsP."],"1":["Vier Personen ","(FW 12, 4 AP): Der Zauber betrifft bis zu 4 Personen gleichzeitig. Pro weiterer Person nach der ersten entstehen zusätzliche Kosten in Höhe von 2 AsP. Voraussetzung: Zaubererweiterung "],"2":["Acht Personen ","(FW 16, 6 AP): Der Zauber betrifft bis zu 8 Personen gleichzeitig. Pro weiterer Person nach der ersten entstehen zusätzliche Kosten in Höhe von 2 AsP. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Zauberfeder.html","properties":{"Wirkungsdauer":"10 Minuten","Merkmal":"Telekinese","Reichweite":"1 Schritt","Zielkategorie":"Objekte (Federkiele)","Publikation":"Aventurische Magie 114","Wirkung":"Dieser Trick verzaubert einen Federkiel, sodass dieser alles wortgetreu auf einem bereitgelegten Papier oder in ein Buch notiert, was der Zauberer sagt. Der Federkiel kann die Seiten des Buches umschlagen und taucht sich selbst in ein Tintenfäßchen ein, solange es maximal einen Schritt weit entfernt ist.","Verbreitung":"Gildenmagier (Punin)"},"spellclass":"Zaubertrick","name":"Zauberfeder","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/zeichenderangst.html","properties":{"Merkmal":"Keines","AP-Wert":"8 Abenteuerpunkte","Wirkung":"Das Zeichen der Angst kann an einen Baum oder einen Felsen aufgetragen werden. Man muss nicht wissen, was das Zeichen ist, man muss es lediglich sehen können, damit es seine Wirkung entfaltet. Mit Ausnahme des Erschaffers des Zeichens haben alle Personen, die das Zeichen sehen und sich in einem Radius von 8 Schritt aufhalten, ein mulmiges Gefühl und wollen den Ort so schnell wie möglich verlassen. Jeder Betroffene muss eine Probe auf <em>Willenskraft (Bedrohungen standhalten)</em> ablegen, bei deren Misslingen er 1 Stufe <em>Furcht </em>erleidet. Das Zeichen ist aber nicht stark genug, um jemanden direkt davon abzuhalten, sich dem Zeichen zu nähern.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"16 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Zeichen der Angst","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Blick_in_die_Gedanken.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","Zielkategorie":"Kulturschaffende, übernatürliche Wesen","Reichweite":"4 Schritt","Probe":"MU/KL/IN (modifiziert um SK)","Wirkung":"Der Zaubernde kann die Gedanken des Verzauberten lesen. Er kann nur erkennen, was der Verzauberte gerade denkt, nicht jedoch gezielt in seinen Erinnerungen forschen. Der Verzauberte kann mit einer Erfolgsprobe auf Sinnesschärfe erschwert um QS des Zaubers entdecken, dass etwas Fremdes in seine Gedanken blickt. Mit einer um QS der Zauberprobe erschwerte Probe auf Willenskraft kann er gezielt Gedanken mit irreführenden Informationen aussenden oder seine Gedanken mit nutzlosem Durcheinander, inneren Monologen oder Gesang füllen.","Publikation":"Regelwerk Seite 289","AsP-Kosten":"8 AsP (Aktivierung des Zaubers), 4 AsP pro 30 Sekunden","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Blick in die Gedanken","spellextensions":{"0":["Kampfhandlung vorhersehen ","(FW 8, 3 AP): Im Kampf kann der Zauberer Handlungen des verzauberten Gegners vorausahnen. Die Verteidigung des Zauberers ist gegen diesen Gegner um 1 erhöht. Dabei gelten 5 Sekunden der Wirkungsdauer als 1 KR."],"1":["Unbemerkt ","(FW 12, 6 AP): Die "],"2":["Keine Aufrechterhaltung ","(FW 16, 9 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Tiersinne.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","AP-Wert":"10 Abenteuerpunkte","Wirkung":"Der Vertraute leiht seiner Hexe eine spezielle Art der Wahrnehmung, abhängig von der Tierart des Vertrauten. Katzen können so eine Stufe Dunkelsicht und Herausragender Sinn (Gehör) verleihen, Raben  Herausragender Sinn (Sicht), Kröten Herausragender Sinn (Geruch). Die Tiersinne funktionieren wie die  entsprechenden Vorteile. Der Vertraute muss während der gesamten Wirkungsdauer Körperkontakt zur Hexe halten.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"1 AsP pro 5 Minuten","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Tiersinne","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_UngesehenerBeobachter.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Einfluss","AP-Wert":"35 Abenteuerpunkte","Wirkung":"Der Vertraute wird Auge und Ohr der Hexe. Sie kann wahrnehmen, was er wahrnimmt. Um den Kontakt aufrechtzuerhalten, muss die Hexe mit geschlossenen Augen verweilen und sich auf ihren Vertrauten konzentrieren. Die Verbindung wirkt nur in eine Richtung, die Hexe kann ihrem Vertrauten daher keine Aufträge erteilen. Die Reichweite der Verbindung beträgt sieben Meilen. Sollte der Vertraute verletzt werden, erleidet die Hexe den gleichen Schaden wie der Vertraute. Der so miterlebte Tod eines Vertrauten kann eine Hexe stark traumatisieren.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"1 AsP pro 10 Minuten","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Ungesehener Beobachter","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Zwiegespraech.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Einfluss","AP-Wert":"beherrschen Vertrautentiere von Anfang an","Wirkung":"Der Vertraute übermittelt Bilder, Gefühle und Erfahrungen telepathisch an die Hexe, so lange beide Körperkontakt aufrechterhalten. Da Vertrautentiere die menschliche Sprache begrenzt verstehen, kann sich so ein Zwiegespräch entwickeln, bei dem die Hexe spricht und der Vertraute telepathisch antwortet.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"1 AsP pro 10 Minuten","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Zwiegespräch","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_ZauberspeicherdesAdepten.html","properties":{"Merkmal":"Objekt","AP-Wert":"20 Abenteuerpunkte","Volumen":"7 Punkte","Wirkung":"Der Zauberer kann einen Zauberspruch wirken, der maximal 8 AsP kosten darf, und ihn in seinen Stab fließen lassen. Der Zauberer kann den Zauberspruch dann mittels 1 Aktion aus dem Stab heraus wirken, vorausgesetzt, dass ein gültiges Ziel vorhanden ist. Er muss dabei weder Formel noch Geste des Zauberspruchs einsetzen und erleidet für deren Weglassen auch keinerlei Erschwernisse. Wird der gespeicherte Zauber eingesetzt, wird auf die Eigenschaften 14/14/14 gewürfelt. Der FW entspricht dabei den FP des gespeicherten Zaubers. Der Zauberspeicher des Adepten kann keine Rituale speichern, und er kann immer nur einen einzigen Zauberspruch beinhalten.","Publikation":"Aventurische Magie Seite 25","AsP-Kosten":"keine (außer die des gespeicherten Zauberspruchs)","Voraussetzungen":"Merkmalsfokus für den zu speichernden Zauber","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Zauberspeicher des Adepten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/FluchderSchlaflosigkeit.html","properties":{"Wirkungsdauer":"QS x 2 in Tagen","Merkmal":"Einfluss","Probe":"KL/IN/CH (modifiziert durch SK)","Wirkung":"Das Opfer kann nicht schlafen. Wenn es doch für einige Sekunden einnickt, wird es von Alpträumen wieder aufgeschreckt. Regeneration ist in diesem Zustand nicht möglich. Der Schlafentzug macht das Opfer  zudem  fahrig,  reizbar  und  es  erleidet  nach  einigen Tagen Sinnestäuschungen. Pro zwei Tagen ohne Schlaf erhält das Opfer eine Stufe <em>Verwirrung</em>, die erst abklingt, wenn es wieder schlafen kann.","Publikation":"Die Streitenden Königreiche Seite 170","AsP-Kosten":"8 AsP","Verbreitung":"Druiden"},"spellclass":"Herrschaftsritual","name":"Fluch der Schlaflosigkeit","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/MachtueberSchlafwandler.html","properties":{"Wirkungsdauer":"QS Stunden","Merkmal":"Einfluss","Probe":"MU/KL/IN (modifiziert durch SK)","Wirkung":"Der Druide verbrennt einen Haufen Misteln, die  er  selbst  gepflückt  hat  und  gibt  die  körperliche Komponente des Opfers in das Feuer. Das nächste Mal, wenn es schläft, kann er es schlafwandeln lassen. Der Druide  kann  durch  seine  Augen  sehen  und  hat vollständige  Kontrolle  über  den  Körper  des  Opfers.  Der Schlafwandler ist jedoch weder besonders schnell (GS halbiert) noch geschickt (alle Fertigkeitsproben –1, –2 AT, –1 PA, –1 AW). Für Körper Talente/körperliche Eigenschaften wird der FW/Wert des Opfers benutzt, ebenso alle seine Kampfwerte. Bei anderen Talenten/geistigen Eigenschaften kommt der FW/Wert des Druiden zu  tragen.  Zauber/Liturgien  kann  der  Druide  nicht durch das Opfer wirken. Sollte das Opfer TP oder SP erleiden,  so  bricht  die  Kontrolle  ab.  Soll  das  Opfer sich  selbst  schaden  oder  soll  es  gezwungen  werden, gegen seine Prinzipien zu handeln, so steht ihm eine Probe auf <em>Willenskraft</em> zu, um den Befehl zu verweigern. Diese  Probe  kann  nach Meisterentscheid  modifiziert  werden.  Während der  Druide  die  mentale Kontrolle  ausübt,  ist  er selbst  in  Meditation  erstarrt  und  erleidet  den Status <em>Bewegungsunfähig</em>.","Publikation":"Die Streitenden Königreiche Seite 170","AsP-Kosten":"32 AsP","Verbreitung":"Druiden"},"spellclass":"Herrschaftsritual","name":"Macht über Schlafwandler","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/wegsweiser.html","properties":{"Merkmal":"Telekinese","AP-Wert":"5 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Wird der Dolch aufgehängt oder auf eine ebene Fläche gelegt, zeigt er nach einem kurzen Augenblick in die Richtung des Ortes, an dem die Bindung des Dolches durchgeführt wurde. Dadurch sind Proben auf <em>Orientierung</em> für den Druiden um 3 erleichtert.","Publikation":"Die Streitenden Königreiche Seite 159","AsP-Kosten":"1 AsP","Voraussetzungen":"Bindung des Dolches","Verbreitung":"Druiden"},"spellclass":"Dolchritual","name":"Wegweiser","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Kraftdererde.html","properties":{"Merkmal":"Heilung","AP-Wert":"10 Abenteuerpunkte","Volumen":"5 Punkte","Wirkung":"Der Druide steckt den Dolch 5 Minuten lang in die Erde. Wenn er ihn danach wieder herauszieht, kann er  zwischen  den  beiden  folgenden  Vorteilen  wählen: Entweder fließt Lebenskraft aus der Erde über den Dolch, und der Druide erlangt auf der Stelle 1W6 LeP zurück. Er kann alternativ aber auch entscheiden, eine Stufe eines beliebigen Zustands (außer Belastung) aufzuheben.","Publikation":"Die Streitenden Königreiche Seite 159","AsP-Kosten":"4 AsP","Voraussetzungen":"Bindung des Dolches","Verbreitung":"Druiden"},"spellclass":"Dolchritual","name":"Kraft der Erde","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/FluchderPestilenz.html","properties":{"Wirkungsdauer":"bis zum Ende der Krankheit, maximal aber QS x 3 Tage","Merkmal":"Einfluss","Probe":"MU/KL/CH (modifiziert durch SK)","Wirkung":"Das Opfer wird vom Druiden mit einer ihm bekannten  Krankheit  infiziert.  Die Stufe  der  Krankheit darf maximal dem Fertigkeitswert des Rituals entsprechen. Ab der Infektion verläuft die Krankheit normal, ist allerdings nicht ansteckend. Nicht wählbar sind magische Erkrankungen wie Duglumspest.","Publikation":"Die Streitenden Königreiche Seite 170","AsP-Kosten":"8 AsP","Verbreitung":"Druiden"},"spellclass":"Herrschaftsritual","name":"Fluch der Pestilenz","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Dolch-Apport.html","properties":{"Merkmal":"Telekinese","AP-Wert":"15 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Der Dolch kann vom Druiden zu sich gerufen werden. Er fliegt auf dem kürzesten Weg mit Geschwindigkeit 15 zum Druiden und umfliegt selbstständig Hindernisse. Fenster durchschlägt der Dolch, Wände aus Holz oder Stein halten ihn jedoch auf. Der Druide muss für den Einsatz des Dolch-Apports seinen Dolch nicht sehen.","Publikation":"Die Streitenden Königreiche Seite 159","AsP-Kosten":"1 AsP","Voraussetzungen":"Bindung des Dolches","Verbreitung":"Druiden"},"spellclass":"Dolchritual","name":"Dolch-Apport","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Bindungdolch.html","properties":{"Merkmal":"Objekt","AP-Wert":"10 Abenteuerpunkte","Volumen":"0 Punkte","Bindungskosten":"2 permanente AsP","Wirkung":"Die Bindung des Dolches ist stets das erste Ritual, das auf einen Druidendolch gewirkt wird. Mit ihm wird die Klinge an den Druiden gebunden und dadurch unzerbrechlich. Einzig elementares Feuer, das heißer brennt als ein Drachenodem oder ein Ignifaxius, oder aber gezielte Antimagie können ihn fortan zerstören. Der Dolch gilt von nun an als magische Waffe und kann nicht auf anderem Weg, wie etwa dem ARCANOVI, mit weiteren Zaubern belegt werden. Ein Druide kann immer nur einen Vulkanglasdolch besitzen. Das Binden eines Dolches kostet ihn einmalig 2 permanente AsP. Der Druide kann sich entscheiden, seine Verbindung zum Dolch zu lösen, beispielsweise um einen neuen Dolch an sich zu binden. Nach dem Tod eines Druidenverliert der Dolch nach 24 Stunden die permanent gespeicherten AsP und jegliche Magie.","Publikation":"Die Streitenden Königreiche Seite 158","Voraussetzungen":"Druide (Tradition)","Verbreitung":"Druiden"},"spellclass":"Dolchritual","name":"Bindung des Dolches","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Ausbruchunterdr.html","properties":{"Wirkungsdauer":"QS Tage","Merkmal":"Einfluss","Probe":"MU/IN/CH (modifiziert durch SK)","Wirkung":"Durch dieses Ritual kann der Druide dem Opfer eine Persönlichkeitsschwäche oder eine Schlechte Eigenschaft anhexen. Das Opfer enthält den entsprechenden Nachteil bis zum Ablauf der Wirkungsdauer.","Publikation":"Die Streitenden Königreiche Seite 170","AsP-Kosten":"8 AsP","Verbreitung":"Druiden"},"spellclass":"Herrschaftsritual","name":"Ausbruch unterdrückter Gefühle","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/sichtdunke.html","properties":{"Merkmal":"Verwandlung","AP-Wert":"10 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Der Druide fügt sich eine kleine Schnittwunde zu, vorzugsweise an der Stirn, was 1 LeP kostet. Danach kann er in der Dunkelheit so gut sehen, wie am  helllichten  Tag. Sichtmodifikatoren  durch  Dunkelheit können ignoriert werden. Andere Sichtmodifikatoren, etwa  aufgrund  von  Nebel,  bleiben  allerdings bestehen. Das Ritual wirkt auch gegen magische Dunkelheit, nicht jedoch gegen Dunkelheit, die durch Liturgien oder Zeremonien verursacht wurde.","Publikation":"Die Streitenden Königreiche Seite 159","AsP-Kosten":"4 AsP (Aktivierung) +2 AsP pro 5 Minuten","Voraussetzungen":"Bindung des Dolches","Verbreitung":"Druiden"},"spellclass":"Dolchritual","name":"Sicht im Dunkeln","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/FigurderSchmerzen.html","properties":{"Wirkungsdauer":"QS/2 Tage","Merkmal":"Einfluss","Probe":"MU/IN/CH (modifiziert durch SK)","Wirkung":"Der Druide erschafft eine Figur aus Wachs, Holz oder einem anderen Material (nicht jedoch aus Metall)  und  bindet  dabei  die  körperliche  Komponente des Opfers ein, das er verzaubern will. Am Ende der Ritualdauer kann er die Figur mit seinem Dolch stechen, über Feuer halten oder sie auf andere Weise beschädigen, um dem Opfer Schmerzen zu zufügen. Das Opfer erleidet für die Dauer von 24 Stunden 1 Stufe <em>Schmerz</em>, wenn der Druide die Figur pro Tag für mindestens 5 Minuten foltert. Mehr als 1 Stufe <em>Schmerz</em> lässt sich durch dieses Ritual nicht verursachen. Nutzt der Druide den Tag nicht für die Folter der Figur, geschieht zwar nichts, aber das magische Band zwischen Figur und Opfer besteht fort, sofern die Wirkungsdauer noch nicht abgelaufen ist. Der Schmerz vergeht spätestens 1 Stunde nach dem Ende der Wirkungsdauer.","Publikation":"Die Streitenden Königreiche Seite 170","AsP-Kosten":"8 AsP","Verbreitung":"Druiden"},"spellclass":"Herrschaftsritual","name":"Figur der Schmerzen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_VolumenerweiterungdesZauberstabsI-V.html","properties":{"Merkmal":"Objekt","AP-Wert":"5 Abenteuerpunkte pro Stufe","Volumen":"0 Punkte","Wirkung":"Das Volumen des Zauberstabs steigt permanent um 1 Punkt. Dieser Stabzauber kann mehrfach gewählt werden, um die jeweils nächsthöhere Stufe zu erhalten.","Publikation":"Aventurische Magie Seite 25","AsP-Kosten":"1 permanenter AsP pro Stufe","Voraussetzungen":"keine, bei höheren Stufen die jeweils vorige Stufe der SF","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Volumenerweiterung des Zauberstabs I-V","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Telekineseschlag.html","properties":{"Merkmal":"Telekinese","AP-Wert":"15 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Der Zauberer kann einen Gegner in einer Entfernung von bis zu 8 Schritt mit einem telekinetischen Schlag angreifen. Dem Zauberer muss eine AT mit seinem Traditionsartefakt gelingen. Der Gegner kann nur ausweichen, eine Schilde-PA einsetzen oder mit seinem Traditionsartefakt parieren. Die Reichweite der Waffen spielt hierbei keine Rolle. Gelingt der Angriff und wird nicht verteidigt, erhält der Gegner 1W6+1 TP und muss eine Probe auf <em>Körperbeherrschung (Balance) </em>bestehen, um nicht den Status <em>Liegend </em>zu erhalten.","Publikation":"Aventurische Magie Seite 24","AsP-Kosten":"2 AsP pro AT","Voraussetzungen":"Hammer des Magus","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Telekineseschlag","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Stabfixierung.html","properties":{"Merkmal":"Telekinese","AP-Wert":"10 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Dieser Stabzauber wirkt wie ein Objectofixo, nur, dass der Stab an einer beliebigen Oberfläche fixiert werden kann.","Publikation":"Aventurische Magie Seite 23","AsP-Kosten":"4 AsP pro 5 Minuten","Voraussetzungen":"Stab-Apport","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Stabfixierung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Stabexplosion.html","properties":{"Merkmal":"Telekinese","AP-Wert":"20 Abenteuerpunkte","Volumen":"6 Punkte","Wirkung":"Der Stab explodiert 4 Aktionen nach Aktivierung des Stabzaubers in winzige Splitter und trifft alle Lebewesen im Radius von 4 Schritt mit 2W6+2 magischen TP. Der Schaden ist magischen Ursprungs. Man kann sich nur mittels einer Schilde-PA dagegen verteidigen. 4 KR nach der Explosion setzt sich der Stab wieder an dem Ort zusammen, an dem er explodiert ist. Der Stab darf nach Aktivierung nicht wesentlich bewegt oder berührt werden, sonst wird der Stabzauber abgebrochen.","Publikation":"Aventurische Magie Seite 23","AsP-Kosten":"16 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Stabexplosion","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Stabbohrer.html","properties":{"Merkmal":"Telekinese","AP-Wert":"8 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Der Stab bohrt sich durch jedes beliebige Material, das nicht härter als Diamant ist. Der Stab dreht sich dabei und bohrt pro KR 1 Halbfinger tief. Der Stab kann sich vertikal oder horizontal durch das Material bohren, allerdings nur durch leblose Objekte.","Publikation":"Aventurische Magie Seite 23","AsP-Kosten":"keine","Voraussetzungen":"Stab-Apport","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Stabbohrer","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Tierwandlung.html","properties":{"Merkmal":"Verwandlung","Wirkung":"Mit diesem Stabzauber kann der Zauberer seinen Stab in ein Tier verwandeln und seinen Geist in dieses einfließen lassen. Der Stabzauber existiert in mehreren Verwandlungsgraden und Pfaden, meist aber nicht ausschließlich beginnend mit einer kleinen Tierverwandlung. Die Voraussetzung für die nächstgrößere Verwandlung ist jeweils die vorhergehend kleinere Verwandlung des Pfades. Beim Erlernen des Stabzaubers muss der Zauberer sich für einen der folgenden Pfade entscheiden. Das erforderliche Stabvolumen und die AP-Kosten sind je nach Mächtigkeit der Tierverwandlung unterschiedlich.<ul><li>#Echsenpfad: Chamäleon (5 Volumen, 15 AP) und Speikobra (7 Volumen, 20 AP)</li><li>#Nagerpfad: Eichhörnchen (5 Volumen, 10 AP) und Biber (6 Volumen, 15 AP)</li><li>#Vogelpfad: Taube (6 Volumen, 15 AP) und Rabe (7 Volumen, 20 AP)</li></ul>Die Verwandlung dauert hin und zurück je 4 Aktionen. Der Zauberer behält seine geistigen Eigenschaften in Tiergestalt, die restlichen Werte werden durch die des Tieres ersetzt. In Tiergestalt verfügt der Zauberer außerdem über die Fähigkeiten des Tieres, also beispielsweise Farbwechsel beim Chamäleon oder Giftspucken bei der Speikobra. Zauber kann er während der Zeit der Verwandlung nicht einsetzen und er regeneriert keine AsP durch Regenerationsphasen. Sein eigener Körper erhält während der Verwandlung 4 Stufen Paralyse wie unter dem Zauber Paralysis und gilt als versteinert. Der Zauberer kann den Stabzauber nur beenden, wenn er wieder Körperkontakt mit seinem Ursprungskörper hat oder ihm die AsP ausgehen. Wird das Tier getötet, wird der Geist des Zauberers wieder in seinen Körper zurückgeschleudert und er erleidet 4W6 SP. Gleiches gilt, wenn dem Zauberer einfach die AsP ausgehen und so die Verbindung plötzlich getrennt wird.","AP-Wert":"10/15/20 Abenteuerpunkte für eine kleine/mittlere/große Tierverwandlung","Tiere":"Chamäleon (mittlere Tierverwandlung, Echsenpfad)</strong></li><li><strong>FF</strong> 13 <strong>GE</strong> 14 <strong>KO</strong> 5 <strong>KK</strong> 8 (k)</li><li>LeP wie Zauberer <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI</strong> 10+1W6</li><li><strong>VW</strong> 7 <strong>SK</strong> wie Zauberer <strong>ZK</strong> –2 <strong>GS</strong> 8</li><li><strong>Zunge:</strong> <strong>AT</strong> 16 <strong>TP</strong> 1W3–2 <strong>RW</strong> kurz</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li><strong>Zusätzliche Vorteile:</strong> Proben auf <em>Verbergen (sich Verstecken) </em>sind um 3 erleichtert.</li><li><strong>Biber (mittlere Tierverwandlung, Nagerpfad)</strong></li><li><strong>FF</strong> 11 <strong>GE </strong>10 <strong>KO</strong> 8 <strong>KK</strong> 11 (k)</li><li><strong>LeP</strong> wie Zauberer <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI</strong> 11+1W6</li><li><strong>VW</strong> 5 <strong>SK</strong> wie Zauberer <strong>ZK</strong> 0 <strong>GS </strong>7</li><li><strong>Biss:</strong> <strong>AT</strong> 13 <strong>TP</strong> 1W6+1 <strong>RW</strong> kurz</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten: </strong>keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li>Zusätzliche Vorteile: kann bis zu 20 Minuten tauchen, ohne Luft zu holen</li><li><strong>Taube (mittlere Tierverwandlung, Vogelpfad)</strong></li><li><strong>FF </strong>12 <strong>GE </strong>12 <strong>KO</strong> 5 <strong>KK</strong> 8 (k)</li><li><strong>LeP</strong> wie Zauberer <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI </strong>12+1W6</li><li><strong>VW</strong> 6 <strong>SK</strong> wie Zauberer <strong>ZK</strong> –2 <strong>GS</strong> 4/16 (am Boden/in der Luft)</li><li><strong>Schnabel:</strong> <strong>AT</strong> 12 <strong>TP</strong> 1W2–1 <strong>RW</strong> kurz</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li><strong>Zusätzliche Vorteile:</strong> Unscheinbar (vor allem in Städten)</li><li><strong>Rabe (große Tierverwandlung, Vogelpfad)</strong></li><li><strong>FF</strong> 12 <strong>GE</strong> 10 <strong>KO</strong> 6 <strong>KK</strong> 9 (k)</li><li><strong>LeP</strong> wie Zauberer <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI</strong> 11+1W6</li><li><strong>VW </strong>5 <strong>SK</strong> wie Zauberer <strong>ZK</strong> –2 <strong>GS </strong>2/16 (am Boden/in der Luft)</li><li><strong>Schnabel:</strong> <strong>AT</strong> 14 <strong>TP </strong>1W3–1 <strong>RW </strong>kurz</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li><strong>Zusätzliche Vorteile:</strong> Herausragender Sinn (Sicht)</li><li><strong>Speikobra (große Tierverwandlung, Echsenpfad)</strong></li><li><strong>FF</strong> 8 <strong>GE</strong> 9 <strong>KO</strong> 12 <strong>KK</strong> 10 (k)</li><li><strong>LeP</strong> wie Zauberer <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI</strong> 12+1W6</li><li><strong>VW </strong>5 <strong>SK </strong>wie Zauberer <strong>ZK</strong> 0 <strong>GS</strong> 3</li><li><strong>Biss:</strong> <strong>AT</strong> 12 <strong>TP</strong> 1W3+1(+Gift)* <strong>RW</strong> kurz</li><li><strong>Speiangriff:</strong> <strong>FK</strong> 17 <strong>LZ</strong> 2 <strong>TP</strong> Gift* <strong>RW</strong> 2/4/6</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li><strong>Zusätzliche Vorteile:</strong> Giftspucken</li><li><strong>Sonderregeln:</strong></li><li><em>*) </em><em>Speikobragift</em></li><li>Das Gift der Speikobra kann mehrmalig gegen den gleichen Gegner wirken. Der Speiangriff wird wie ein Fernkampfangriff mit einer Schusswaffe behandelt.</li><li><strong>Stufe:</strong> 7</li><li><strong>Art:</strong> Kontaktgift, tierisch","Volumen":"5/6/7 Punkte für eine kleine/mittlere/große Tierverwandlung <strong>Wirkung:</strong> im Blutkreislauf durch Biss: 2 SP pro KR / 1 SP pro KR; auf der Haut durch Spucken: 1W6 SP / 1W3 SP</li><li><strong>Beginn:</strong> sofort</li><li><strong>Dauer:</strong> im Blutkreislauf: 2W6 KR / 1W6 KR<!-- Zusätzlich zu Gifte und Krankheiten / GifteZusätzlich zu Gifte und Krankheiten / GifteSonderfertigkeiten / Stabzauber --></li><li><strong>Eichhörnchen (kleine Tierverwandlung, Nagerpfad)</strong></li><li><strong>FF</strong> 13 <strong>GE </strong>13 <strong>KO</strong> 5 <strong>KK</strong> 9 (k)</li><li><strong>LeP</strong> wie Zauberer <strong>AsP</strong> – <strong>KaP </strong>– <strong>INI</strong> 14+1W6</li><li><strong>VW</strong> 6 <strong>SK</strong> wie Zauberer <strong>ZK</strong> –3 <strong>GS</strong> 13</li><li><strong>Biss:</strong> <strong>AT</strong> 14 <strong>TP </strong>1W3–1 <strong>RW</strong> kurz</li><li><strong>RS/BE:</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> wie Zauberer</li><li><strong>Anzahl:</strong> 1</li><li><strong>Größenkategorie:</strong> winzig</li><li><strong>Typus:</strong> Tier, nicht humanoid</li><li><strong>Zusätzliche Vorteile:</strong> Herausragender Sinn (Gehör), Proben auf <em>Klettern (Baumklettern </em>oder <em>Fassadenklettern) </em>sind um 3 erleichtert","Publikation":"Aventurische Magie Seite 24","AsP-Kosten":"2 AsP pro Tag","Voraussetzungen":"Tierkunde 10","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Tierwandlung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Stab-Apport.html","properties":{"Merkmal":"Telekinese","AP-Wert":"15 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Der Stab kann vom Magier zu sich gerufen werden. Er fliegt auf dem kürzesten Weg mit Geschwindigkeit 15 zum Magier und umfliegt selbstständig Hindernisse. Fenster durchschlägt der Stab, Wände aus Holz oder Stein halten ihn jedoch auf. Der Magier muss für den Einsatz des Stab-Apports seinen Magierstab nicht sehen.","Publikation":"Regelwerk Seite 278","AsP-Kosten":"1 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Stab-Apport","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Siegelbrecher.html","properties":{"Merkmal":"Antimagie","AP-Wert":"10 Abenteuerpunkte","Volumen":"6 Punkte","Wirkung":"Der Stab zerstört im Radius von 8 Schritt beliebige Bann- oder Schutzkreise. Dies funktioniert aber nur bei Materialien, die eine weiche Komponente haben, also beispielsweise Wachs, Kreide oder Farbe. Steinerne Kreise sind davon nicht betroffen.","Publikation":"Aventurische Magie Seite 23","AsP-Kosten":"5 AsP pro Schutz- oder Bannkreis","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Siegelbrecher","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Seilschlange.html","properties":{"Merkmal":"Telekinese","AP-Wert":"10 Abenteuerpunkte","Seilschlange":"<ul><li><strong>MU</strong> 12 <strong>KL</strong> 12 (t) <strong>IN</strong> 13 <strong>CH</strong> 13</li><li><strong>FF</strong> 12 <strong>GE</strong> 14 <strong>KO</strong> 12 <strong>KK</strong> 10</li><li><strong>LeP</strong> 18 <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI </strong>12+1W6</li><li><strong>VW</strong> 3 <strong>SK</strong> 1 <strong>ZK</strong> 0 <strong>GS</strong> 3</li><li><strong>Biss:</strong> <strong>AT</strong> 12 <strong>TP</strong> 1W6(+Gift)* <strong>RW</strong> kurz</li><li><strong>RS/BE</strong> 0/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> Finte I (Biss)</li><li><strong>Talente:</strong> Einschüchtern 6, Klettern 4, Körperbeherrschung 8, Kraftakt 3, Schwimmen 15, Selbstbeherrschung 7, Sinnesschärfe 7, Verbergen 9, Willenskraft 7</li><li><strong>Größenkategorie:</strong> klein</li><li><strong>Typus:</strong> Tier (magisch), nicht humanoid</li><li><strong>Kampfverhalten:</strong> Die Schlange verteidigt den Magier bis zum Tod.</li><li><strong>Flucht:</strong> siehe Kampfverhalten</li><li><strong>Sonderregeln:</strong></li><li><em>*) </em><em>Seilschlangengift</em><em></em>Die Wirkung durch mehrere Bisse ist kumulativ.</li><li><strong>Stufe:</strong> 3</li><li><strong>Art:</strong> Waffengift, tierisch</li><li><strong>Widerstand:</strong> Zähigkeit <strong>Wirkung</strong>: einmalig 1W6 SP / 1W3 SP</li><li><strong>Beginn:</strong> 2 KR</li><li><strong>Dauer:</strong> sofort</li><li><strong>Kosten:</strong> keine, Gift kann nicht extrahiert werden</li></ul>","Volumen":"5 Punkte","Wirkung":"Der Stab verwandelt sich in eine Seilschlange. Sie beschützt den Zauberer mit den ihr zur Verfügung stehenden Mitteln und folgt ihm. „Stirbt“ die Schlange, verwandelt sie sich wieder zurück in den Stab. Die Schlange gilt als magisch.","Publikation":"Aventurische Magie Seite 23","AsP-Kosten":"5 AsP für 5 Minuten","Voraussetzungen":"Seilpeitsche","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Seilschlange","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_SeildesMagus.html","properties":{"Merkmal":"Objekt","AP-Wert":"15 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Der Stab kann in ein unzerstörbares Seil von 25 Schritt Länge verwandelt werden. Es kann sich um Objekte schlingen, wenn es geworfen wird. Knoten können so willentlich gebunden oder gelöst werden, solange der Magier das Seil berührt. Das Knoten und Entknoten zählt jeweils als 1 Aktion. Mit dem Seil lässt sich auch ein Wesen in Reichweite von bis zu 20 Schritt treffen und behindern. Das Seil hat einen AT-Wert von 14 und kann jede KR eine Attacke ablegen. Seine Initiative entspricht der des Zauberers. Bei einer gelungenen AT muss dem Ziel eine Raufen-PA oder Ausweichen gelingen, ansonsten gilt es als <em>Eingeengt </em>oder <em>Fixiert</em>: <em>Eingeengt </em>dann, wenn vor dem Angriff auf den Oberkörper gezielt wurde, <em>Fixiert</em> bei den Beinen. Diese Entscheidung muss vor dem Angriff angesagt werden. Das Wesen kann sich mit einer Probe auf <em>Körperbeherrschung (Entwinden) </em>wieder befreien. Das Seil kann nur eine Person und nur eine bis zur Größenkategorie groß attackieren. Verliert der Magier den Kontakt zum Seil, verwandelt es sich zurück in seine ursprüngliche Stabform.","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"1 AsP pro Attacke / Einsatz","Voraussetzungen":"Seil des Adepten","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Seil des Magus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Seilpeitsche.html","properties":{"Merkmal":"Telekinese","AP-Wert":"15 Abenteuerpunkte","Volumen":"5 Punkte","Wirkung":"Der Stab verwandelt sich in eine Peitsche, die als magische Waffe gilt. Nun hat der Magier zwei Möglichkeiten:<ul><li>#Entweder er lässt die Peitsche selbst mit AT 14 und 1W6+2 TP angreifen. Da sie wegen der nur schwer kontrollierbaren Bewegungen kaum abzuwehren ist, ist eine Parade gegen sie um 2 erschwert. Schilde können darüber hinaus nur ihren einfachen Bonus auf die Verteidigung nutzen. Die Reichweite der Peitsche ist <em>lang</em>, man kann mit ihr keine Paraden ausführen.</li><li>Die Peitsche kann wahlweise statt TP den Status <em>Liegend </em>verursachen. Dies muss vor der Attacke angekündigt werden. Ein Gegner kann dies verhindern, indem ihm eine Probe auf <em>Körperbeherrschung</em> <em>(Kampfmanöver) </em>erschwert um 3 gelingt.</li><ul>","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"1 AsP pro Attacke","Voraussetzungen":"Seil des Magus","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Seilpeitsche","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Modifikationsfokus.html","properties":{"Merkmal":"Objekt","AP-Wert":"10 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Solange der Zauberer in Körperkontakt mit dem Stab ist, kann er 1 zusätzliche Zaubermodifikation vornehmen (bis zu einem Maximum von 6).","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"keine","Voraussetzungen":"Kraftfokus","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Modifikationsfokus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Merkmalsfokus.html","properties":{"Merkmal":"je nach Merkmal","AP-Wert":"35 Abenteuerpunkte","Volumen":"8 Punkte","Wirkung":"Bei diesem Stabzauber muss ein Merkmal gewählt werden, das der Fokus unterstützen soll. Proben auf Zauber mit dem so unterstützten Merkmal sind um 1 erleichtert. Der Merkmalsfokus kann mehrfach mit unterschiedlichen Merkmalen auf den Stab gewirkt werden. Solange der Stab in ein Flammenschwert, eine Fackel oder etwas anderes verwandelt ist, gilt dieser Vorteil nicht. Der Magier muss den Stab in der Hand halten, wenn er von dem Stabzauber profitieren will.","Publikation":"Regelwerk Seite 277","AsP-Kosten":"keine","Voraussetzungen":"Kraftfokus, passende Merkmalskenntnis","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Merkmalsfokus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_SeildesAdepten.html","properties":{"Merkmal":"Objekt","AP-Wert":"10 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Der Stab kann in ein unzerstörbares Seil von 10 Metern Länge verwandelt werden. Es kann sich um Objekte schlingen, wenn es geworfen wird. Knoten können so willentlich gebunden oder gelöst werden, solange der Magier das Seil berührt. Das Knoten und Entknoten zählt jeweils als 1 Aktion. Personen lassen sich damit nicht fesseln, außer sie haben den Status <em>Bewegungsunfähig</em>. Verliert der Magier den Kontakt zum Seil, verwandelt es sich zurück in seine ursprüngliche Stabform.","Publikation":"Regelwerk Seite 277","AsP-Kosten":"1 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Seil des Adepten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SF_Kraftfokus.html","properties":{"Merkmal":"Objekt","AP-Wert":"30 Abenteuerpunkte","Volumen":"6 Punkte","Wirkung":"Solange der Magier den Stab berührt, spart er bei jedem Zauber 1 AsP ein. Dieser Abzug wird nach Berechnung aller anderen Modifikationen eingerechnet. Die Ersparnis kann nicht dazu führen, dass die Kosten unter 1 AsP fallen. Solange der Stab in ein Flammenschwert, eine Fackel oder etwas anderes verwandelt ist, gilt die Ersparnis nicht. Der Magier muss den Stab in der Hand halten, wenn er von dem Stabzauber profitieren will.","Publikation":"Regelwerk Seite 277","AsP-Kosten":"keine","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Kraftfokus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_MachvolleStabzauberI-IV.html","properties":{"Merkmal":"Objekt","AP-Wert":"3 Abenteuerpunkte pro Stufe","Volumen":"1 pro Stufe ","Wirkung":"Die QS von Stabzaubern gilt pro Stufe als um 1 höher. Dieser Stabzauber kann mehrfach gewählt werden, um die jeweils nächsthöhere Stufe zu erhalten.","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"keine","Voraussetzungen":"keine, bei höheren Stufen die jeweils vorige Stufe der SF","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Machvolle Stabzauber I-IV","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_HammerdesMagus.html","properties":{"Merkmal":"Telekinese","AP-Wert":"10 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Gegenstände bis zu 50 Strukturpunkten werden bei einer Berührung durch den Stab zerstört.","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"2 AsP pro Treffer","Voraussetzungen":"Hammer des Adepten","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Hammer des Magus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Heilschlaf.html","properties":{"Merkmal":"Heilung","AP-Wert":"8 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Der Zauberer kann ein Wesen berühren, sodass es bei seiner nächsten Regenerationsphase 1 zusätzlichen LeP, AsP und KaP durch erholsamen Schlaf heilt.","Publikation":null,"AsP-Kosten":"3 AsP pro Wesen","Voraussetzungen":"Heilkunde Wunden 10","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Heilschlaf","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_HalbesMass.html","properties":{"Merkmal":"Objekt","AP-Wert":"3 Abenteuerpunkte","Volumen":"1 Punkt","Wirkung":"Der Stab kann auf die Hälfte seiner Länge verkleinert werden, ohne dass er merklich an Dicke einbüßt. Die Wirkung hält solange an, wie der Magier Körperkontakt mit dem Stab hält. Ein langer Stab wird regeltechnisch zu einem mittleren Stab, ein mittlerer Stab zu einem kurzen Stab und ein kurzer Stab wird so klein, dass er sich nicht mehr als Waffe eignet.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"1 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Halbes Maß","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_HammerdesAdepten.html","properties":{"Merkmal":"Telekinese","AP-Wert":"5 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Gegenstände mit bis zu 25 Strukturpunkten werden bei einer Berührung durch den Stab zerstört.","Publikation":"Aventurische Magie Seite 22","AsP-Kosten":"keine","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Hammer des Adepten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Energiestrahl.html","properties":{"Merkmal":"Verwandlung","AP-Wert":"<strong>:</strong> 15 Abenteuerpunkte","Volumen":"5 Punkte","Wirkung":"Aus dem Stab schießt ein Strahl aus gebündelter astraler Kraft, der in gerader Linie sein Ziel trifft. Das getroffene Ziel erleidet 1W6+4 Trefferpunkte Schaden. Der Energiestrahl zählt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist möglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Der Strahl trifft automatisch, wenn man sich nicht verteidigt. Trifft der Energiestrahl sein Ziel, werden die TP durch den RS des Ziels vermindert.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"8 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Energiestrahl","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_EwigeFlamme.html","properties":{"Merkmal":"Elementar","AP-Wert":"10 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Das Ende des Stabes kann nun willentlich entzündet werden und brennt wie eine Fackel. Das Feuer hat die natürliche Helligkeit und Hitze einer solchen Flamme, zehrt den Stab jedoch nicht auf. Im Kampf eingesetzt bietet die ewige Flamme keinen besonderen Vorteil. Die Wirkung des Stabzaubers endet automatisch, wenn der Magier schläft, den Status <em>Bewusstlos </em>erleidet oder spätestens nach 1 Stunde.","Publikation":"Regelwerk Seite 277","AsP-Kosten":"1 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Ewige Flamme","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Flammenschwert.html","properties":{"Merkmal":"Elementar","AP-Wert":"35 Abenteuerpunkte","Volumen":"7 Punkte","Wirkung":"Mit diesem Stabzauber kann der Magier seinen Stab in ein heiß brennendes Flammenschwert oder, je nach Region, in einen Flammensäbel verwandeln. Dieses Flammenschwert gilt als magische Waffe und richtet wie jedes Feuer zusätzlichen Schaden an Wesen an, die feuerempfindlich sind. Es verursacht 1W6+7 Trefferpunkte. Der Stab muss eine Mindestlänge von einem Schritt besitzen, um in ein Flammenschwert verzaubert werden zu können. Die übrigen Werte entsprechen denen eines Langschwertes. Der Magier kann das Schwert auf zwei Varianten einsetzen: <ol><li>Er kann es wie ein normales Schwert mit der Kampftechnik Schwerter führen. Bei einem Patzer verwandelt sich das Flammenschwert zurück in den Stab. Pro Kampfrunde kostet diese Variante nach der Aktivierung 1 AsP.</li><li>Er kann es per Telekinese fliegen und kämpfen lassen. Hierbei werden die Kampftechnik Schwerter und der entsprechende Attackewert des Magiers für Attacken verwendet. Es bewegt sich mit Geschwindigkeit 8. Der Magier muss sich zur Kontrolle des Schwertes konzentrieren. Das Flammenschwert kontrolliert fliegen zu lassen, ist eine länger dauernde Handlung.</li></ol>Wenn sich das Schwert weiter als 32 Schritt weg vom Magier bewegt, bricht der Stabzauber ab und das Schwert verwandelt sich wieder zurück. Pro Kampfrunde kostet diese Variante nach der Aktivierung 2 AsP.<br/>Um ein fliegendes Flammenschwert festzuhalten, ist ein erfolgreicher Angriff mit Raufen und der Sonderfertigkeit Haltegriff nötig. Für diese Zwecke verfügt das Schwert über eine PA in Höhe der Hälfte der AT und einen Wert in <em>Kraftakt </em>von 10 Punkten mit Eigenschaften von 14 für die Probe.","Publikation":"Regelwerk Seite 277","AsP-Kosten":"3 AsP, danach 1 bzw. 2 AsP pro Kampfrunde","Voraussetzungen":"Ewige Flamme und Kraftfokus","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Flammenschwert","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_BannstabdesMagus.html","properties":{"Merkmal":"Objekt","AP-Wert":"20 Abenteuerpunkte","Volumen":"5 Punkte","Wirkung":"Der Stab richtet gegen beschwörbare Wesen wie Dämonen, Elementare, Feen und Geister +1 TP an. Dieser Bonus ist kumulativ mit dem aus Bannstab des Adepten, damit richtet ein mit beiden Stabzaubern verzauberter Stab sogar +2 TP an.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"keine","Voraussetzungen":"Bannstab des Adepten","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Bannstab des Magus","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_BannstabdesAdepten.html","properties":{"Merkmal":"Objekt","AP-Wert":"12 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Der Stab richtet gegen beschwörbare Wesen wie Dämonen, Elementare, Feen und Geister +1 TP an.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"keine","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Bannstab des Adepten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Druckwelle.html","properties":{"Merkmal":"Telekinese","AP-Wert":"15 Abenteuerpunkte","Volumen":"5 Punkte","Wirkung":"Der Zauberer kann den Stab mit 2 Aktionen in den Boden rammen und so eine Druckwelle mit einem Radius von 8 Schritt auslösen. Alle Wesen der Größenkategorie mittel und kleiner müssen eine Probe auf <em>Körperbeherrschung (Balance) </em>erleichtert um 1 bestehen um nicht den Status <em>Liegend </em>zu erhalten. Der Zauberer ist von der Wirkung ausgenommen.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"8 AsP","Voraussetzungen":"Telekineseschlag","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Druckwelle","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_DoppeltesMa%C3%9F.html","properties":{"Merkmal":"Objekt","AP-Wert":"5 Abenteuerpunkte","Volumen":"2 Punkte","Wirkung":"Der Stab kann auf seine doppelte Länge verlängert werden, ohne dass er merklich an Dicke einbüßt. Die Wirkung hält an, solange der Magier Körperkontakt mit dem Stab hält. Ein mittlerer Stab wird so lang, dass er unter Stangenwaffen fällt und die Reichweite lang erhält, der lange Stab wird so lang, dass er als Waffe nicht mehr zu benutzen ist. Ein kurzer Stab erhält die Reichweite mittel.","Publikation":"Regelwerk Seite 276","AsP-Kosten":"1 AsP","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Doppeltes Maß","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_BindungdesStabes.html","properties":{"Merkmal":"Objekt","AP-Wert":"10 Abenteuerpunkte","Volumen":"0 Punkte","Bindungskosten":"2 permanente AsP","Wirkung":"Die Bindung des Stabes ist der erste Stabzauber, der auf einen Magierstab gewirkt wird. Mit ihm wird der Stab an den Magier gebunden. Zudem wird er unzerbrechlich, auch wenn er die Flexibilität des Holzes bewahrt. Einzig elementares Feuer, das heißer brennt als ein Drachenodem oder ein IGNIFAXIUS, oder aber gezielte Antimagie können ihn zerstören. Der Stab gilt von nun an als magische Waffe und kann nicht durch andere Mittel wie den ARCANOVI mit weiteren Zaubern belegt werden. Ein Magier kann immer nur einen Magierstab besitzen.<br/>Das Binden eines Stabes kostet den Magier einmalig 2 permanente AsP. Der Magier kann sich entscheiden, seine Verbindung zum Stab zu lösen, beispielsweise um einen neuen Stab an sich zu binden. Nach dem Tod eines Magiers verliert der Stab nach 24 Stunden die permanent gespeicherten AsP und jegliche Magie.","Publikation":"Regelwerk Seite 276","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Bindung des Stabes","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Astraltransfer.html","properties":{"Merkmal":"Heilung","AP-Wert":"10 Abenteuerpunkte","Volumen":"3 Punkte","Wirkung":"Der Zauberer kann einen anderen Zauberkundigen mit dem Stab berühren und ihm pro 5 KR 1 AsP übertragen. Die Übertragung erfolgt aus dem eigenen Vorrat des Zauberers im Verhältnis 3 zu 1.","Publikation":"Aventurische Magie Seite 20","AsP-Kosten":"1 AsP + 3 AsP pro übertragenem Punkt","Voraussetzungen":"keine","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Astraltransfer","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/sturmlied.html","properties":{"Merkmal":"Elementar","Steigerungsfaktor":"B","Windgeschwindigkeit":"<ul><li>Stufe 1: Windstill</li><li>Stufe 2: Brise</li><li>Stufe 3: Sturm</li><li>Stufe 4: Orkan, jede KR 1 Aktion aufwenden und Probe auf <em>Körperbeherrschung (Balance)</em>, um nichts den Status <em>Liegend</em> zu erhalten.</li></ul>","Talent":"Musizieren oder Singen","Probe":"MU/IN/KO","Wirkung":"Solange das Lied gespielt wird, steigt die Windgeschwindigkeit innerhalb eines Radius’ von QS x 10 Schritt um QS/2 Stufen (maximal ist Stufe 4 möglich). Der Spielende ist selbst nicht von den Auswirkungen betroffen.","Publikation":"Klingen der Nacht Seite 106","AsP-Kosten":"2 AsP pro Minute","Verbreitung":"Elfen"},"spellclass":"Verzerrtes Elfenlied","name":"Sturmlied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Astralentzug.html","properties":{"Merkmal":"Heilung","AP-Wert":"15 Abenteuerpunkte","Volumen":"4 Punkte","Wirkung":"Der Zauberer kann einen anderen Zauberkundigen mit dem Stab berühren und ihm pro 5 KR 1 AsP entziehen, die dieser in die Umgebung verströmt. Hierfür fallen pro entzogenem AsP für den Zaubernden Kosten in Höhe von 3 AsP an. Das Opfer bemerkt den Verlust ab 10 AsP automatisch.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"2 AsP + 3 AsP pro entzogenem Punkt","Voraussetzungen":"Astraltransfer","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Astralentzug","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SSF_Astralraub.html","properties":{"Merkmal":"Heilung","AP-Wert":"20 Abenteuerpunkte","Volumen":"6 Punkte","Wirkung":"Der Zauberer kann einen anderen Zauberkundigen mit dem Stab berühren und ihm pro 5 KR 3 AsP rauben. Pro 3 geraubte AsP darf sich der Zauberer selbst 1 AsP gutschreiben, darf dabei jedoch seinen eigenen Maximalwert nicht überschreiten. Das Opferbemerkt den Verlust ab 5 AsP automatisch.","Publikation":"Aventurische Magie Seite 21","AsP-Kosten":"3 AsP","Voraussetzungen":"Astralentzug","Verbreitung":"Gildenmagier"},"spellclass":"Stabzauber","name":"Astralraub","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Sklavenlied.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"D","Talent":"Musizieren","Probe":"MU/IN/CH","Wirkung":"Innerhalb von QS Schritt befindliche Personen sind von dem Lied betroffen und folgen den gedanklichen Befehlen des Musizierenden, solange es gespielt wird. Betroffene werden diesen nachkommen, solange die Anweisungen dem Selbsterhaltungstrieb nicht im groben Maße widersprechen. Sie werden also einen Diebstahl begehen, Fragen beantworten und dem Zauberer im Kampf beistehen, nicht aber etwa von einer Klippe springen oder ihre eigenen Hände verspeisen. Sie vermögen zudem nichts Anderes zu tun, als das, was explizit befohlen wurde oder zur Erfüllung des Befehls notwendig ist. Betroffen sind nur solche Wesen, deren aufsummierte SK die QS des Lieds nicht übersteigen, beginnend bei den Wesen mit der niedrigsten SK (bei Gleichstand nach der Wahl des Shakagra). Negative SK zählt bei der Aufsummeriung als 0. Wer droht, von der Wirkung betroffen zu sein, kann eine Probe auf Willenskraft –3 ablegen, gelingt die Probe, so zählt die SK dennoch zur Gesamtsumme. Nachtalben sind gegen die Auswirkungen immun.","Publikation":"Klingen der Nacht Seite 106","AsP-Kosten":"4 AsP pro Minute","Verbreitung":"Elfen"},"spellclass":"Verzerrtes Elfenlied","name":"Sklavenlied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Schlachtlied.html","properties":{"Merkmal":"Heilung","Steigerungsfaktor":"B","Talent":"Musizieren oder Singen","Probe":"MU/IN/CH","Wirkung":"Alle Personen innerhalb von QS x 3 Schritt, die auf der Seite des Musizierenden streiten, erhalten eine Erleichterung von 1 Punkt auf alle Attacken und Paraden. Der Spielende selbst ist nicht davon betroffen und kann, wie bei den Verzerrten Elfenliedern üblich, nur eine Bewegungshandlung durchführen.","Publikation":"Klingen der Nacht Seite 105","AsP-Kosten":"1 AsP pro Kampfrunde","Verbreitung":"Elfen"},"spellclass":"Verzerrtes Elfenlied","name":"Schlachtlied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/lieddesschmerz.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"D","Talent":"Singen","Probe":"MU/KL/IN","Wirkung":"Alle Lebewesen innerhalb von QS x 3 Schritt erhalten während des Spielens 2 Stufen des Zustands <em>Schmerz</em>. Betroffen sind nur solche Wesen, deren SK die QS des Lieds nicht übersteigen. Nachtalben sind gegen die Auswirkungen immun.","Publikation":"Klingen der Nacht Seite 106","AsP-Kosten":"8 AsP pro 5 Minuten","Verbreitung":"Elfen"},"spellclass":"Verzerrtes Elfenlied","name":"Lied des Schmerzes","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Kroetenschlag.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","AP-Wert":"25 Abenteuerpunkte","Wirkung":"Die Kröte wandelt auf einen Schlag ihre gesamten AsP in einen magischen Angriff um, der an seinem Ziel viele kleinste Verletzungen bewirkt. Die Kröte fügt dem nächststehenden Ziel 2W6+2 Trefferpunkte. Rüstungen schützen nicht vor diesem Vertrautentrick.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"alle AsP, mindestens aber 8 AsP","Tierarten":"Kröten","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Krötenschlag","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Tarnung.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","AP-Wert":"10 Abenteuerpunkte","Wirkung":"Der Vertraute nimmt die Farbe der Umgebung an und verschmilzt so mit dem Hintergrund. Proben auf <em>Verbergen (sich Verstecken) </em>sind für ihn um 2 erleichtert. Die Tarnung wirkt nur, solange sich das Tier nicht bewegt.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"1 AsP pro 5 Minuten","Tierarten":"Kröte","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Tarnung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/kako.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"C","Talent":"Musizieren","Probe":"MU/KL/CH","Wirkung":"Solange das Lied gespielt wird, erhalten alle Lebewesen im Radius von QS x 3 Schritt 2 Stufen des Zustands <em>Verwirrung</em>. Betroffen sind nur solche Wesen, deren SK die QS des Liedes nicht übersteigen. Nachtalben sind gegen die Auswirkungen immun.","Publikation":"Klingen der Nacht Seite 105","AsP-Kosten":"8 AsP pro 5 Minuten","Verbreitung":"Elfen"},"spellclass":"Verzerrtes Elfenlied","name":"Kakophonie des Wahnsinns","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Stimmungssinn.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","AP-Wert":"20 Abenteuerpunkte","Wirkung":"Der Vertraute erkennt die Gefühle und Stimmungen einer Person und übermittelt sie der Hexe. Zwei Hexen können so in begrenztem Rahmen stille Zwiesprache halten, indem ihre Vertrautentiere ihnen gegenseitig die Gefühle schildern. Gegebenenfalls kann der Spielleiter eine Erleichterung von 1 bei Proben auf <em>Menschenkenntnis </em>zugestehen, um die betroffene Person zu durchschauen.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"1 AsP pro 5 Minuten","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Stimmungssinn","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Kraftraub.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","AP-Wert":"15 Abenteuerpunkte","Wirkung":"Der Vertraute kann einer Person während einer Ruhephase deren Regeneration entziehen, wenn er sie beim Schlafen beobachtet. Das Tier muss das Opfer die ganze Schlafphase beobachten und entzieht ihm dafür die ganze Regeneration der Regenerationsphase. Die entzogene Lebensenergie kann er seiner eigenen hinzufügen, wenn ihm noch Lebenspunkte fehlen. Ansonsten verfällt sie. Die Person hat während der Beobachtungsphase Alpträume von dem Vertrautentier. Es wird keine AE oder KE gestohlen.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"3 AsP","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Kraftraub","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Kroetengift.html","properties":{"Wirkungsdauer":"5 Kampfrunden","Merkmal":"Verwandlung","AP-Wert":"10 Abenteuerpunkte","Wirkung":"Die Kröte sondert statt des sonst bei Kröten üblichen Gifts ein magisches Hautgift ab, das jeden, der sie berührt, mit dem Fluch Warzen Sprießen schlägt. Der Warzen-Fluch wirkt in diesem Fall drei Tage.","Publikation":"Regelwerk Seite 280","AsP-Kosten":"3 AsP","Tierarten":"Kröten","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Krötengift","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Hexensinn.html","properties":{"Wirkungsdauer":"eine halbe Stunde","Merkmal":"Hellsicht","AP-Wert":"beherrschen Vertrautentiere von Anfang an","Wirkung":"Der Vertraute kann über eine beliebige Distanz hinweg die Richtung ausmachen, in der sich die Hexe befindet. Nur göttliches Wirken und Antimagie gegen Hellsicht können diese Verbindung stören.","Publikation":"Regelwerk Seite 279","AsP-Kosten":"1 AsP","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Hexensinn","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Fluchbote.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"je nach Fluch","AP-Wert":"15 Abenteuerpunkte","Wirkung":"Der Vertraute überbringt einen Fluch an ein von der Hexe ausgewähltes Opfer. Er muss hierzu bis zu 7 Schritt an sein Ziel herankommen und es anstarren. Sobald er den Fluch auf das Opfer gewirkt hat, übermittelt er  telepathisch die mit dem Fluch verbundenen Worte der Hexe, die die Bedingungen erklären, ob und wie der Fluch gelöst werden kann.","Publikation":"Regelwerk Seite 279","AsP-Kosten":"1 AsP","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Fluchbote","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/schutzpfeil.html","properties":{"Merkmal":"Keines","AP-Wert":"8 Abenteuerpunkte","Wirkung":"Auf Kleidung gemalt, oder besser noch in den Stoff gewebt, vermag dieses Ahnenzeichen den Träger vor Fernkampfangriffen zu schützen. Alle Proben auf Fernkampfangriffe gegen ihn sind um 1 erschwert.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"8 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Schutz wider Pfeilhagel","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/namensbogen.html","properties":{"Merkmal":"Keines","AP-Wert":"8 Abenteuerpunkte","Wirkung":"Zunächst muss auf einen Bogen ein Ahnenzeichen mit dem Namen des Schützen versehen werden. Dieser Schütze kann eine Erschwernis von 1 bei Schüssen mit diesem Bogen ignorieren. Darüber hinausgehende Erschwernisse gelten jedoch weiterhin.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"4 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Namensbogen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/windfang.html","properties":{"Merkmal":"Keines","AP-Wert":"8 Abenteuerpunkte","Wirkung":"Dieses Ahnenzeichen kann auf Segeltuch eingewebt oder aufgemalt werden. Es lockt freie Elementarwesen des Windes an, die dem Schiff zu größerer Geschwindigkeit verhelfen. Die GS des Schiffes wird dadurch um 10 % erhöht.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"8 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Windfang","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_Dingeerspueren.html","properties":{"Wirkungsdauer":"eine halbe Stunde","Merkmal":"Hellsicht","AP-Wert":"10 Abenteuerpunkte","Wirkung":"Der Vertraute kann ein Objekt ausfindig machen, das längere Zeit im Besitz der Hexe war und zu dem sie eine emotionale Bindung hat. Üblicherweise übermittelt die Hexe ihrem Vertrauten durch Zwiegespräch, welches Objekt er suchen soll. Befindet sich das Objekt im Umkreis von sieben Meilen, kann der Vertraute die Richtung ausmachen, in der sich das Objekt befindet, jedoch nicht zwingend den exakten Weg, über den man zu dem Objekt gelangt.","Publikation":"Regelwerk Seite 279","AsP-Kosten":"1 AsP","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Dinge erspüren","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/VSF_ErsterunterGleichen.html","properties":{"Wirkungsdauer":"eine halbe Stunde","Merkmal":"Einfluss","AP-Wert":"5 Abenteuerpunkte","Wirkung":"Der Vertraute kann Tiere seiner Art einschüchtern und vertreiben, solange sie nicht größer sind als er selbst.","Publikation":"Regelwerk Seite 279","AsP-Kosten":"3 AsP","Tierarten":"alle","Verbreitung":"Hexen"},"spellclass":"Vertrautentrick","name":"Erster unter Gleichen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/harterkern.html","properties":{"Merkmal":"Keines","AP-Wert":"2 Abenteuerpunkte","Wirkung":"Ein Gegenstand von maximal 5 Stein Gewicht erhält die doppelte Anzahl seiner üblichen Strukturpunkte.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"2 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Harter Kern","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/leichtwieeine.html","properties":{"Merkmal":"Keines","AP-Wert":"3 Abenteuerpunkte","Wirkung":"Wer auf einem Truhe, einer Kiste oder einem Korb dieses Ahnenzeichen anbringt, hat weniger zu tragen. Maximal darf ein solches Behältnis 4 Raumschritt bemessen. Das Gewicht von Gegenständen, die in diesen Behältern transportiert werden, ist 20 % leichter als üblich.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"4 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Leicht wie eine Feder","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/feinstesleder.html","properties":{"Merkmal":"Keines","AP-Wert":"4 Abenteuerpunkte","Wirkung":"Wird eine Lederrüstung mit diesem Ahnenzeichen versehen, können die zusätzlichen Abzüge auf GS und INI ignoriert werden.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"4 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Feinstes Leder","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/fischreichtum.html","properties":{"Merkmal":"Keines","AP-Wert":"3 Abenteuerpunkte","Wirkung":"Dieses Ahnenzeichen kann auf eine Angel, ein Netz oder eine Reuse gezeichnet oder geschnitzt werden. Zu Beginn muss dabei der Fluss oder See benannt werden, in dem gefischt wird. Wird das mit dem Ahnenzeichen versehene Gerät in dem benannten Gewässer benutzt, erhöht sich bei einer gelungenen Probe auf <em>Fischen &amp; Angeln (Süßwassertiere)</em> die Anzahl der erbeuteten Rationen pro Tag um 1 QS.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"4 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Fischreichtum","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/diebesschreck.html","properties":{"Merkmal":"Keines","AP-Wert":"4 Abenteuerpunkte","Wirkung":"Ein Schloss, das mit diesem Ahnenzeichen versehen ist, sorgt zum einen dafür, dass die Teilproben auf FF beim <em>Schlösserknacken</em> um 1 erschwert sind, zum anderen verletzt sich der Dieb bei einer misslungenen Probe und erleidet 1W3 SP.","Publikation":"Die Streitenden Königreiche Seite 172","AsP-Kosten":"4 AsP","Verbreitung":"Nostria"},"spellclass":"Ahnenzeichen","name":"Diebesschreck","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_W%C3%BCrze.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Illusion","Reichweite":"1 Schritt","Zielkategorie":"Objekte (Essen)","Publikation":"Die Streitenden Königreiche Seite 161","Wirkung":"Die Hexe kann mit diesem Zaubertrick eine einfache Speise, etwa eine Suppe, würzen, auch ohne über passende Gewürze wie Salz und Pfeffer zu verfügen. Ob die Speise gut gewürzt ist oder z. B. versalzen, hängt weiterhin von den Kochkünsten der Hexe ab. Die Hexe kann nur den Geschmack von Gewürzen erzeugen, die sie selbst schon einmal gekostet hat.","Verbreitung":"Hexen"},"spellclass":"Zaubertrick","name":"Würze","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Wasserhoehe.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Reichweite":"4 Schritt","Zielkategorie":"Zone","Publikation":"Aventurische Magie 114","Wirkung":"Der Zauberer kann sagen, ob ein Gewässer vor ihm tiefer ist, als er selbst groß ist.","Verbreitung":"Gildenmagier (Thorwaler)"},"spellclass":"Zaubertrick","name":"Wasserhöhe","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Trocken.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Elementar","Zielkategorie":"Objekt und Kulturschaffende","Reichweite":"selbst","Wirkung":"Der Zaubernde und seine Kleidung sind etwa 5 Minuten vor Nässe, beispielsweise durch Regen oder Schnee, geschützt. Nasse Kleidung wird durch den Trick nicht wieder trocken.","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Trocken","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_WeisheitdesLeytfadens.html","properties":{"Wirkungsdauer":"max. 10 Sekunden","Merkmal":"Illusion","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 114","Wirkung":"Die Hörer innerhalb einer Reichweite von 8 Schritt Radius um den Zauberer vernehmen eine Stimme, die ihnen eine Passage aus einem Zauberbuch vorliest. Der Text muss dem Zaubernden bekannt sein. Die Passage kann er selbst auswählen, sie muss aber innerhalb von 10 Sekunden vorlesbar sein. Nur die Personen, die die Passage hören sollen, hören sie auch, alle übrigen bekommen von der Belehrung nichts mit.","Verbreitung":"Gildenmagier (Alrik Dagabor)"},"spellclass":"Zaubertrick","name":"Weisheit des Leytfadens","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Signatur.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"Objekt","Reichweite":"Berührung","Wirkung":"Auf einem leblosen Objekt nach Wahl des Zaubernden erscheint eine Glyphe oder ein Symbol und verbleibt dauerhaft. Der Zaubernde muss das Objekt berühren. Das Symbol wirkt wie gemalt und kann weggeputzt werden. Mittels dieses Tricks kann man jeweils nur eine Art von Zeichen auftauchen lassen (z. B. ein Piktogramm).","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Signatur","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_UnheimlichesLachen.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Einfluss","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 114","Wirkung":"Das Lachen des Zauberers hört sich bedrohlich und unheimlich an. Weitere regeltechnische Auswirkungen hat das Lachen jedoch nicht.","Verbreitung":"Gildenmagier (Demirion Ophenos), Hexen"},"spellclass":"Zaubertrick","name":"Unheimliches Lachen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Waffens%C3%A4uberung.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Reichweite":"Berührung","Zielkategorie":"Objekte (Waffen)","Publikation":"Die Streitenden Königreiche Seite 161","Wirkung":"Dieser Zaubertrick reinigt eine beliebige Waffe, die der Zauberer berührt, vollständig von Flugrost, Blut und anderen Verschmutzungen.","Verbreitung":"Gildenmagier"},"spellclass":"Zaubertrick","name":"Waffensäuberung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Schminken.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 113","Wirkung":"Durch diesen Zaubertrick tragen Schminkutensilien die Schminke so wie gewünscht auf, ohne dass der Zauberer seine Hände benutzen muss. Das Ziel muss in einem ruhigen Zustand verharren, bis der Schminkvorgang vollendet ist.","Verbreitung":"Gildenmagier, Hexen, Scharlatane, Zaubertänzer"},"spellclass":"Zaubertrick","name":"Schminken","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Schnipsen.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Zielkategorie":"Objekt und Wesen","Reichweite":"4 Schritt","Wirkung":"Eine kurze telekinetische Entladung mit einer Reichweite von 4 Schritt, die beispielsweise eine Münze von einem Tisch fegen kann. Sie ist nicht stark genug, um eine Glasflasche zu zerbrechen, genügt aber für eine Ohrfeige.","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Schnipsen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_T%C3%A4towierung.html","properties":{"Wirkungsdauer":"1 Stunde","Merkmal":"Verwandlung","Reichweite":"Berührung","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 113","Wirkung":"Auf dem Körper des Verzauberten entsteht eine maximal 10 Halbfinger auf 10 Halbfinger große Tätowierung nach Wunsch des Zauberers. Bei den Tätowierungen muss es sich um einfache Runen oder einfache Muster handeln.","Verbreitung":"Gildenmagier"},"spellclass":"Zaubertrick","name":"Tätowierung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Schlangenh%C3%A4nde.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Wirkung":"Die Hand- und Fingerknochen des Zaubernden werden sehr flexibel, sodass sie durch enge Öffnungen gezwängt werden können. Die Wirkung hält 5 Minuten an.","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Schlangenhände","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Regenbogenaugen.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Illusion","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Wirkung":"Die Augenfarbe des Zaubernden ändert sich für 5 Minuten. Es sind unnatürliche Farben möglich.","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Regenbogenaugen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Sandfigur.html","properties":{"Wirkungsdauer":"1 Minute","Merkmal":"Elementar","Reichweite":"1 Schritt","Zielkategorie":"Objekte (Sand)","Publikation":"Aventurische Magie 113","Wirkung":"Eine Handvoll Sand beginnt sich zu einer ca. 10 Halbfinger großen Figur zu formen, die nach dem Willen des Zauberers kleine Tricks zur Unterhaltung aufführt. Die Sandfigur muss in einem Radius von 10 Halbfingern verbleiben.","Verbreitung":"Gildenmagier (Vadif)"},"spellclass":"Zaubertrick","name":"Sandfigur","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SF_Ordentlich.html","properties":{"Wirkungsdauer":"6 Stunden","Merkmal":"Telekinese","Reichweite":"selbst","Zielkategorie":"Objekte (Kleidung)","Publikation":"Aventurische Magie 113","Wirkung":"Dieser Zaubertrick sorgt dafür, dass die Kleidung einer Person ordentlich sitzt.","Verbreitung":"Gildenmagier (Halle der Antimagie zu Kuslik)"},"spellclass":"Zaubertrick","name":"Ordentlich","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Pflanzenempathie.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Reichweite":"Berührung","Zielkategorie":"Pflanzen","Publikation":"Die Streitenden Königreiche Seite 161","Wirkung":"Berührt der Druide eine Pflanze, kann er sich ein Bild von ihrem Gesundheitszustand machen und herausfinden, ob sie von Schädlingen befallen, vergiftet wurde oder Ähnliches.","Verbreitung":"Druiden"},"spellclass":"Zaubertrick","name":"Pflanzenempathie","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Selbstarchivierung.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Reichweite":"16 Schritt","Zielkategorie":"Objekte (Bücher)","Publikation":"Aventurische Magie 113","Wirkung":"Ein aus einer Bibliothek geliehenes Buch fliegt wieder dorthin, woher es der Zauberer aus dem Regal entnommen hat. Dieser Zaubertrick funktioniert nur bei Büchern, die der Zauberer selbst ausgeliehen und aus der Bibliothek entnommen hat.","Verbreitung":"Gildenmagier (Lowangen Verwandlung)"},"spellclass":"Zaubertrick","name":"Selbstarchivierung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Rohrstock.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Reichweite":"4 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 113","Wirkung":"Dieser Zaubertrick sorgt dafür, dass jemand einen kleinen Schlag auf die Finger abbekommt, ganz so, als ob ihn jemand mit einem Rohrstock geschlagen hat. Schaden erleidet das Ziel dadurch nicht.","Verbreitung":"Gildenmagier (Akademie Schwert &amp; Stab zu Gareth)"},"spellclass":"Zaubertrick","name":"Rohrstock","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Lockruf.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Tiere","Reichweite":"4 Schritt","Wirkung":"Ein kleines Tier (Eichhörnchen, Taube) kommt neugierig auf den Zaubernden zu. Die Wirkung hält 5 Minuten an.","Publikation":"Regelwerk Seite 287","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Lockruf","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Luftsto%C3%9F.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Reichweite":"selbst","Zielkategorie":"Objekte (kleine Flammen)","Publikation":"Aventurische Magie 113","Wirkung":"Ein kleiner Lufthauch bläst Kerzen und ähnlich große Flammen innerhalb eines Radius von 8 Schritt um den Zauberer aus.","Verbreitung":"Elfen, Gildenmagier"},"spellclass":"Zaubertrick","name":"Luftstoß","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Leselampe.html","properties":{"Wirkungsdauer":"30 Minuten","Merkmal":"Elementar","Reichweite":"1 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Die Streitenden Königreiche Seite 160","Wirkung":"Aus dem rechten Zeigefinger des Magiers leuchtet ein schwaches Licht. Das Licht ist nicht hell genug, um Sichtmodifikatoren zu ändern, aber es reicht aus, um in der Dunkelheit ein Buch zu lesen.","Verbreitung":"Gildenmagier"},"spellclass":"Zaubertrick","name":"Leselampe","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Nagellack.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 113","Wirkung":"Durch diesen Trick kann der Zauberer beliebig viele seiner Finger- oder Fußnägel in einer beliebigen Farbe lackieren.","Verbreitung":"Gildenmagier (Al’Anfa), Hexen"},"spellclass":"Zaubertrick","name":"Nagellack","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Hintergrundmusik.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Illusion","Reichweite":"selbst","Zielkategorie":"Zone","Publikation":"Aventurische Magie 112","Wirkung":"In einem Radius von 16 Schritt um den Zauberer ist eine angenehme, zum Ort passende, Hintergrundmusik zu hören. Das Stück kann der Zauberer selbst bestimmten, es muss aber zum Ambiente passen.","Verbreitung":"Gildenmagier (Zorgan), Zauberbarden"},"spellclass":"Zaubertrick","name":"Hintergrundmusik","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Kehrbesen.html","properties":{"Wirkungsdauer":"30 Minuten","Merkmal":"Telekinese","Reichweite":"8 Schritt","Zielkategorie":"Objekte (Besen)","Publikation":"Die Streitenden Königreiche Seite 160","Wirkung":"Die Hexe kann einem beliebigen Besen innerhalb der Reichweite befehlen, den Boden in einem Radius von 8 Schritt zu kehren. Der Besen weicht dabei Hindernissen aus und versucht Lebewesen der Größenkategorie klein und größer aus dem Weg zu gehen. Wird der Besen berührt, endet der Zaubertrick augenblicklich.","Verbreitung":"Hexen"},"spellclass":"Zaubertrick","name":"Kehrbesen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Handw%C3%A4rmer.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Elementar","Zielkategorie":"Objekt","Reichweite":"Berührung","Wirkung":"Dieser Zaubertrick hält die Temperatur eines maximal faustgroßen Objektes konstant, so lange es in der Hand gehalten wird, maximal jedoch 5 Minuten.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Handwärmer","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Haarpracht.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Der Zauberer kann die Farbe seines Haupthaars nach Belieben bestimmen. Gleiches gilt für die Länge, solange sie 1 Schritt nicht überschreitet. Schneidet der Zauberer die durch den Zaubertrick gewachsenen Haare ab, lösen sich diese sofort auf. Nachwachsende Haare sind von der Veränderung nicht betroffen.","Verbreitung":"Hexen"},"spellclass":"Zaubertrick","name":"Haarpracht","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Insektenbann.html","properties":{"Wirkungsdauer":"6 Stunden","Merkmal":"Verwandlung","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 113","Wirkung":"Die Zauberin wird nicht von einfachen Insekten wie Fliegen oder Mücken belästigt. Der Zaubertrick schützt nicht vor Moskitos oder anderen Insekten, die bei Helden Schaden verursachen, Krankheiten übertragen oder ihnen gefährlich werden können. Die Insekten müssen der Größenkategorie winzig angehören.","Verbreitung":"Hexen, Gildenmagier (Sinoda)"},"spellclass":"Zaubertrick","name":"Insektenbann","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_HellerStern.html","properties":{"Wirkungsdauer":"5 Sekunden","Merkmal":"Hellsicht","Reichweite":"8 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Der Zauberer zeigt auf einen Stern oder ein Sternzeichen am Himmel, der daraufhin kurz für den Zauberer und das Ziel aufleuchtet.","Verbreitung":"Gildenmagier (Töchter Niobaras)"},"spellclass":"Zaubertrick","name":"Heller Stern","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_GutenMorgen.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Einfluss","Reichweite":"4 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Das Ziel des Zaubertricks erwidert freundlich den Gruß des Zauberers. Der Trick funktioniert nur bei Personen, die dem Zauberer zumindest neutral gegenüber eingestellt sind.","Verbreitung":"Gildenmagier (Hesindius Lichtblick)"},"spellclass":"Zaubertrick","name":"Guten Morgen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Gru%C3%9Fworte.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Einfluss","Reichweite":"8 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Mit diesem Zaubertrick kann der Zauberer telepathisch jemandem einige Grußworte übermitteln.","Verbreitung":"Elfen, Gildenmagier (Donnerbach)"},"spellclass":"Zaubertrick","name":"Grußworte","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Hexenblick.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Reichweite":"8 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Die Streitenden Königreiche Seite 160","Wirkung":"Mittels Hexenblick kann eine Hexe herausfinden, ob eine andere Person ebenfalls die hexische Tradition beherrscht. Sollte das ausgewählte Ziel die Tradition besitzen, so leuchten ihre Augen kurz Purpur auf, was aber nur für die zaubernde Hexe sichtbar ist.","Verbreitung":"ausschließlich Hexen"},"spellclass":"Zaubertrick","name":"Hexenblick","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Feuerfinger.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Elementar","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Wirkung":"Eine kleine Flamme entsteht etwa einen Zentimeter über einem Finger des Zaubernden. Sie brennt bis zu 5 Minuten. Der Zaubertrick schützt die Hand nicht vor der Hitze der Flamme.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Feuerfinger","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Gl%C3%BCcksgriff.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Zielkategorie":"Objekt","Reichweite":"selbst","Wirkung":"Der Zaubernde kann aus einer überschaubaren Menge Objekte gezielt das gewünschte herausgreifen. Er kann aus einem Kartenstapel eine bestimmte Karte ziehen, aus einem Beutel eine gewünschte Münze oder von einem Schlüsselbund den richtigen Schlüssel. Er muss dazu das gewünschte Objekt kennen und dieses muss auch wirklich an dem Ort vorhanden sein.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Glücksgriff","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Duft.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Illusion","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Wirkung":"Der Zauberer riecht für etwa 5 Minuten nach einem Parfüm oder einem anderen für ihn angenehmen Geruch.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Duft","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Feenfuesse.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Telekinese","Reichweite":"16 Schritt","Zielkategorie":"Objekte","Publikation":"Aventurische Magie 112","Wirkung":"Einem maximal 1 Stein wiegenden Objekt wachsen ein oder mehrere Paar kurze Beine, die es auf Befehl des Zauberers umhertragen.","Verbreitung":"keine"},"spellclass":"Zaubertrick","name":"Feenfüße","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Bl%C3%BCtenduft.html","properties":{"Wirkungsdauer":"1 Minute","Merkmal":"Illusion","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"In einem Radius von 16 Schritt um den Magier verströmt die Umgebung einen angenehmen Geruch nach Blüten und Waldesgrün.","Verbreitung":"Elfen, Gildenmagier (Gerasim)"},"spellclass":"Zaubertrick","name":"Blütenduft","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Ehrf%C3%BCrchtigesVerhalten.html","properties":{"Wirkungsdauer":"30 Sekunden","Merkmal":"Einfluss","Reichweite":"8 Schritt","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Ein Diener, Bauer oder eine andere, gegenüber dem Zauberer mit Ehrfurcht erfüllte Person weicht ihm sich verneigend aus. Personen, auf die dies nicht zutrifft, sind von dem Zaubertrick nicht betroffen.","Verbreitung":"Gildenmagier (Fasar Beherrschung)"},"spellclass":"Zaubertrick","name":"Ehrfürchtiges Verhalten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Bauchreden.html","properties":{"Wirkungsdauer":"1 Aktion","Merkmal":"Illusion","Zielkategorie":"Zone","Reichweite":"8 Schritt","Wirkung":"Ein einzelnes Wort oder ein anderes kurzes, nicht allzu lautes Geräusch nach Wahl erklingt bis zu 8 Schritt entfernt vom Zaubernden.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Bauchreden","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_BeerenundN%C3%BCsse.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Einfluss","Reichweite":"64 Schritt","Zielkategorie":"Tiere","Publikation":"Die Streitenden Königreiche Seite 159","Wirkung":"Mit Hilfe dieses Zaubertricks sendet der Druide einen Befehl an ein nahes, kleines Tier (z. B. Eichhörnchen, Amsel), das ihm eine Nuss oder einige Beeren bringt. Zum Sattwerden reicht dies jedoch nicht aus, erst 30 Anwendungen des Zaubertricks ergeben eine Ration Nahrung.","Verbreitung":"Druiden"},"spellclass":"Zaubertrick","name":"Beeren & Nüsse","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Bartwuchs.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Reichweite":"selbst","Zielkategorie":"Kulturschaffende","Publikation":"Die Streitenden Königreiche Seite 159","Wirkung":"Der Druide kann seinen Bart augenblicklich auf bis zu 1 Schritt Länge wachsen lassen. Normales und magisches Haar unterscheidet sich optisch nicht voneinander. Schneidet der Druide den durch den Zaubertrick gewachsenen Bart ab, lösen sich die abgeschnittenen Haare sofort auf.","Verbreitung":"Druiden"},"spellclass":"Zaubertrick","name":"Bartwuchs","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Beruhigung.html","properties":{"Wirkungsdauer":"1 Stunde","Merkmal":"Heilung","Reichweite":"Berührung","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Ein schreiender Patient wird für kurze Zeit still, seine Angst verschwindet in dieser Zeit aber nicht. Das Ziel muss vorher den Status <em>Fixiert </em>erlitten haben, damit der Zaubertrick funktioniert.","Verbreitung":"Gildenmagier (Perricum)"},"spellclass":"Zaubertrick","name":"Beruhigung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Warzensprie%C3%9Fen.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Verwandlung","Probe":"IN/FF/KO (modifiziert durch ZK)","Wirkung":"Dem Opfer wachsen innerhalb einiger Minuten dicke Warzen im Gesicht und an den Händen und verleihen ihm so eine Stufe des Nachteils Hässlich. Der Vorteil Gutaussehend wird hierdurch zeitweise Hässlich.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"7 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Warzen sprießen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_AranischeRasur.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Reichweite":"Berührung","Zielkategorie":"Kulturschaffende","Publikation":"Aventurische Magie 112","Wirkung":"Körperhaare werden durch diesen Zaubertrick entfernt. Der Zauberer kann festlegen, dass bestimmte Körperstellen nicht enthaart werden (beispielsweise, damit Augenbrauen oder ein Bart nicht entfernt werden).","Verbreitung":"Gildenmagier (Khelbara ay Baburia)"},"spellclass":"Zaubertrick","name":"Aranische Rasur","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/1178.html","properties":{"Wirkungsdauer":"QS x 3 in Wochen","Merkmal":"Verwandlung","Probe":"IN/FF/KO (modifiziert durch ZK)","Wirkung":"Das Opfer, Person oder Tier, ist nicht mehr in der Lage, Nachkommen zu zeugen oder zu gebären. Schwangere Wesen erleiden oft eine Miss- oder Totgeburt.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Unfruchtbarkeit","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Pestilenz.html","properties":{"Wirkungsdauer":"bis zum Ende der Krankheit, maximal aber QS x 3 in Tagen","Merkmal":"Dämonisch","Probe":"MU/CH/GE (modifiziert durch ZK)","Wirkung":"Das Opfer wird von der Hexe mit einer ihr bekannten Krankheit infiziert, maximal jedoch mit einer Krankheitsstufe gleich dem Fertigkeitswert des Fluches. Ab der Infektion verläuft die Krankheit wie normal, ist allerdings nicht ansteckend. Nicht wählbar sind magische Erkrankungen wie Duglumspest.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"Stufe der Krankheit in AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Pestilenz","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZT_Abk%C3%BChlung.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Objekt","Reichweite":"Berührung","Wirkung":"Der Zauberer senkt die Temperatur einer kleinen Menge Flüssigkeit (maximal ein Becher voll) um bis zu 10 Grad.","Publikation":"Regelwerk Seite 286","Verbreitung":"Allgemein"},"spellclass":"Zaubertrick","name":"Abkühlung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Schlafrauben.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Einfluss","Probe":"KL/IN/CH (modifiziert durch SK)","Wirkung":"Das Opfer kann nicht schlafen. Wenn es doch für einige Sekunden einschläft, wird es von Alpträumen wieder aufgeschreckt. Regeneration ist in diesem Zustand nicht möglich. Es reagiert genauso wie andere Menschen, die lange Zeit wach gehalten werden, wird also fahrig, reizbar und erleidet nach einigen Tagen Sinnestäuschungen. Pro zwei Tagen ohne Schlaf erhält das Opfer eine Stufe <em>Verwirrung</em>, die erst abklingt, wenn es wieder schlafen kann.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Schlaf rauben","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_PechandenHalswuenschen.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Einfluss","Probe":"KL/IN/CH (modifiziert durch SK)","Wirkung":"Neben der 20 gilt nun auch die 19 als Patzer. Für Patzer bei Fertigkeitsproben reicht es so, wenn zwei Würfel eine 19 oder mehr zeigen. Außerdem zerbricht das Opfer aus Versehen Gegenstände, stolpert über Hindernisse, vergisst Verabredungen oder tritt bei Gesprächen in Fettnäpfchen. Details entscheidet der Meister.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"7 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Pech an den Hals wünschen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Hexenschuss.html","properties":{"Wirkungsdauer":"QS x 3 in Stunden","Merkmal":"Verwandlung","Probe":"IN/FF/KO (modifiziert durch ZK)","Wirkung":"Das Opfer erleidet plötzlich einen starken Schmerz im Rücken oder im Kopf, je nach Wahl der Hexe. Durch den Hexenschuss erleidet das Opfer eine Stufe des Zustands <em>Schmerz</em>.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Hexenschuss","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Hagelschlag.html","properties":{"Wirkungsdauer":"QS x 15 Minuten","Merkmal":"Elementar","Probe":"MU/IN/CH","Wirkung":"Auf einer Fläche von bis zu (QS x 20.000) Rechtschritt geht ein schwerer Hagelsturm nieder. Je nach Region kann auch ein Schnee- oder Sandsturm die Fläche verheeren. Solche Stürme können Ernten zerstören, Dächer abdecken und bei ungeschützten Tieren und Personen 2W6 TP Schaden pro 5 Minuten verursachen.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"21 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Hagelschlag","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/reegregre.html","properties":{"Merkmal":"Keines","Schutzkreis":"Elementar- und Mindergeister können den Kreis nicht betreten.","AP-Wert":"5 Abenteuerpunkte","Publikation":"Regelwerk Seite 266","AsP-Kosten":"4 AsP","Bannkreis":"Der erste Elementargeist, der sich dem Kreis auf 7 Schritt nähert, wird in ihn hineingezogen und kann ihn während der Wirkungsdauer nicht mehr verlassen.","Verbreitung":"Gildenmagier"},"spellclass":"Bann und Schutzkreis","name":"Bann- und Schutzkreis gegen Elementargeister","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/dsfdsfdsfdf.html","properties":{"Merkmal":"Keines","Schutzkreis":"Dschinne können den Kreis nicht betreten.","AP-Wert":"15 Abenteuerpunkte","Publikation":"Regelwerk Seite 266","AsP-Kosten":"8 AsP","Bannkreis":"Der erste Dschinn, der sich dem Kreis auf 7 Schritt nähert, wird in ihn hineingezogen und kann ihn während der Wirkungsdauer nicht mehr verlassen.","Verbreitung":"Gildenmagier"},"spellclass":"Bann und Schutzkreis","name":"Bann- und Schutzkreis gegen Dschinne","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/refregrefer.html","properties":{"Merkmal":"Keines","Schutzkreis":"Gehörnte Dämonen mit bis zu 5 Hörnern können den Kreis nicht betreten.","AP-Wert":"15 Abenteuerpunkte","Publikation":"Regelwerk Seite 266","AsP-Kosten":"8 AsP","Bannkreis":"Der erste gehörnte Dämon mit bis zu 5 Hörnern, der sich dem Kreis auf 7 Schritt nähert, wird in ihn hineingezogen und kann ihn nicht mehr verlassen.","Verbreitung":"Gildenmagier"},"spellclass":"Bann und Schutzkreis","name":"Bann- und Schutzkreis gegen gehörnte Dämonen","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Bei%C3%9FaufGranit.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Objekt","Probe":"KL/IN/FF (modifiziert durch ZK)","Wirkung":"Feste Nahrungsmittel, die das Opfer berührt, werden zu Stein oder einer anderen Substanz, die die Hexe im Fluch benannt hat. Üblich sind neben Stein auch Holz, Mist oder Schleim. Edelmetalle oder Ähnliches sind nicht möglich. Die Lebensmittel verwandeln sich nach fünf Minuten wieder zurück, insofern das Opfer sie nicht berührt. Der Versuch, Lebensmittel mit Zangen oder Trichtern in den Mund zu führen, kann dazu führen, dass das Opfer an einem Stein im Hals erstickt. Für Suppen, Milch und andere Flüssigkeiten gilt der Fluch nicht.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Beiß auf Granit!","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_Sorgenlied.html","properties":{"Merkmal":"Hellsicht","Steigerungsfaktor":"A","Talent":"Singen","Probe":"MU/IN/IN","Wirkung":"Der Elf flicht Fragen in das Lied ein, die er an einen guten Freund stellen möchte. Hierdurch kann er erfahren, ob sein Freund gesund, krank oder gar tot ist, ob er um sein Leben fürchtet oder ob er zufrieden und glücklich ist. Mit dem Lied sind nur allgemeine Eindrücke zu erzielen. Klar formulierte Gedankenbilder lassen sich so nicht übertragen. Je weiter der gesuchte Freund vom Sänger entfernt ist, desto länger dauert das Lied. Pro 100 Meilen muss er 5 Minuten singen, um ihn zu erreichen.","Publikation":"Regelwerk Seite 284","AsP-Kosten":"2 AsP pro 5 Minuten","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Sorgenlied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_MelodiederKunstfertigkeit.html","properties":{"Merkmal":"Heilung","Steigerungsfaktor":"B","Talent":"Singen","Probe":"IN/IN/CH","Wirkung":"Proben auf Handwerkstalente, die der Elf während der Melodie der Kunstfertigkeit nutzt, sind um QS des Rituals erleichtert.","Publikation":"Regelwerk Seite 284","AsP-Kosten":"4 AsP pro Stunde","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Melodie der Kunstfertigkeit","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/HSF_Beute.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Einfluss","Probe":"KL/IN/CH (modifiziert durch SK)","Wirkung":"Der Geruch des Opfers lockt alle Arten von fleischfressenden Wirbeltieren an. Diese sehen das Opfer als leichte Beute an. Während domestizierte Tiere mit einer um die Fertigkeitspunkte des Fluches erschwerten Tierkundeprobe beruhigt werden können, lassen sich wilde Tiere hiervon nicht abbringen. Der nur für die Tiere wahrnehmbare Geruch ist so stark, dass Wildtiere das Opfer bereits aus mehreren Meilen Entfernung wahrnehmen können.","Publikation":"Regelwerk Seite 282","AsP-Kosten":"14 AsP","Verbreitung":"Hexen"},"spellclass":"Fluch","name":"Beute!","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_Freundschaftslied.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"A","Talent":"Musizieren","Probe":"IN/CH/CH","Wirkung":"Mit dem Freundschaftslied können zwei Elfen, die es gemeinsam spielen, ein festes Band der Freundschaft zwischeneinander weben. Dies ist nur einmal im Leben eines Elfen möglich. Es besteht aus drei einstündigen Strophen, die an drei aufeinanderfolgenden Tagen von beiden gemeinsam gespielt werden müssen. So miteinander verbundene Elfen spüren die Anwesenheit des anderen und können starke Emotionen des anderen auch auf größere Entfernungen wahrnehmen. Sie können gegenseitig den Zauberspruch BALSAM aufeinander wirken, selbst wenn sie keine AsP zur Verfügung haben. In solchen Fällen können sie mit dem Zauberspruch ihre Lebensenergie direkt auf den anderen übertragen.","Publikation":"Regelwerk Seite 284","AsP-Kosten":"4 AsP pro Stunde, 1 permanenter AsP bei der letzten Strophe","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Freundschaftslied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_Erinnerungsmelodie.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"A","Talent":"Musizieren","Probe":"KL/IN/IN","Wirkung":"Mit dem Lied ruft sich der Elf eine Erinnerung aus lang vergangener Zeit zurück. Für je ein Jahr, das das Ereignis zurückliegt, muss er 5 Minuten spielen. Zusätzlich spielt er so lange, wie die Situation dauert, an die er sich zurückentsinnen will.","Publikation":"Regelwerk Seite 283","AsP-Kosten":"1 AsP pro 5 Minuten","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Erinnerungsmelodie","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Wirbelform.html","properties":{"Wirkungsdauer":"QS x 10 Minuten","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"KL/FF/KK","Wirkung":"Innerhalb eines Radius von QS Schritt vermag der Zauberer alle Materie des Elements Luft zu verformen, solange sie nicht Teil eines Wesens ist. Auf diesem Weg kann eine Zone erschaffen werden, in der ein Sturm nicht wütet, aber auch die Formung von Rauch und Dampf ist möglich. Der Prozess geht dabei nur langsam vonstatten. Soll die Luft in eine bestimmte Richtung geformt werden, dauert dies 1 KR pro Schritt Fortbewegung. Am Ende der Wirkungsdauer ist das Verformte wieder den normalen Gesetzen der Natur ausgeliefert.","AsP-Kosten":"16 AsP","Publikation":null,"Steigerungsfaktor":"B","Verbreitung":"allgemein","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Wirbelform","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 2 AP): Die Wirkungsdauer beträgt QS Stunden."],"1":["Größerer Radius ","(FW 12, 4 AP): Der Radius beträgt QS x 2 Schritt."],"2":["Längere Wirkungsdauer 2 ","(FW 16, 6 AP): Die Wirkungsdauer beträgt QS x 2 Stunden. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Xenographus.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Hellsicht","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"MU/KL/IN","Wirkung":"Liest der Zauberer einen Text, erhält er ein Gefühl für die Bedeutung des Niedergelegten, selbst wenn er die Schrift oder Sprache nicht beherrscht. Damit ist es ihm möglich, jeden Text zu verstehen, jedoch lediglich auf dem Niveau einer Person, die die entsprechende Sprache auf Stufe II beherrscht. Die Wirkung umfasst lediglich das geschriebene Wort, nicht jedoch das gesprochene. Ebenso bietet das Ritual keine Hilfestellung bei Schwierigkeiten, die nicht sprachlicher Natur sind, etwa wenn der Text verschlüsselt, in Rätseln verfasst, unleserlich oder unzusammenhängend geschrieben wurde. Das Erlernen einer Sprache oder einer Schrift ist auf diesem Wege nicht möglich, da lediglich die Gesamtbedeutung eines Textes erfasst werden kann, nicht jedoch die Bedeutung einzelner Elemente.","AsP-Kosten":"8 AsP","Publikation":null,"Steigerungsfaktor":"A","Verbreitung":"Gildenmagier, Scharlatane","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Xenographus","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 1 AP): Die Wirkungsdauer beträgt QS x 30 Minuten."],"1":["Längere Wirkungsdauer 2 ","(FW 12, 2 AP): Die Wirkungsdauer beträgt QS Stunden. Voraussetzung: Zaubererweiterung "],"2":["Besseres Sprachverständnis ","(FW 16, 3 AP): Man kann den Text so verstehen, wie eine Person, welche die Sprache auf Stufe III beherrscht."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Widerwille.html","properties":{"Wirkungsdauer":"QS Stunden","Merkmal":"Einfluss","Zielkategorie":"Objekte","Reichweite":"16 Schritt","Probe":"IN/CH/GE","Wirkung":"Personen mit SK 2 oder schlechter ignorieren ein vom Zauberer bestimmtes Objekt, selbst wenn es sich direkt vor ihnen befindet. Kommt es zu einer Interaktion mit dem Opfer, etwa wenn dieses mit einem bislang ignorierten Schwert angegriffen wird, endet die Wirkung jedoch. Ein einfaches Berühren oder Ertasten reicht hierfür aber nicht aus. Der Zauberer selbst nimmt das Objekt immer wahr, auch wenn der Zauber wirkt. Es können lediglich kleinere Gegenstände bis zur Größe einer Zweihandwaffe oder eines Seils verzaubert werden, nicht jedoch Truhen, Türen oder gar ganze Gebäude.","AsP-Kosten":"8 AsP","Publikation":null,"Steigerungsfaktor":"B","Verbreitung":"Gildenmagier, Scharlatane","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Widerwille","spellextensions":{"0":["Nicht alle ","(FW 8, 2 AP): Der Zauberer kann beliebig viele Leute benennen, die nicht von der Wirkung betroffen sind."],"1":["Größere Objekte ","(FW 12, 4 AP): Der Zauberer kann auch Objekte bis zur Größe einer Tür verzaubern."],"2":["Längere Wirkungsdauer ","(FW 16, 6 AP): Die Wirkungsdauer beträgt QS Tage."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ESF_Friedenslied.html","properties":{"Merkmal":"Einfluss","Steigerungsfaktor":"A","Talent":"Musizieren","Probe":"MU/IN/CH","Wirkung":"Nach 5 Minuten des Spielens erzeugt dieses Lied eine Zone des Friedens um den Elfen herum (QS x 3 Schritt Radius). So lange er weiterspielt, verlieren Tiere und kulturschaffende Wesen das Interesse daran, mit Gewalt gegeneinander oder gegen den Elfen vorzugehen. Um trotzdem aktiv zu kämpfen, ist eine Fertigkeitsprobe auf <em>Willenskraft</em>, erschwert durch die QS x 2 in der Probe auf das Friedenslied fällig. Die Möglichkeit, sich zu verteidigen, ist durch das Friedenslied nicht eingeschränkt. Nicht betroffen sind widernatürliche Wesenheiten wie Untote, Dämonen, Elementare oder Golems.","Publikation":"Regelwerk Seite 283","AsP-Kosten":"2 AsP pro 5 Minuten","Verbreitung":"Elfen"},"spellclass":"Elfenlied","name":"Friedenslied","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Traumgestalt.html","properties":{"Wirkungsdauer":"QS x 10 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"32 Schritt*","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Zauberer dringt in die Träume des Ziels ein und kann diese manipulieren. Der Zauberer kann QS einsetzen, um seine Werte im Traum zu verbessern oder sich andere Vorteile zu verschaffen. Dazu muss er die angegebene Menge an QS investieren. Die Boni sind kumulativ.<ul><li>für 1 QS: +2 TP oder + 1 AW oder +2 GS oder +1 auf MU, KL, IN oder CH oder +1 ZK</li><li>für 2 QS: +1 RS oder SK oder +1 auf alle AT oder +1 auf alle PA, oder der Zauberer hat nach Wunsch eine andere Gestalt im Traum des Opfers</li><li>#für 3 QS: Der Zauberer ist im Traum getarnt und kann nicht entdeckt werden. Er kann aber diese Tarnung jederzeit selbst aufgeben.</li></ul>","AsP-Kosten":"16 AsP","Publikation":"Der schwarze Forst Seite 33","Steigerungsfaktor":"C","Verbreitung":"Druiden, Elfen, Hexen","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Traumgestalt","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_OrvaiKurimsKriegstrommel.html","properties":{"Wirkungsdauer":"QS x 3 in Tagen","Merkmal":"Objekt","Zielkategorie":"profane Objekte (Trommeln)","Reichweite":"Berührung","Probe":"IN/FF/KO","Wirkung":"Der Klang der Trommel versetzt Angehörige der Spezies in Raserei, aus deren Haut das Trommelfell gefertigt wurde. Wenn ihnen keine Probe auf <em>Willenskraft (Bedrohungen</em> <em>standhalten) </em>erschwert um die QS des Rituals gelingt, erhalten alle Kulturschaffenden oder Tiere den Status <em>Blutrausch </em>(siehe Regelwerk Seite 35). Der Wirkungsradius beträgt 16 Schritt. Zum Schlagen der Trommel benötigt man 1 Aktion, der Blutrausch dauert für 2W20+QS KR an.","AsP-Kosten":"16 AsP","Publikation":"Der weiße See Seite 9","Steigerungsfaktor":"C","Verbreitung":"Goblinzauberinnen","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Orvai Kurims Kriegstrommel","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_RufdesBienenstocks.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"profane Objekte","Reichweite":"FW x 50 Meilen","Probe":"KL/IN/FF","Wirkung":"Durch Berührung einer Münze oder Ware, die gegen das gesuchte Objekt getauscht wurde, erfährt man Richtung und Entfernung des Objekts in Tagesreisen. Jedes angefangene Jahr, in dem die Zibilja (oder ihre Sippe) das Objekt vorher besaß, erleichtert die Probe um 1, jedes angefangene Jahr nach dem Verkauf erschwert sie um 1 (insgesamt maximal +/–5).","AsP-Kosten":"16 AsP","Publikation":"Der weiße See Seite 5","Steigerungsfaktor":"C","Verbreitung":"Zibilja","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Ruf des Bienenstocks","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_UnberuehrtvonSatinav.html","properties":{"Wirkungsdauer":"QS x 3 Wochen","Merkmal":"Zeit","Zielkategorie":"Objekte","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Ein maximal 5 Stein wiegendes Objekt ist während der Wirkungsdauer vor Verfall geschützt. Die Zeit kann ihm nichts anhaben und es wird also weder verrotten, verrosten noch verfallen. Dies gilt auch in widrigen Umgebungen, etwa überdurchschnittlich feuchten oder heißen Gegenden. Vor gewaltsamen Einflüssen wie Waffenschaden, Feuer oder Säure bietet die Verzauberung jedoch keinen Schutz.","AsP-Kosten":"16 AsP","Publikation":null,"Steigerungsfaktor":"B","Verbreitung":"Kristallomanten","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Unberührt von Satinav","spellextensions":{"0":["Höheres Gewicht (","FW 8, 2 AP): Das Gewicht darf bis zu 10 Stein betragen."],"1":["Längere Wirkungsdauer ","1 (FW 12, 4 AP): Die Wirkungsdauer erhöht sich auf QS x 3 Monate."],"2":["Längere Wirkungsdauer ","2 (FW 16, 6 AP): Die Wirkungsdauer erhöht sich auf QS x 3 Jahre. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_RuheKoerper.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/CH/KO","Wirkung":"Die während der nächsten Regenerationsphase zurückgewonnenen LeP erhöhen sich um die doppelte QS. Zudem verläuft der Schlaf ruhig und traumlos, solange äußere Einflüsse dem nicht entgegenwirken. Der Zauber kann nicht verhindern, dass Gifte und Krankheiten in der Regenerationsphase wirken.","AsP-Kosten":"4 AsP (Kosten sind nicht modifizierbar)","Publikation":null,"Steigerungsfaktor":"B","Verbreitung":"Elfen, Gildenmagier","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Ruhe Körper","spellextensions":{"0":["Kosten modifizierbar ","(FW 8, 2 AP): Die Kosten sind modifizierbar."],"1":["Kurze Regenerationsphase ","(FW 12, 4 AP): Unter der Wirkung des Zaubers dauert eine Regenerationsphase nur 4 Stunden statt der üblichen 6."],"2":["Längerfristige Wirkung ","(FW 16, 6 AP): Bei der übernächsten Regenerationsphase nach dem Zauber bekommt das Ziel QS LeP dazu. Die ursprüngliche Zauberwirkung kommt ebenfalls zum Tragen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Nihilogravo.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Telekinese","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"KL/IN/KO","Wirkung":"Der Zauberspruch sorgt für eine zylinderförmige Zone, in der die Schwerkraft aufgehoben ist. Der Zauberer muss einen Punkt innerhalb der Reichweite des Zauberspruchs benennen. Von dort aus wirkt der Zauber in einem Radius von QS in Schritt. Die Höhe der schwerelosen Zone beträgt QS x 5 Schritt. Durch die ungewohnte Umgebung sind alle Proben innerhalb dieser Zone um 1 erschwert. Um sich gezielt in der Schwerelosigkeit bewegen zu können, ist eine Probe auf Schwimmen (Tauchen) notwendig. Pro erreichter QS schafft man 1 Schritt. Unfälle, Patzer oder Einwirkungen nach Meisterentscheid können dazu führen, dass eine Person unbeabsichtigt aus der Zone gerät und stürzt. Sollten sich am Ende der Wirkungsdauer noch Wesen in der Zone der Schwerelosigkeit befinden, stürzen diese herunter und erleiden Sturzschaden (siehe Regelwerk Seite 340). Über die Sturzhöhe entscheidet der Meister.","AsP-Kosten":"16 AsP","Publikation":"Die Streitenden Königreiche Seite 168","Steigerungsfaktor":"B","Verbreitung":"Gildenmagier","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Nihilogravo","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Movimento.html","properties":{"Wirkungsdauer":"8 Stunden (Wirkungsdauer nicht modifizierbar)","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/IN/KO","Wirkung":"Während der Wirkungsdauer kann sich der Zauberer beliebig lange im Dauerlauf fortbewegen, ohne dass ihn dies erschöpft. Die Reisestrecke erhöht sich um QS x 10 % für eine Tagesleistung Fußmarsch (siehe <strong>Strategische Bewegung </strong>im <strong>Regelwerk </strong>auf Seite <strong>248</strong>).","AsP-Kosten":"4 AsP","Publikation":null,"Steigerungsfaktor":"A","Verbreitung":"Elfen","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Movimento","spellextensions":{"0":["Mehr Ziele ","(FW 8, 1 AP): Durch eine Berührung können bis zu QS weitere Ziele von der Wirkung des Zaubers profitieren. Jedes weitere Ziel kostet zusätzlich 2 AsP."],"1":["Geänderte Zielkategorie 1 ","(FW 12, 2 AP): Als Ziel des Zaubers dürfen auch Lebewesen der Größenkategorie winzig, klein und mittel gewählt werden."],"2":["Geänderte Zielkategorie 2 ","(FW 16, 3 AP): Als Ziel des Zaubers dürfen auch Lebewesen"]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_MagischerRaub.html","properties":{"Wirkungsdauer":"QS x 30 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/KL/KO (modifiziert um SK)","Wirkung":"Während der Wirkungsdauer kann der Zauberer auf die AsP des Ziels zugreifen. Das Opfer kann sich nach Ende der Zauberdauer beliebig weit vom Zauberer entfernt haben. Die Verbindung endet nur dann automatisch, wenn das Opfer sich nicht mehr in der gleichen Sphäre wie der Zauberer aufhält. Wirkt der Zauberer Magie, während die Verbindung besteht, werden die benötigten AsP auf seinen Wunsch hin aus dem Vorrat des Opfers verbraucht. Sind alle AsP des Opfers aufgebraucht, muss der Zauberer alle weiteren Kosten selbst aufbringen. Der Verbrauch von pAsP kann jedoch nicht auf das Ziel übertragen werden. Maximal kann durch den Zauber 1 Ziel beraubt werden.","AsP-Kosten":"4 AsP (Kosten nicht modifizierbar)","Publikation":null,"Steigerungsfaktor":"C","Verbreitung":"Druiden, Gildenmagier","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Magischer Raub","spellextensions":{"0":["Sofortraub ","(FW 8, 3 AP): Der Zauberer entzieht dem Opfer zum Ende der Ritualdauer sofort AsP und fügt sie seinem eigenen Vorrat hinzu. Er kann bis zu QS x 4 AsP rauben, dabei jedoch seinen eigenen Maximalwert nicht überschreiten."],"1":["Längere Wirkungsdauer ","(FW 12, 6 AP): Die Wirkungsdauer beträgt QS Stunden."],"2":["Zwei Opfer ","(FW 16, 9 AP): Der Zauberer kann bis zu 2 Opfer gleichzeitig mit dem Zauber belegen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Leidensbund.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/IN/KO","Wirkung":"Der Zauberer kann den erlittenen LeP-Verlust von einer anderen Person auf sich selbst übertragen. Dadurch werden die Wunden des Ziels geheilt und entstehen dafür beim Zaubernden. Maximal können auf diesem Wege QS x 3 verlorene LeP übertragen werden, wobei sich der Zauberer auch für weniger entscheiden kann. Auch ist keine Übertragung möglich, die beim Zauberer zu einem LeP-Stand führen würde, der bei 0 oder darunter liegt. Die Wirkung des Zaubers setzt erst nach Ende der Ritualdauer ein, es ist daher mit diesem Ritual nicht möglich, Sterbende von der Schwelle des Todes zu retten.","AsP-Kosten":"4 AsP","Publikation":null,"Steigerungsfaktor":"A","Verbreitung":"Gildenmagier","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Leidensbund","spellextensions":{"0":["Krankheitsübertragung ","(FW 8, 1 AP): Anstelle der verlorenen LeP kann eine Krankheit auf den Zauberer übertragen werden. Die Krankheitsstufe darf maximal QS Punkte betragen."],"1":["Giftübertragung ","(FW 12, 2 AP): Anstelle der verlorenen LeP kann ein Gift auf den Zauberer übertragen werden. Die Giftstufe darf maximal QS Punkte betragen."],"2":["Freiwilliger ","(FW 16, 3 AP): Die verlorenen LeP können statt auf den Zauberer auf einen Freiwilligen übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_MemorabiaFalsifir.html","properties":{"Wirkungsdauer":"1 Jahr","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/IN/CH (modifiziert um SK)","Wirkung":"Der Zauberer entfernt Erinnerungen aus dem Gedächtnis des Ziels. Dabei kann er jedoch keine bestimmte Erinnerung festlegen, sondern lediglich einen Zeitraum von maximal QS x 3 Tagen ab einem von ihm gewünschten Anfangszeitpunkt.","AsP-Kosten":"8 AsP","Publikation":null,"Steigerungsfaktor":"C","Verbreitung":"Druiden","Ritualdauer":"2 Stunden"},"spellclass":"Ritual","name":"Memorabia Falsifir","spellextensions":{"0":["Ohne Erinnerung ","(FW 8, 3 AP): Das Opfer kann sich an den Moment der Verzauberung nicht erinnern."],"1":["Phasen ","(FW 12, 6 AP): Der gelöschte Zeitraum muss nicht zusammenhängend sein, sondern darf auf bis zu drei Phasen aufgeteilt werden. "],"2":["Andere Erinnerung ","(FW 16, 9 AP): Das Opfer erhält eine andere Erinnerung an den gelöschten Zeitraum, deren Qualität abhängig von den QS ist."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Hartesschmelze.html","properties":{"Wirkungsdauer":"QS x 2 Minuten","Merkmal":"Elementar","Zielkategorie":"Objekt (profane Objekte bis zur Größe einer Truhe)","Reichweite":"4 Schritt","Probe":"MU/IN/KO","Wirkung":"Das Ritual raubt einem Objekt seine Festigkeit und macht einen harten Gegenstand weich und formbar. Dies erlaubt sowohl künstlerische Gestaltungen, ermöglicht aber auch das Erschaffen von Löchern oder das Abtrennen einzelner Teile. Der Zauberer kann z. B. Türen wie Knetmasse zusammenrollen oder ein Schloss aus der Tür entfernen. Dabei ist zu beachten, dass der Meister entscheidet, welche Gegenstände miteinander verbunden sind. Ein Schloss gehört zu einer Tür, eine Tür aber nicht zu einer Wand. Der Zauber ist kein Ersatz für Handwerkstalente, man kann damit beispielsweise nicht Metall verformen, um eine scharfe Schwertklinge zu erhalten. Am Ende der Wirkungsdauer wird der Gegenstand augenblicklich wieder hart.","AsP-Kosten":"2/4/8/16 AsP für einen Gegenstand von der Größe einer Tasse/Truhe/Tür/Burgtor (Kosten nicht modifizierbar)","Publikation":null,"Steigerungsfaktor":"B","Verbreitung":"Gildenmagier","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Hartes schmelze","spellextensions":{"0":["Größere Objekte 1 ","(FW 8, 2 AP): Objekte bis zur Größe einer Tür können verformt werden."],"1":["Längere Wirkungsdauer ","(FW 12, 4 AP): Die Wirkungsdauer beträgt QS x 5 Minuten."],"2":["Größere Objekte 2 ","(FW 16, 6 AP): Objekte bis zur Größe eines Burgtors können verformt werden. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Invomaior.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Dämonen","Reichweite":"8 Schritt","Probe":"MU/CH/KO","Wirkung":"Ein zuvor ausgewählter gehörnter Dämon mit maximal fünf Hörnern erscheint. Die Ritualprobe ist um die Beschwörungsschwierigkeit des Dämons erschwert. Details zur Beschwörung und dem Umgang mit Dämonen finden sich im Abschnitt Die Kunst der<br/>Dämonenbeschwörung im Regelwerk ab Seite 262.","AsP-Kosten":"32 AsP","Publikation":"Regelwerk Seite 300","Steigerungsfaktor":"C","Verbreitung":"Gildenmagier, Hexen","Ritualdauer":"8 Stunden"},"spellclass":"Ritual","name":"Invovatio maior","spellextensions":{"0":["Mehr Dienste ","(FW 8, 3 AP): Der Zauberer kann QS +2 Dienste von dem Wesen verlangen."],"1":["Längerer Dienst ","(FW 12, 6 AP): Das Wesen verliert erst am Ende einer Woche (statt am Ende eines Tages) automatisch einen Dienst."],"2":["Mächtiges Wesen ","(FW 16, 9 AP): Dem Wesen können zweimal die gleichen Verbesserungen beim Beschwören gegeben werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Invominor.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Dämonen","Reichweite":"8 Schritt","Probe":"MU/CH/KO","Wirkung":"Ein zuvor ausgewählter niederer Dämon erscheint. Die Ritualprobe ist um die Anrufungsschwierigkeit des Dämons erschwert. Details zur Beschwörung und dem Umgang mit Dämonen finden sich im Abschnitt Die Kunst der Dämonenbeschwörung im Regelwerk ab Seite 262.","AsP-Kosten":"16 AsP","Publikation":"Regelwerk Seite 300","Steigerungsfaktor":"B","Verbreitung":"Gildenmagier, Hexen","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Invocatio Minor","spellextensions":{"0":["Mehr Dienste ","(FW 8, 2 AP): Der Zauberer kann QS +2 Dienste von dem Wesen verlangen."],"1":["Längerer Dienst ","(FW 12, 4 AP): Das Wesen verliert erst am Ende einer Woche (statt am Ende eines Tages) automatisch einen Dienst."],"2":["Mächtiges Wesen ","(FW 16, 6 AP): Dem Wesen können zweimal die gleichen Verbesserungen beim Beschwören gegeben werden. "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Dschinnenruf.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Elementare","Reichweite":"8 Schritt","Probe":"MU/CH/KO","Wirkung":"Ein Dschinn des gewünschten Elements erscheint. Details zur Herbeirufung und dem Umgang mit Dschinnen finden sich im Abschnitt Die Kunst der Elementarherbeirufung im Regelwerk ab Seite 262.","AsP-Kosten":"32 AsP","Publikation":"Regelwerk Seite 300","Steigerungsfaktor":"C","Verbreitung":"Druiden, Gildenmagier","Ritualdauer":"8 Stunden"},"spellclass":"Ritual","name":"Dschinnenruf","spellextensions":{"0":["Mehr Dienste ","(FW 8, 3 AP): Der Zauberer kann QS +2 Dienste von dem Wesen verlangen."],"1":["Längerer Dienst ","(FW 12, 6 AP): Das Wesen verliert erst am Ende einer Woche (statt am Ende eines Tages) automatisch einen Dienst."],"2":["Mächtiges Wesen ","(FW 16, 9 AP): Dem Wesen können zweimal die gleichen Verbesserungen beim Beschwören gegeben werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Diener.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Elementare","Reichweite":"8 Schritt","Probe":"MU/CH/KO","Wirkung":"Ein Elementargeist des gewünschten Elements erscheint. Details zur Herbeirufung und dem Umgang mit Elementargeistern finden sich im Abschnitt Die Kunst der Elementarherbeirufung im Regelwerk ab Seite 262.","AsP-Kosten":"16 AsP","Publikation":"Regelwerk Seite 300","Steigerungsfaktor":"B","Verbreitung":"Druiden, Gildenmagier","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Elementarer Diener","spellextensions":{"0":["Mehr Dienste ","(FW 8, 2 AP): Der Zauberer kann QS +2 Dienste von dem Wesen verlangen."],"1":["Längerer Dienst ","(FW 12, 4 AP): Das Wesen verliert erst am Ende einer Woche (statt am Ende eines Tages) automatisch einen Dienst."],"2":["Mächtiges Wesen ","(FW 16, 6 AP): Dem Wesen können zweimal die gleichen Verbesserungen beim Beschwören gegeben werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Caldofrigo.html","properties":{"Wirkungsdauer":"QS x 2 Minuten","Merkmal":"Elementar","Zielkategorie":"Objekt (profane Objekte bis zur Größe einer Truhe)","Reichweite":"4 Schritt","Probe":"IN/CH/KO","Wirkung":"Mittels Caldofrigo kann die Temperatur eines maximal truhengroßen Objekts verändert werden. Es kann abgekühlt oder erwärmt werden und zwar in einem Spielraum von +/–100 Grad Celsius der Ausgangstemperatur. Der Zauberer muss hierzu die gewünschte Gradzahl festlegen und kann nicht beliebig während der Wirkungsdauer die Temperatur ändern. Die Temperaturveränderung funktioniert außerdem immer nur in eine Richtung. Sollte ein Held mit einem Gegenstand in Berührung kommen, der extrem heiß ist (oder kalt; dabei einfach analog dieselben Regeln verwenden), gelten die Regeln für Brennende Personen (siehe Regelwerk Seite 341). Nach dem Ende der Wirkungsdauer nimmt das Objekt wieder seine ursprüngliche Temperatur an. Durch große Temperaturänderungen entsteht bei den meisten Objekten ein Schaden von 1W6 Strukturpunkten. Bei welchen Temperaturen dies der Fall ist, entscheidet der Meister.","AsP-Kosten":"4 AsP","Publikation":"Die Streitenden Königreiche Seite 168","Steigerungsfaktor":"B","Verbreitung":"Druiden, Gildenmagier","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Caldofrigo","spellextensions":{"0":["Höhere/Niedrigere Temperatur 1 ","(FW 8, 2 AP): Die Temperatur kann um bis zu 200 Grad verändert werden. Der Schaden für Gegenstände steigt auf 2W6 Strukturpunkte."],"1":["Höhere/Niedrigere Temperatur 2 ","(FW 12, 4 AP): Die Temperatur kann um bis zu 500 Grad verändert werden. Der Schaden für Gegenstände steigt auf 3W6 Strukturpunkte. Voraussetzung: Zaubererweiterung "],"2":["Höhere/Niedrigere Temperatur 3 ","(FW 16, 6 AP): Die Temperatur kann um bis zu 1.000 Grad geändert werden. Der Schaden für Gegenstände steigt auf 4W6 Strukturpunkte. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_AufmerksamerWaechter.html","properties":{"Wirkungsdauer":"QS x 3 Stunden","Merkmal":"Objekt","Zielkategorie":"profane Objekte (Tierbild)","Reichweite":"Sichtweite des Tieres","Probe":"IN/CH/FF","Wirkung":"Der Tiergeist stößt einen Warnruf aus, wenn sich etwas nähert, das er als Gefahr betrachtet. Seine <em>Sinnesschärfe</em> entspricht QS x 3, seine Eigenschaften denen eines typischen Exemplars der Tierart.","AsP-Kosten":"8 AsP","Publikation":"Der Weiße See Seite 61","Steigerungsfaktor":"B","Verbreitung":"Goblinzauberinnen","Ritualdauer":"30 Minuten"},"spellclass":"Ritual","name":"Aufmerksamer Wächter","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Accuratum.html","properties":{"Wirkungsdauer":"QS x 3 Stunden","Merkmal":"Objekt","Zielkategorie":"Objekte","Reichweite":"Berührung","Probe":"KL/CH/FF","Wirkung":"Ein Satz Kleidung wird nach Wünschen des Zauberers abgeändert. Dies betrifft Schnitt und Farbe, nicht jedoch Menge und Art des Materials. Auf diesem Wege sind sowohl das simple Flicken zerrissener Hosen wie auch die Umwandlung eines Stoffballens oder einer schlichten Kutte in eine deutlich prächtigere Robe möglich. Die Kleidung kann nach Meisterentscheid Erschwernisse bei Gesellschaftstalenten ausgleichen, beispielsweise bei <em>Etikette (Benehmen) </em>oder <em>Betören (Aufhübschen)</em>.","AsP-Kosten":"4 AsP","Publikation":null,"Steigerungsfaktor":"A","Verbreitung":"Scharlatane","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Accuratum","spellextensions":{"0":["Gaukelei-Vorteil ","(FW 8, 1 AP): Der Zauberer bekommt durch zum Anlass passende Kleidung eine Erleichterung von 1 auf "],"1":["Etikette-Vorteil ","(FW 12, 2 AP): Der Zauberer bekommt durch zum Anlass passende Kleidung eine Erleichterung von 1 auf "],"2":["Gassenwissen-Vorteil ","(FW 16, 3 AP): Der Zauberer bekommt durch zum Anlass passende Kleidung eine Erleichterung von 1 auf "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_HagelschlagundSturmgebruell.html","properties":{"Wirkungsdauer":"QS Stunden","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/CH/KO","Wirkung":"Der Zauberer erschafft um sich herum eine Zone von QS x 50 Schritt Radius, innerhalb der er Bewölkung, Niederschlag und Windgeschwindigkeit um insgesamt QS Stufen senken oder anheben kann. Mit einer QS von 5 ist es beispielsweise möglich, die Windgeschwindigkeit um 2 Stufen zu verändern und den Niederschlag um 3. Bei Temperaturen unter dem Gefrierpunkt fällt der Niederschlag als Schnee oder Hagel zu Boden. Die Stufe des Niederschlags darf die Stufe der Bewölkung nicht überschreiten.","AsP-Kosten":"32 AsP","Publikation":null,"Steigerungsfaktor":"D","Bewölkung":"<ul><li><strong>Stufe 1 </strong>klar</li><li><strong>Stufe 2 </strong>wolkig</li><li><strong>Stufe 3 </strong>bedeckt</li><li><strong>Stufe 4 </strong>verhangen, Sichtstufe sinkt um 1</li></ul>","Niederschlag":"<strong>Stufe 1 </strong>kein Niederschlag<ul><li><strong>Stufe 2 </strong>Nieselregen</li><li><strong>Stufe 3 </strong>Dauerregen</li><li><strong>Stufe 4 </strong>Wolkenbruch</li></ul>","Windgeschwindigkeit":"<ul><li><strong>Stufe 1 </strong>Windstille</li><li><strong>Stufe 2 </strong>Brise</li><li><strong>Stufe 3 </strong>Sturm</li><li><strong>Stufe 4 </strong>Orkan, jede KR 1 Aktion aufwenden und Probe auf <em>Kraftakt </em>(<em>Drücken &amp;</em> <em>Verbiegen</em>) bestehen, um nicht den Status <em>Liegend </em>zu erhalten</li></ul>","Verbreitung":"Druiden","Ritualdauer":"5 Minuten"},"spellclass":"Ritual","name":"Hagelschlag und Sturmgebrüll","spellextensions":{"0":["Längere Wirkungsdauer ","(FW 8, 4 AP): Die Wirkungsdauer beträgt QS x 2 Stunden."],"1":["Größerer Radius ","(FW 12, 8 AP): Der Wirkungsradius beträgt QS x 100 Schritt."],"2":["Höhere Reichweite ","(FW 16, 12 AP): Die Reichweite beträgt 512 Schritt, ist aber nicht mehr modifizierbar."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Wolfstatze.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/IN/GE","Wirkung":"Der Zauberer verwandelt sich in ein Landtier seiner Wahl, das jedoch weder fliegen, noch im Wasser leben darf (also keine Vögel, Fische usw.) und maximal die Größenkategorie klein aufweist. Während der Wirkungsdauer verwendet der Zauberer die körperlichen Werte des Tieres, einschließlich seiner Eigenschaften, Talente und Kampfwerte. Eine niedrige LE wird bei der Verwandlung anteilsmäßig angerechnet, gleiches geschieht bei der Rückverwandlung. Die geistigen Werte des Zauberers bleiben ebenso wie sein Bewusstsein erhalten. Die Verwandlung betrifft nur den Zauberer selbst, nicht seine Kleidung oder Ausrüstung. Es ist keine Verwandlung in ein kulturschaffendes Wesen möglich.","Publikation":null,"AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro Stunde","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Wolfstatze","spellextensions":{"0":["Mittlere Tiere ","(FW 8, 3 AP): Der Zauberer kann sich auch in Landtiere der Größenkategorie mittel verwandeln. Es entstehen zusätzliche Kosten in Höhe von 5 AsP."],"1":["Keine Aufrechterhaltung ","(FW 12, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Große Tiere ","(FW 16, 9 AP): Der Zauberer kann sich auch in Landtiere der Größenkategorie groß verwandeln. Es entstehen zusätzliche Kosten in Höhe von 6 AsP. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Wasseratem.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/KO","Wirkung":"Der Verzauberte kann sowohl normale Luft als auch Wasser atmen. Er kann grundsätzlich 1 Minute pro QS unter Wasser atmen + je nach AsP-Einsatz.","Publikation":"Regelwerk Seite 300","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Wasseratem","spellextensions":{"0":["Andere Kosten ","(FW 8, 2 AP): Die AsP-Kosten, um den Zauber aufrechtzuerhalten, betragen 4 AsP pro 30 Minuten."],"1":["Keine Aufrechterhaltung ","(FW 12, 4 AP): Der Zaubergilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Mehrere Ziele ","(FW 16, 6 AP): Der Zauberer kann weitere QS Ziele berühren, die unter Wasser atmen können. Nach dem Wirken des Zauberspruchs muss er sie nicht mehr berühren. Sie können sich dann unabhängig von ihm wegbewegen. Die Grundkosten steigen auf 8 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Vogelzwitschern.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Illusion","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/IN/GE","Wirkung":"Innerhalb von QS x 2 Schritt um sich herum kann der Zauberer jedes beliebige Geräusch durch ein anderes ersetzen. Die Lautstärke bleibt dabei jedoch unverändert. Eine Stimme kann so etwa deutlich höher oder das Auftreten mit dem Fuß wie leiser Donner oder Fanfarenschall klingen. Details zum Thema Illusionen finden sich im <strong>Regelwerk</strong> auf Seite <strong>259</strong>.","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Vogelzwitschern","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Zunge_bet%C3%A4uben.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"KL/IN/CH (modifiziert um ZK)","Wirkung":"Zunge und Lippen des Opfers sind gelähmt. Seine Fähigkeit, verständlich zu sprechen ist daher stark beeinträchtigt. Der Verzauberte erhält den Status Stumm.","Publikation":"Die Streitenden Königreiche Seite 168","AsP-Kosten":"8 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Zunge betäuben","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Verwandlungsbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/CH/FF","Wirkung":"Ein Zauber des Merkmals Verwandlung wird aufgehoben, solange dessen QS nicht höher ist als die QS des Verwandlungsbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt. Auch gegen Zauber, deren Wirkung bereits vollendet ist oder permanent wirkende Sprüche hilft er nicht. Wird der Verwandlungsbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":null,"AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Verwandlungsbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/Rit_Arcanovi.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"geweihte Objekte, profane Objekte","Reichweite":"8 Schritt","Probe":"KL/IN/FF","Wirkung":"Details zur Verzauberung stehen im Abschnitt Die Artefakte im Detail im Regelwerk ab Seite 266.","AsP-Kosten":"16 AsP","Publikation":"Regelwerk Seite 300","Steigerungsfaktor":"D","Verbreitung":"allgemein","Ritualdauer":"8 Stunden"},"spellclass":"Ritual","name":"Arcanovi","spellextensions":{"0":["Geringere Ritualdauer ","(FW 8, 4 AP): Die Ritualdauer beträgt 4 Stunden."],"1":["Weniger Erschwernis 1 ","(FW 12, 8 AP): Die Erschwernisse bei zusätzlichen Aufladungen bei Zauberspeicher-Artfakten sinken beim Arcanovi um 1 (bis zu einem Minimum von 0)."],"2":["Weniger Erschwernis 2 ","(FW 16, 12 AP): Die Erschwernisse bei zusätzlichen Aufladungen bei Zauberspeicher-Artfakten sinken beim Arcanovi und den auf dem Artefakt gespeicherten Zauber um 1 (bis zu einem Minimum von 0). Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Visibili.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/KO","Wirkung":"Der Verzauberte wird innerhalb von 20 – (QS x 3) Kampfrunden unsichtbar.<br/>Während dieser Zeit wird er immer durchscheinender, bis er schließlich nicht mehr zu sehen ist. Die Unsichtbarkeit gilt nur für ihn selbst, nicht für Kleidung oder Ausrüstungsgegenstände. Substanzen, die länger als 20 Sekunden im Körper verbleiben, werden auch unsichtbar. Substanzen, die den Körper verlassen, werden entsprechend nach 20 Sekunden wieder sichtbar. Während der Wirkungsdauer erhält der Verzauberte den Status Unsichtbar.","Publikation":"Regelwerk Seite 299","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Visibili","spellextensions":{"0":["Andere Kosten ","(FW 8, 2 AP): Die AsP-Kosten, um den Zauber aufrechtzuerhalten, betragen 8 AsP für 30 Minuten."],"1":["Keine Aufrechterhaltung ","(FW 12, 4 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Unsichtbare Kleidung ","(FW 16, 6 AP): Auch die Kleidung des Zauberers wird unsichtbar. Dies gilt nicht für Waffen, Rüstungen (alles, was einen RS von 1 oder mehr aufweist) und größere Gegenstände."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Transversalis.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Objekt, Wesen","Reichweite":"selbst","Probe":"MU/CH/KO","Wirkung":"Mittels dieses Zaubers kann ein Held sich und maximal QS x 5 Stein Gegenstände (Kleidung, Waffen usw.) an einen anderen Ort teleportieren. Je näher Gegenstände am Körper liegen (meist die Kleidung), desto eher reisen sie mit dem Zaubernden mit. Sie reisen entweder am Stück mit oder bleiben zurück, werden jedoch nicht zerteilt. Die exakte Auswahl trifft der Meister. Der Held muss schon einmal an dem Zielort des Zaubers gewesen sein oder ihn sehen können. Der Ort darf maximal QS x 3 Meilen vom Zaubernden entfernt sein. Wenn er ihn nur sieht, ist die Probe um 2 erschwert.","Publikation":"Regelwerk Seite 299","AsP-Kosten":"8 AsP + 1 AsP pro Meile (nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Transversalis","spellextensions":{"0":["Mehr Gewicht ","(FW 8, 3 AP): Der Zauberer kann QS x 10 Stein an Gegenständen mitnehmen."],"1":["Größere Teleportationsmöglichkeit ","(FW 12, 6 AP): Die Teleportation reicht bis zu QS x 10 Meilen weit und kostet 1 AsP pro 3 Meilen."],"2":["Transport ","(FW 16, 9 AP): Der Zauberer kann einen zweiten Kulturschaffenden seiner Größenkategorie oder einer kleineren mitteleportieren. Das zulässige Maximalgewicht für Ausrüstung gilt jedoch unverändert – und zwar für beide Teilnehmer zusammen. Die Grundkosten betragen 16 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Telekinesebann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/IN/FF","Wirkung":"Ein Zauber des Merkmals Telekinese wird aufgehoben, wenn dessen QS nicht höher ist als die QS des Bewegungsbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt. Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche hilft er nicht. Wird der Telekinesebann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":null,"AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Telekinesebann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Sumus_Elixiere.html","properties":{"Wirkungsdauer":"QS in Jahren","Merkmal":"Objekt","Zielkategorie":"Objekte (Zutaten für Alchimica)","Reichweite":"Berührung","Probe":"KL/IN/IN","Wirkung":"Durch diesen Zauberspruch kann der Druide Astralkraft in Zutaten für Alchimica einfließen lassen. Zum einen erhöht er die Haltbarkeit von Zutaten und/oder Alchimica um die Wirkungsdauer des Zauberspruchs, zum anderen hat erhält der Druide eine Erleichterung von 1 bei der Probe auf Alchimie, um das Elixier herzustellen.","Publikation":"Die Streitenden Königreiche Seite 167","AsP-Kosten":"8 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Sumus Elixiere","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Tiergedanken.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","Zielkategorie":"Tiere","Reichweite":"4 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Zaubernde kann in den Gedanken und Erinnerungen eines Tieres lesen. Dabei erhält er Zugriff auf Erinnerungen, die maximal QS Tage zurückliegen dürfen. Je nach Intelligenz, Erinnerungsvermögen und Lebenswandel des Tieres wird er z. B. bei einem Goldfisch nur wenig erfahren, bei einem Affen hingegen verhältnismäßig viel. Um die Gedankenwelt des Tieres zu verstehen, ist eine Probe auf <em>Tierkunde </em>erforderlich. Der Meister kann je nach erreichter QS der <em>Tierkunde</em>-Probe detaillierter berichten, was der Zaubernde gesehen und richtig interpretiert hat.","Publikation":null,"AsP-Kosten":"4 AsP (Aktivierung des Zaubers), + 2 AsP pro 30 Sekunden","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Elfen, Hexen"},"spellclass":"Zauberspruch","name":"Tiergedanken","spellextensions":{"0":["Erleichterte Verteidigung ","(FW 8, 1 AP): Verteidigungen gegen das Ziel des Zaubers sind um QS/2 erleichtert."],"1":["Höhere Reichweite ","(FW 12, 2 AP): Die Reichweite des Zaubers beträgt 16 Schritt."],"2":["Mehr Ziele ","(FW 16, 3 AP): Der Zauber kann QS Ziele der gleichen Art verzaubern, also z. B. mehrere Wölfe eines Rudels innerhalb der Reichweite."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Vipernblick.html","properties":{"Wirkungsdauer":"aufrechterhaltend, maximal jedoch 50 KR","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"MU/MU/CH (modifiziert um SK)","Wirkung":"Mit diesem Zauberspruch kann die Hexe ein Opfer, solange sie ihren Blick nicht von ihm abwendet, hypnostieren und somit lähmen. Das Opfer erleidet je nach QS die folgenden Einbußen. Die Wirkung ist nicht kumulativ, es gelten die Auswirkungen der höchsten QS.<br/>QS 1: Status Fixiert<br/>QS 2: 1 Stufe Paralyse, Status Fixiert<br/>QS 3: 2 Stufen Paralyse, Status Fixiert<br/>QS 4: 3 Stufen Paralyse, Status Fixiert<br/>QS 5: 4 Stufen Paralyse, Status Fixiert<br/>QS 6: 4 Stufen Paralyse, Status Handlungsunfähig<br/>Die Hexe muss dem Opfer in die Augen starren, um es zu fixieren. Alle ihre Proben sind um 2 Punkte erschwert, solang sie starrt. Schließt sie ihre Augen, wendet den Blick ab oder entfernt sie sich weiter vom Opfer, als die Reichweite des Zaubers beträgt, endet die Wirkung augenblicklich. Erleidet das Opfer TP oder SP, oder wird es von der Fixierungsstelle bewegt, endet der Zauberspruch ebenfalls. Wird der Sichtkontakt kurzzeitig unterbrochen (z. B., weil jemand in die Blicklinie läuft, es zu dunkel wird oder die Augen der Hexe mit einer Hand verdeckt werden), so muss die Hexe eine Probe auf Selbstbeherrschung (Störungen ignorieren) ablegen, um den Spruch aufrechtzuerhalten. Wird der Sichtkontakt länger als 2 KR unterbrochen, endet die Wirkung. Das Opfer kann den Blickkontakt von sich aus nicht abbrechen. Während der Wirkung des Spruches kann das Opfer keine Attacken, Fernkampfangriffe oder Verteidigungen ausführen.","Publikation":"Die Streitenden Königreiche Seite 167","AsP-Kosten":"8 AsP + 4 pro 5 Kampfrunden","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Vipernblick","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Spurlos.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Telekinese","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/FF/KK","Wirkung":"Der Zaubernde hinterlässt keine Spuren mehr. Er knickt keine Grashalme um und erzeugt keine Fußabdrücke auf weichem Boden. Selbst verräterische Gerüche hinterlässt er nicht. Um Spuren des Zaubernden zu finden oder zu verfolgen, sind Proben auf Fährtensuchen um QS erschwert.","Publikation":"Regelwerk Seite 299","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Spurlos","spellextensions":{"0":["Reichweite Berührung ","(FW 8, 2 AP): Die Reichweite ist Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"1":["Andere Kosten ","(FW 12, 4 AP): Die AsP-Kosten, um den Zauber aufrechtzuerhalten, betragen 2 AsP pro 30 Minuten."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Taubheit.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"KL/IN/CH (modifiziert um SK)","Wirkung":"Das Ziel kann nichts mehr hören und erhält dadurch den Status <em>Taub</em>.","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Taubheit","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."],"1":["Hörschwierigkeiten ","(FW 12, 4 AP): Das Ziel hat auch noch QS Stunden nach Ende der Wirkungsdauer Hörschwierigkeiten. Alle Proben, die das Gehör erfordern, sind um 1 erschwert."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 2 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Stillstand.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"16 Schritt","Probe":"MU/IN/KO","Wirkung":"Der Zauberer muss einen Mittelpunkt innerhalb der Reichweite des Zauberspruchs benennen, um den herum sich die Zone kugelförmig mit einem Radius von QS x 2 Schritt ausbreitet. Innerhalb dieser verdichtet sich die Luft zu einer nur mühsam zu durchdringenden Masse, die Attacken, Verteidigungen sowie Proben auf körperliche Talente um 2 erschwert und die GS um den gleichen Wert senkt. Durchquert die Flugbahn eines Projektils die Zone zumindest teilweise, werde die angerichteten TP halbiert. Stürze in der Zone oder in die Zone hinein sind wie Stürze ins Wasser zu werten.","Publikation":"Klingen der Nacht Seite 110","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Stillstand","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Somnigravis.html","properties":{"Wirkungsdauer":"QS x 3 in Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Zauberspruch betäubt den Bezauberten. Erreicht er so Betäubung Stufe IV, schläft er ein und ist vor Ablauf der Wirkungsdauer nur durch großen Lärm, kräftiges Anstoßen oder Ähnliches zu wecken. Ungestört schläft er, bis er auf natürlichem Wege erwacht.<br/>QS 1: 1 Stufe Betäubung, aber nur für 1 Minute<br/>QS 2: 1 Stufe Betäubung<br/>QS 3: 2 Stufen Betäubung<br/>QS 4: 3 Stufen Betäubung<br/>QS 5: 4 Stufen Betäubung<br/>QS 6: 4 Stufen Betäubung, aber für die doppelte Wirkungsdauer","Publikation":"Regelwerk Seite 298","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Druiden, Elfen"},"spellclass":"Zauberspruch","name":"Somnigravis","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 2 AP): Die Zustandsstufen können auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Kann der Zauberer z. B. 3 Stufen "],"1":["Längere Wirkung ","(FW 12, 4 AP): Die Wirkungsdauer steigt auf QS x 4 Minuten."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Skelettarius.html","properties":{"Wirkungsdauer":"QS x 2 Kampfrunden","Merkmal":"Dämonisch","Zielkategorie":"Kulturschaffende (Verstorbene)","Reichweite":"8 Schritt","Probe":"MU/KL/CH","Wirkung":"Der Zauberer erhebt die körperlichen Überreste eines verstorbenen Wesens kurzfristig zum Untoten. Er kann mit dem Zauberspruch einfache Mumien, Skelette und Zombies erwecken, jeweils maximal bis zur Größenkategorie mittel. Die Erschaffung funktioniert nach den gleichen Regeln wie die Beschwörung. Das untote Wesen verrichtet 1 + QS Dienste und kann wie Dämonen und Elementare verbessert werden (siehe <strong>Regelwerk </strong>Seite <strong>263</strong>). Es ist zu den gleichen Diensten wie Dämonen und Untote in der Lage.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Skelettarius","spellextensions":{"0":["Mehr Dienste ","(FW 8, 2 AP): Der Zauberer kann QS +2Dienste von dem Wesen verlangen."],"1":["Längerer Dienst ","(FW 12, 4 AP): Das Wesen verliert erst am Ende einer Woche (statt am Ende eines Tages) automatisch einen Dienst."],"2":["Mächtiges Wesen ","(FW 16, 6 AP): Dem Wesen können zweimal die gleichen Verbesserungen beim Beschwören gegeben werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Solidirid.html","properties":{"Merkmal":"Keines","Publikation":"Aventurische Magie 146","Verbreitung":"Allgemein"},"spellclass":"Zauberspruch","name":"Zaubererweiterungen","spellextensions":{"0":["Mehr Strukturpunkte ","(FW 8, 3 AP): Die Strukturpunkte betragen 100 statt 50."],"1":["Höheres Gewicht ","(FW 12, 6 AP): Die Brücke trägt ein Gewicht von bis zu 1.000 Stein."],"2":["Längere Brücke ","(FW 16, 9 AP): Die Brücke verfügt über eine Länge von QS x 10 Schritt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Spinnenlauf.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"KL/IN/KO","Wirkung":"Hände und Füße des Verzauberten erhalten die Eigenschaft, sich auch an den glattesten Oberflächen festsaugen zu können. So kann er glatte Felswände und Mauern überwinden und an Decken Halt finden. Das Gewicht des Verzauberten ändert sich nicht. Daher kann es bei verputzten Wänden passieren, dass sich der Putz an Händen und Füßen unter der Belastung von der Mauer löst. Der Verzauberte muss sich mindestens mit drei Gliedmaßen festhalten. Für jeweils 2 QS kann es eine Gliedmaße weniger sein bis zu einem Minimum von einer Gliedmaße.","Publikation":"Regelwerk Seite 298","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Spinnenlauf","spellextensions":{"0":["Wirkt durch Kleidung ","(FW 8, 1 AP): Der Zauber entfaltet seine Wirkung auch durch getragene Handschuhe und Schuhwerk."],"1":["Reichweite Berührung ","(FW 12, 2 AP): Die Reichweite ist Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"2":["Keine Aufrechterhaltung ","(FW 16, 3 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Sensibar.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","Zielkategorie":"Kulturschaffende","Reichweite":"4 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Zaubernde kann die Gefühle des Verzauberten ergründen. Der Zauber offenbart nur die Gefühle selbst, nicht jedoch deren Grund oder Ursprung. Für den Zaubernden sind Proben auf Menschenkenntnis (Lügen durchschauen oder Motivation erkennen) gegen das Ziel des Sensibar um die erreichte QS erleichtert. Der Verzauberte kann bemerken, dass etwas Fremdes in seinen Geist blickt. Hierzu muss ihm eine Erfolgsprobe auf Sinnesschärfe erschwert um die QS des Zaubers gelingen. Er kann aber auch dann nicht zweifelsfrei sagen, wer dafür verantwortlich ist. Mit einer Probe auf Willenskraft, ebenfalls erschwert um die QS des Zaubers, kann er seine Gefühle gezielt unterdrücken. Ist der Verzauberte emotional stark aufgewühlt, ist dies jedoch nach Meisterentscheid um bis zu 3 Punkte zusätzlich erschwert.","Publikation":"Die Streitenden Königreiche Seite 167","AsP-Kosten":"8 AsP (Aktivierung des Zaubers), 4 AsP pro Minute","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Sensibar","spellextensions":{"0":["Kampfhandlung vorhersehen ","(FW 8, 2 AP): Im Kampf kann der Zauberer Handlungen des verzauberten Gegners vorausahnen. Die Verteidigung des Zauberers ist gegen diesen Gegner um 1 erhöht. Dabei gelten 5 Sekunden der Wirkungsdauer als 1 KR."],"1":["Unbemerkt ","(FW 12, 4 AP): Die "],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Silentium.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Telekinese","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"KL/FF/KK","Wirkung":"Der Zaubernde erschafft eine kugelförmige Zone, in der keine Geräusche mehr übertragen werden. Der Radius der Zone beträgt QS x 3 in Schritt. Die Zone entsteht mit dem Zaubernden als Mittelpunkt. Er kann sich vor der Fertigkeitsprobe entscheiden, ob sie sich mit ihm mit bewegt oder an Ort und Stelle verbleiben soll. In letzterem Falle darf er sich maximal QS x 3 Schritt von der Zone entfernen, ansonsten endet der Zauber.","Publikation":"Regelwerk Seite 298","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Silentium","spellextensions":{"0":["Andere Kosten ","(FW 8, 2 AP): Die AsP-Kosten, um den Zauber aufrechtzuerhalten, betragen 4 AsP pro 30 Minuten."],"1":["Reichweite Berührung ","(FW 12, 4 AP): Der Zauber hat die Reichweite Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"2":["Keine Aufrechterhaltung ","(FW, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_SchwarzundRot.html","properties":{"Wirkungsdauer":"8 Stunden","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/CH/KO (modifiziert um ZK)","Wirkung":"Das Ziel verliert am Ende jeder Stunde LeP in Höhe der QS.","Publikation":null,"AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Schwarz und Rot","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 3 AP): Die Zielkategorie des Zaubers ist Lebewesen."],"1":["Schwächung ","(FW 12, 6 AP): Am Ende jeder Stunde sinken zusätzlich zum Schaden alle körperlichen Eigenschaften um 1 (bis zu einem Minimum von 6). Sie regenerieren sich augenblicklich am Ende der Wirkungsdauer."],"2":["Todesfluch ","(FW 16, 9 AP): Der Zauberer kann zusätzlich beliebig viele seiner LeP opfern, um am Ende der ersten Stunde in dieser Höhe SP beim Ziel zu verursachen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_SchwarzerSchrecken.html","properties":{"Wirkungsdauer":"QS Stunden","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Das Ziel erhält 1 Stufe des Zustands <em>Furcht</em>, der sich gegen alles richtet, was Schwarz ist.","Publikation":"Klingen der Nacht Seite 110","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Schwarzer Schrecken","spellextensions":{"0":["Andere Farbe ","(FW 8, 1 AP): Der Zauberer kann eine andere Farbe als Schwarz wählen."],"1":["Längere Wirkungsdauer ","(FW 12, 2 AP): Die Wirkungsdauer beträgt QS x 2 Stunden."],"2":["Große Angst ","(FW 16, 3 AP): Das Opfer erleidet 2 Stufen "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Schmerzenlindern.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/IN/CH","Wirkung":"Je nach QS können mit diesem Zauberspruch Stufen von <em>Schmerz </em>augenblicklich abgebaut werden. Die Wirkung ist nicht kumulativ, es gelten jeweils die Auswirkungen der höchsten QS.<ul><li><strong>QS 1: </strong>1 Stufe <em>Schmerz</em></li><li><strong>QS 2: </strong>1 Stufe <em>Schmerz</em>, +1 KO für 1 Minute</li><li><strong>QS 3: </strong>2 Stufen <em>Schmerz</em>, +1 KO für 1 Minute</li><li><strong>QS 4: </strong>3 Stufen <em>Schmerz</em>, +1 KO für 1 Minute</li><li><strong>QS 5: </strong>4 Stufen <em>Schmerz</em>, +1 KO für 1 Minute</li><li><strong>QS 6: </strong>4 Stufen <em>Schmerz</em>, +2 KO für 1 Minute</li></ul>","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Elfen, Gildenmagier"},"spellclass":"Zauberspruch","name":"Schmerzen lindern","spellextensions":{"0":["Größere Reichweite ","(FW 8, 2 AP): Der Zauber hat die Reichweite 2 Schritt."],"1":["Verteilte Wirkung ","(FW 12, 4 AP): Die Senkung der Zustandsstufe "],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann auf bis zu 2 Ziele gleichzeitig gesprochen werden. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Schlangenruf.html","properties":{"Wirkungsdauer":"bis zum Tod der Tiere, maximal jedoch 20 KR","Merkmal":"Dämonisch","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"MU/CH/CH","Wirkung":"Die Hexe ruft mit diesem Zauberspruch bis zu QS x 2 Schlangen herbei, die für sie kämpfen. Es erscheinen maximal so viele Schlangen, wie die Anzahl der Gegner der Hexe beträgt. Die Hexe muss einen Punkt dem die Tiere dann in einem Radius von QS/2 Schritt erscheinen. Die Hexe kann gezielt Gegner benennen, welche die Schlangen dann angreifen. Sie kann jedoch nicht mehrere Tiere auf einen Gegner hetzen. Stirbt ein Gegner, verschwindet die gerufene Schlange wieder im Nichts. Nach ihrem Erscheinen können die Schlangen sich beliebig weit vom Ort ihrer Herbeirufung wegbewegen. Die Schlangen sind magische Wesen, die aus Magie erschaffen wurden. Endet die Wirkungsdauer, verschwinden die Schlangen wieder im Nichts. Ihre Werte entsprechen der Stabschlange (siehe Regelwerk Seite 329), allerdings sind sie ungiftig.","Publikation":"Die Streitenden Königreiche Seite 166","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Schlangenruf","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Sanftmut.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Einfluss","Zielkategorie":"Tiere","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Das verzauberte Tier wird friedfertig und lässt sich ohne Schwierigkeiten von dem Zauberer anfassen. Der Zauberspruch lässt sich nur auf Tiere anwenden.","Publikation":"Regelwerk Seite 297","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Hexen"},"spellclass":"Zauberspruch","name":"Sanftmut","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 1 AP): Die Wirkungsdauer beträgt QS x 5 Minuten."],"1":["Erhöhte Reichweite ","(FW 12, 2 AP): Die Reichweite beträgt 16 Schritt."],"2":["Längere Wirkungsdauer 2 ","(FW 16, 3 AP): Die Wirkungsdauer beträgt QS x 10 Minuten. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Satuarias_Herrlichkeit.html","properties":{"Wirkungsdauer":"QS x 3 in Stunden","Merkmal":"Verwandlung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/KO","Wirkung":"Der Zaubernde erzeugt subtile Veränderungen seines Körpers, die ihn begehrlicher wirken lassen. Haar und Teint werden verbessert, die Zähne aufgehellt und begradigt und die gesamte Statur dem gewünschten Schönheitsideal angepasst. Proben auf das Talent Betören (Anbändeln, Liebeskünste) und Überreden (Schmeicheln) sind um QS erleichtert. Je nach Situation können andere Gesellschaftstalente auf die gleiche Weise erleichtert werden. Auf Wesen mit anderem Schönheitsideal wirkt die Veränderung nicht.","Publikation":"Regelwerk Seite 297","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Satuarias Herrlichkeit","spellextensions":{"0":["Reichweite Berührung ","(FW 8, 2 AP): Die Reichweite ist Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"1":["Längere Wirkungsdauer ","(FW 12, 4 AP): Die Wirkungsdauer beträgt QS x 4 Stunden."],"2":["Wahre Schönheit ","(FW 16, 6 AP): Alle Lebewesen erleiden gegen den Verzauberten eine Erschwernis von 1 auf alle AT und FK sowie alle Zauber und Liturgien, die ihm Schaden verursachen würden, da sie nur ungern gegen ihn kämpfen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Sapefacta.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Telekinese","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"KL/CH/FF","Wirkung":"Innerhalb eines Radius von QS Schritt um den Zauberer werden Lebewesen und ihre am Körper getragene Kleidung von jeglichem Schmutz befreit. Dies kann durch Verschmutzung verursachte Erschwernisse bei Gesellschaftstalenten, beispielsweise bei <em>Etikette (Benehmen) </em>oder <em>Betören (Aufhübschen)</em>, nach Meisterentscheid reduzieren. Tätowierungen und ähnliche permanente Veränderungen sind nicht von der Zauberwirkung betroffen.","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Sapefacta","spellextensions":{"0":["Gerüche ","(FW 8, 1 AP) Auch Gerüche werden entfernt."],"1":["Zielkategorie Lebewesen ","(FW 12, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."],"2":["Größerer Radius ","(FW 16, 3 AP): Der Wirkungsradius beträgt QS x 2 Schritt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Respondami.html","properties":{"Wirkungsdauer":"QS x 15 in Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Verzauberte muss auf QS Fragen wahrheitsgemäß antworten. Die Fragen müssen mit Ja oder Nein zu beantworten sein. Auf andere Fragen kann der Verzauberte schweigen. Das Opfer ist sich während der Wirkung des Zaubers bewusst, was es sagt. Das Opfer muss die Sprache des Zauberers verstehen und sprechen können, ansonsten kann es ihm nicht antworten.","Publikation":"Regelwerk Seite 297","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Respondami","spellextensions":{"0":["Mehr Fragen ","(FW 8, 2 AP): Das Ziel muss QS + 2 Fragen wahrheitsgemäß beantworten."],"1":["Antworten mit Zahlen ","(FW 12, 4 AP): Das Ziel kann auch Fragen beantworten, deren Antwort Zahlen darstellen („Wie viele Gäste waren auf der Feier gestern anwesend?“ „42!“)"],"2":["Wann-Fragen beantworten ","(FW 16, 6 AP): Das Ziel kannauch Fragen zu Zeiten beantworten („Wann ist die Kaiserin nach Gareth aufgebrochen?“ „Vor drei Tagen.“)."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Salander.html","properties":{"Wirkungsdauer":"QS x 3 in Stunden","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/KO (modifiziert um ZK)","Wirkung":"Mit diesem Zauberspruch wird ein Lebewesen in ein anderes, kleineres und leichteres Lebewesen verwandelt. Bei sehr massiven Änderungen (Mammut zu Maus) ist die Probe um bis zu 3 erschwert. Es kann nur in Lebewesen verwandelt werden, die dem Zaubernden bekannt sind. Das Ziel behält einen kleinen Teil seiner geistigen Fähigkeiten und Erinnerungen, übernimmt jedoch die körperlichen Fähigkeiten der neuen Gestalt. Zudem behält es seine Lebensenergie und seinen natürlichen Rüstungsschutz, weshalb ein in einen Löwenzahn verwandelter Krieger kaum auszureißen ist. Das Ziel kann nicht in magische oder übernatürliche Lebewesen verzaubert werden. Ausrüstung wird nicht mitverwandelt. Verwandelte können sich nach dem Ende der Wirkungsdauer bruchstückhaft an die Zeit als Tier oder Pflanze erinnern. Details zum Thema Verwandlung finden sich im Abschnitt Verwandlungsregeln im Regelwerk auf Seite 258.","Publikation":"Regelwerk Seite 297","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Salander","spellextensions":{"0":["Gleichwertig ","(FW 8, 3 AP): Der Zauber kann genutzt werden, um das Ziel in ein Wesen der gleichen Größe zu verwandeln (z. B. einen Menschen in ein Reh oder einen Raben in eine Taube)."],"1":["Geistige Fähigkeiten ","(FW 12, 6 AP): Das Ziel behält während der Verwandlung seine vollständigen geistigen Fähigkeiten und kann sich nach der Rückverwandlung an alles erinnern."],"2":["Längere Wirkungsdauer ","(FW 16, 9 AP): Die Wirkungsdauer beträgt QS Tage."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Regeneratio.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"IN/CH/KO","Wirkung":"Der Zauberer regeneriert jede Kampfrunde LeP in Höhe der QS/2. Maximal kann der Zauberspruch FW Kampfrunden aufrechterhalten werden.","Publikation":null,"AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro KR (Kosten nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"4 Aktionen (Zauberdauer ist nicht modifizierbar)","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Regeneratio","spellextensions":{"0":["Zauberdauer modifizierbar ","(FW 8, 4 AP): Die Zauberdauer ist modifizierbar."],"1":["Reichweite Berührung ","(FW 12, 8 AP): Die Reichweite umfasst Berührung, sodass der Zauber auch auf andere gelegt werden kann."],"2":["Reichweite 8 Schritt ","(FW 16, 12 AP): Die Reichweite beträgt 8 Schritt und der Zauber kann auch auf andere gelegt werden. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Reflectimago.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Illusion","Zielkategorie":"Objekte","Reichweite":"4 Schritt","Probe":"KL/CH/FF","Wirkung":"Der Zauberer überzieht die glatte Fläche eines Objekts mit einer maximal 2 Rechtschritt messenden spiegelnden Oberfläche. Dabei handelt es sich jedoch nicht um einen echten Spiegel, sondern eine Illusion, man kann das Objekt also nicht dazu verwenden, um z. B. Sonnenstrahlen zu bündeln oder jemanden zu blenden. Details zum Thema Illusionen finden sich im <strong>Regelwerk</strong> auf Seite <strong>259.</strong>","Publikation":null,"AsP-Kosten":"2 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Reflectimago","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 1 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 2 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 3 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Radau.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Telekinese","Zielkategorie":"Objekt","Reichweite":"16 Schritt","Probe":"KL/FF/KK","Wirkung":"Der Besen oder ein anderer Stab oder Stock nach Wahl des Zaubernden greift von sich aus einen von der Hexe bestimmten Gegner innerhalb von 8 Schritt an (nach dem Wirken des Zaubers können Ziel und Besen sich weiter entfernen). Er gilt während des Zaubers als unzerbrechlich und magisch und schlägt eine Attacke pro Runde zu. Er kann dabei keine Kampfmanöver ausführen. Seine Werte sind INI 12+1W6, AT 10+(QS), TP 1W6+3, GS 12. Sollte der Besen vor Ablauf der Wirkungsdauer den Gegner getötet haben oder keinen Gegner mehr vor sich haben, dann greift er ein weiteres zufälliges Opfer innerhalb von 16 Schritt an (eventuell auch die Hexe selbst). Ist kein Opfer in dieser Reichweite, endet der Zauber. Um den Besen festzuhalten, ist ein erfolgreicher Angriff mit Raufen und der Sonderfertigkeit Haltegriff nötig. Für diese Zwecke verfügt der Stab über eine PA in Höhe der Hälfte der AT und einen Wert in Kraftakt von 10 Punkten mit Eigenschaften von 14 für die Probe.","Publikation":"Regelwerk Seite 296","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro Kampfrunde","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Radau","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Plumbumbarum.html","properties":{"Wirkungsdauer":"5 Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"MU/CH/GE (modifiziert um SK)","Wirkung":"Das Ziel erleidet einen Abzug in Höhe der QS des Zaubers auf seine AT.","Publikation":null,"AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen (Zauberdauer ist nicht modifizierbar)","Verbreitung":"Druiden, Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Plumbumbarum","spellextensions":{"0":["Senkung der Parade ","(FW 8, 2 AP): Zusätzlich zur AT sinkt auch die PA des Ziels um QS/2."],"1":["Zielkategorie Lebewesen ","(FW 12, 4 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Psychostabilis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Mittels dieses Zauberspruchs wird kurzfristig die Resistenz gegen geistige Beeinflussung gestärkt. Die Seelenkraft verbessert sich um QS/2.","Publikation":"Regelwerk Seite 296","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Psychostabilis","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 2 AP): Die Steigerung der Seelenkraft kann auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Kann der Zauberer z. B. 3 Punkte Seelenkraft erhöhen, darf er drei Zielen 1 Punkt geben, oder einem Ziel 2 Punkte und einem anderen 1 Punkt."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Seelenkraft steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Dafür dauert der Zauber nur 2 Aktionen. Das Wirken dieser Erweiterung ist um 2 Punkte erschwert."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Projectimago.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Illusion","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/IN/CH","Wirkung":"Der Zauberer projiziert ein optisches Ebenbild seiner selbst an einen gewünschten Ort (inklusive Kleidung und Gegenständen, die er am Körper trägt). Für die dortigen Beobachter sieht es so aus, als stünde eine reale Person vor ihnen, die alle Handlungen vollzieht, die der Zauberer durch Bewegung vorgibt. Dreht er sich also im Kreis und springt in die Luft, wird die Projektion das Gleiche tun. Der Ort der Erscheinung darf maximal 5 Meilen + 1 Meile pro vom Zauberer zusätzlich freiwillig investierten AsP entfernt sein, und dieser muss ihn schon einmal aufgesucht haben oder ihn sehen können. Eine Übertragung der Sinne findet nicht statt, der Zauberer kann also im Normalfall nur vermuten, wie die Reaktionen am Zielort ausfallen. Details zum Thema Illusionen finden sich im <strong>Regelwerk</strong> auf Seite <strong>259</strong>.","Publikation":null,"AsP-Kosten":"8 AsP + 1 AsP pro zusätzlicher Meile","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Projectimago","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Physiostabilis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Mittels dieses Zauberspruchs wird kurzfristig die Widerstandskraft gegen körperliche Beeinflussungen gestärkt. Die Zähigkeit des Verzauberten verbessert sich um QS/2.","Publikation":null,"AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Physiostabilis","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 2 AP): Die Steigerung der Zähigkeit kann auf mehrere Ziele verteilt werden. Diese müssen vor Wirken des Zaubers benannt werden. Kann der Zauberer z. B. 3 Punkte Zähigkeit erhöhen, kann er drei Zielen 1 Punkt geben, oder einem Ziel 2 Punkte und einem anderen 1 Punkt."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Zähigkeit steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Dafür dauert der Zauber nur 2 Aktionen. Das Wirken dieser Erweiterung ist um 2 erschwert."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Pestilenz_ersp%C3%BCren.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Zielkategorie":"Kulturschaffende","Reichweite":"1 Schritt","Probe":"MU/KL/IN","Wirkung":"Mit diesem Zauberspruch kann der Druide erspüren, welche Art von Krankheit das Opfer befallen hat. Je mehr QS er erzielt, desto mehr kann er über die Erkrankung herausfinden. Zudem erhält er eine Erleichterung von QS/2, wenn er zur Behandlung eines Patienten eine Probe auf Heilkunde Krankheiten ablegt.<br/>QS 1: Hat das Opfer den Status Krank?<br/>QS 2: Welche Stufe hat die Krankheit? Hat das Opfer schon jemand anderen mit der Krankheit angesteckt?<br/>QS 3: Wie heißt die Krankheit? Litt das Opfer schon einmal an der Krankheit?<br/>QS 4: Welche Behandlung gibt es?<br/>QS 5: Welche Gegenmittel gibt es?<br/>QS 6: Gibt es besondere Umstände (z. B. magische Ursache)?","Publikation":"Die Streitenden Königreiche Seite 166","AsP-Kosten":"8 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Pestilenz erspüren","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Penetrizzel.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Hellsicht","Zielkategorie":"alle","Reichweite":"selbst","Probe":"MU/KL/IN","Wirkung":"Der Zaubernde kann durch feste Materie blicken. Pro QS kann der Zaubernde 20 Halbfinger durchblicken. Magiestörendes Material kann die Probe erschweren (z. B. Eisen –1, Koschbasalt –4). Magische Objekte können nicht durchblickt werden. Herrscht auf der anderen Seite des Hindernisses Dunkelheit oder Nebel, wirkt sich dies ganz normal auf die Sicht des Zaubernden aus.","Publikation":"Regelwerk Seite 296","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro Minute","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Penetrizzel","spellextensions":{"0":["Sehen und Hören ","(FW 8, 2 AP): Der Zauberer kann nicht nur durch das Objekt sehen, sondern auch hindurch hören."],"1":["Helligkeit ","(FW 12, 4 AP): Sollte die Sicht hinter das Objekt mangels Beleuchtung eingeschränkt sein, so kann der Zauberer QS/2 Stufen durch natürliche Dunkelheit verursachte Sichtmodifikatoren ignorieren."],"2":["Durch Wände hindurchzaubern ","(FW 16, 6 AP): Der Zauberer kann auch hinter dem Objekt / der Wand befindliche Ziele verzaubern, solange er sie sieht. Die Probe für den Zauber, der durch das Hindernis wirken soll, ist dabei aber um 1 erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/paral.html","properties":{"Wirkungsdauer":"QS x 2 in Minuten","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"KL/IN/KO (modifiziert um ZK)","Wirkung":"Der  Körper  des  Verzauberten  versteift und verhärtet sich. Wird er vollständig paralysiert (Stufe IV), verwandelt sich sein Körper in eine unzerstörbare Substanz, ohne sein Gewicht zu verändern. Ein so versteinerter Körper ist durch gewöhnliche Waffen, Feuer oder Stürze nicht zu verletzen. Das  Opfer  kann  sich  nicht  bewegen,  fühlen  oder hören, aber innerhalb seines Sichtfeldes sehen. Die Wirkung  von  Giften  und  Krankheiten  kommt  zum Stillstand.<ul><li>QS 1:1 Stufe <em>Paralyse</em>, aber nur für 2 KR</li><li>QS 2: 1 Stufe <em>Paralyse</em></li><li>QS 3: 2 Stufen<em> Paralyse</em></li><li>QS 4: 3 Stufen <em>Paralyse</em></li><li>QS 5: 4 Stufen <em>Paralyse</em></li><li>QS 6: 4 Stufen<strong> Paralyse</strong>, aber für die doppelte Wirkungsdauer</li></ul>","Publikation":"Regelwerk Seite 296","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Paralysis","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 2 AP): Die Zustandsstufen können auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Kann der Zauberer z. B. 3 Stufen "],"1":["Längere Wirkung ","(FW 12, 4 AP): Die Wirkungsdauer steigt auf QS x 3 Minuten."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Orcanofaxius.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"alle","Reichweite":"16 Schritt","Probe":"MU/KL/CH","Wirkung":"Aus den Fingern des Zaubernden schießt ein Luftstrahl, der in gerader Linie sein Ziel trifft. Der Magier muss keine zusätzliche Aktion aufwenden, um nach dem Wirken des Zaubers zu treffen. Das Treffen ist in der Zauberdauer inbegriffen. Das getroffene Ziel erleidet 2W6 + (QS x 2) Trefferpunkte Schaden. Der Luftstrahl zählt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist möglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Der Strahl trifft automatisch, wenn man sich nicht verteidigt. Trifft der Luftstrahl sein Ziel, werden die TP durch den RS des Ziels vermindert. Das Ziel wird zudem bei 1-3 auf 1W6 umgestoßen und erhält den Status <em>Liegend</em>.","Publikation":null,"AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Orcanofaxius","spellextensions":{"0":["Intensiver Strahl ","(FW 8, 3 AP): Es muss nicht gewürfelt werden, ob das Ziel den Status "],"1":["Windender Strahl ","(FW 12, 6 AP): Das Ziel kann schlechter ausweichen. Das Ausweichen ist zusätzlich um 2 erschwert. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Oculus_llusionis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Mit diesem Illusionszauber ist die Täuschung des Sichtsinnes möglich. Es lassen sich bewegliche dreidimensionale Illusionen erzeugen, die sich rein auf Sicht auswirken. Details zum Thema Illusionen finden sich im Abschnitt Illusionsregeln im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":"Regelwerk Seite 295","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Oculus Illusionis","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Pentagramma.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"Dämonen","Reichweite":"8 Schritt","Probe":"MU/KL/CH","Wirkung":"Ein niederer Dämon wird gebannt, wenn der Zauberer mehr QS erzielt, als die Anrufungsschwierigkeit beträgt (Anrufungsschwierigkeiten von 0 oder höher zählen als 0). Es kann sich dabei sowohl um einen beschworenen als auch um einen freien Dämon handeln. Ebenso ist es möglich, Dämonen zu bannen, die an Objekte oder Wesen gebunden bzw. in diese eingefahren sind. Bei der Bannung wird der Dämon in das Pentagramm gezogen und wird zurück in die Siebte Sphäre gezwungen.","Publikation":null,"AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen (Zauberdauer ist nicht modifizierbar)","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Pentagramma","spellextensions":{"0":["Zauberdauer modifizierbar ","(FW 8, 3 AP): Die Zauberdauer ist modifizierbar."],"1":["Zwei Dämonen ","(FW 12, 6 AP): Der Zauber kann gegen zwei Dämonen gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit beider Dämonen zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 8 AsP pro Dämon nach dem ersten."],"2":["Drei Dämonen ","(FW 16, 9 AP): Der Zauber kann gegen drei Dämonen gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit der Dämonen zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 8 AsP pro Dämon nach dem ersten. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_OculusAstralis.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Hellsicht","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/CH","Wirkung":"Die normale Sicht des Zauberers wird um eine astrale Wahrnehmung ergänzt. Sie ist nicht detailreich genug, um magische Analysen vorzunehmen, der Zauberer kann jedoch mittels magischer Unsichtbarkeit verborgene Dinge oder Personen erkennen. Auch die Orientierung an magisch durchdrungenen Orten, beispielsweise dem Limbus, ist dank dieser Astralsicht erleichtert oder überhaupt erst möglich. Während er diese Sicht einsetzt, erleidet der Zauberer einen Modifikator von –2 auf alle Talentproben.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Oculus Astralis","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 2 AP): Die Wirkungsdauer beträgt QS x 5 Minuten."],"1":["Längere Wirkungsdauer 2 ","(FW 12, 4 AP): Die Wirkungsdauer beträgt QS x 10 Minuten. Voraussetzung: Zaubererweiterung "],"2":["Längere Wirkungsdauer 3 ","(FW 16, 6 AP): Die Wirkungsdauer beträgt QS x 30 Minuten. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Odem_Arcanum.html","properties":{"Wirkungsdauer":"1 Minute","Merkmal":"Hellsicht","Zielkategorie":"Wesen, Objekt","Reichweite":"8 Schritt","Probe":"KL/IN/IN","Wirkung":"Mit diesem Zauberspruch lässt sich aktives magisches Wirken auf Gegenständen und Personen erkennen. Je nach Stärke der astralen Kräfte im Objekt kann die Probe erleichtert oder erschwert werden. Je nach QS können dabei nachfolgende Analysen mittels des Zaubers Analys beeinflusst werden. Der Zauberer kann nur ein ausgewähltes Wesen oder Objekt in Reichweite untersuchen. Er hat keine Rundumsicht.<br/>QS 1: Ist Magie vorhanden oder nicht?<br/>QS 2: Die maximale QS bei der Magischen Analyse steigt um 1.<br/>QS 3: Die Fertigkeitsprobe eines Analys auf das gleiche Objekt ist um 1 erleichtert. <br/>QS 4: Die Fertigkeitsprobe eines Analys auf das gleiche Objekt ist um 2 erleichtert.<br/>QS 5: Die maximale QS bei der Magischen Analyse steigt um 2.<br/>QS 6: Die Fertigkeitsprobe eines Analys auf das gleiche Objekt ist um 3 erleichtert.<br/>Die Boni für die maximale QS bei der Magischen Analyse sind nicht kumulativ bzw. auch nicht die Erleichterungen beim Analys. Wer also QS 4 beim Odem erzielt hat, bekommt keine Erleichterung von 3 (1 durch QS 3 + 2 durch QS 4), sondern nur eine von 2. Allerdings erhält der Held die höchsten Boni der beiden Verbesserungsmöglichkeiten. Ein Held mit QS 6 bekommt also eine Erleichterung von 3 auf den nachfolgenden Analys (durch QS 6) und die maximale QS bei der Magischen Analyse ist um 2 erhöht (durch QS 5). Mehr zur Magischen Analyse siehe Regelwerk Seite 269.","Publikation":"Regelwerk Seite 295","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Odem Arcanum","spellextensions":{"0":["Zwei Gegenstände ","(FW 8, 1 AP): Der Zauberer kann den Spruch auf bis zu zwei Gegenstände gleichzeitig wirken."],"1":["Kegelsicht ","(FW 12, 2 AP): Der Zauber wirkt für 1 KR lang innerhalb des Sichtfelds des Zauberers bis maximal 8 Schritt Entfernung auf alle in diesem befindlichen Objekte und Wesen. Das Sichtfeld desZauberers ist nicht veränderlich, erlaubt ist also nur der Blick in eine Richtung. Da er sich nicht auf eine spezielle Sache konzentrieren kann, werden die erreichten QS vor Konsultieren der Tabelle halbiert. Diese Art der magischen Wahrnehmung zählt als ungerichtete magische Wahrnehmung."],"2":["Rundumsicht ","(FW 16, 3 AP): Für 1 KR erhält der Held eine Rundumsicht, die 8 Schritt weit reicht. Er kann sich auf der Stelle drehen, aber sich nicht anderweitig bewegen. Bei Gelingen des Zaubers erhält er automatisch nur 1 QS, kann aber damit alle Gegenstände in Blickreichweite erfassen. Diese Art der magischen Wahrnehmung zählt als ungerichtete magische Wahrnehmung."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Orcanosphaero.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"32 Schritt","Probe":"MU/KL/KO","Wirkung":"Aus den Händen des Zaubernden schießt ein Sturmball, der in gerader Linie sein Ziel trifft. Das Treffen ist in der Zauberdauer inbegriffen, der Magier muss also keine zusätzliche Aktion aufwenden. Der Sturmball explodiert, wenn er sein Ziel oder auf ein großes, festes Hindernis trifft (z. B. eine Wand oder einen Schild). Verlässt der Sturmball die Reichweite, ohne ein Ziel zu treffen, so löst er sich auf. Auch die Gefährten des Zauberers können getroffen werden, wenn er explodiert. Der Radius der Explosion beträgt 5 Schritt. Der Schaden beträgt einen Schritt um das Zentrum der Explosion herum 2W6 + (QS x 3) Trefferpunkte (auch gegen leblose Objekte als Strukturschaden). Pro Schritt Entfernung von diesem Bereich verringert sich der Schaden um 3 TP. Der Sturmball trifft automatisch, wenn man ihm nicht aktiv zu entgehen versucht. Dazu muss eine Verteidigung eingesetzt und eine Probe auf <em>Körperbeherrschung (Kampfmanöver) </em>abgelegt werden. Pro QS hat der Held sich 1 Schritt weit weg vom Zentrum der Explosion bewegt. Trifft der Sturmball sein Ziel, werden die TP durch den RS des Ziels vermindert. Alle getroffenen Ziele werden außerdem bei 1-3 auf 1W6 umgestoßen und erhalten den Status <em>Liegend</em>.","Publikation":null,"AsP-Kosten":"32 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Orcanosphaero","spellextensions":{"0":["Intensive Explosion ","(FW 8, 4 AP): Es muss nicht gewürfelt werden, ob die Ziele den Status "],"1":["Schneller Ball ","(FW 12, 8 AP): Die Ziele können schlechter ausweichen. Das Ausweichen ist um zusätzlich 1 erschwert. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Großer Radius ","(FW 16, 12 AP): Der Explosionsradius beträgt 7 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Objektbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/IN/FF","Wirkung":"Ein Zauber des Merkmals Objekt wird aufgehoben, wenn dessen QS nicht höher als die QS des Objektbanns ist. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt (z. B. einmaliger Schaden). Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche hilft er nicht. Wird der Objektbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":null,"AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Objektbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Objectofixo.html","properties":{"Wirkungsdauer":"QS Minuten","Merkmal":"Objekt","Zielkategorie":"Objekte","Reichweite":"Berührung","Probe":"MU/KL/KK","Wirkung":"Das Zielobjekt wird am Boden befestigt und kann nur mit einer um die QS x 2 erschwerten Probe auf <em>Kraftakt (Drücken &amp; Verbiegen, Stemmen &amp; Heben, Ziehen</em> <em>&amp; Zerren) </em>davon getrennt werden. Ist dies einmal gelungen, endet der Zauber vorzeitig. Das verzauberte Objekt darf maximal 5 Stein wiegen. Bei dem Boden kann es sich auch um andere waagerechte Flächen wie z. B. Tische oder Schiffsplanken handeln. Der gewählte Untergrund ist nicht dagegen gefeit, bewegt zu werden, wodurch eine Befestigung an Sand, lockerer Erde oder kleinen Möbelstücken meist nur wenig Sinn ergibt. Wird das Objekt während der Wirkungsdauer zerstört, haften stattdessen seine Trümmerstücke sowie der eventuell vorhandene Inhalt am Boden. In diesem Fall muss nur für eines der Stücke eine Probe auf <em>Kraftakt </em>gelingen, um den Zauber für alle Teile vorzeitig enden zu lassen.","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Objectofixo","spellextensions":{"0":["Längere Wirkungsdauer ","(FW 8, 1 AP): Die Wirkungsdauer beträgt QS Stunden."],"1":["Höheres Gewicht ","(FW 12, 2 AP): Das Objekt darf bis zu 25 Stein wiegen."],"2":["Wände und Decken ","(FW 16, 3 AP): Das Objekt kann auch an Wänden oder Decken fixiert werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Objectobscuro.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Objekt","Zielkategorie":"Objekte","Reichweite":"Berührung","Probe":"KL/FF/KO","Wirkung":"Das Zielobjekt wird innerhalb von 20 – (QS x 3) Kampfrunden unsichtbar. Während dieser Zeit wird es immer durchscheinender, bis es schließlich nicht mehr zu sehen ist. Objekte und Personen in oder hinter dem Objekt bleiben weiterhin sichtbar. Das verzauberte Objekt darf maximal 5 Stein wiegen.","Publikation":null,"AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Objectobscuro","spellextensions":{"0":["Längere Wirkung ","(FW 8, 2 AP): Die Wirkung hält pro investierten 4 AsP 30 Minuten an."],"1":["Keine Aufrechterhaltung ","(FW 12, 4 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Unsichtbarer Inhalt ","(FW 16, 6 AP): Auch Inhalte des verzauberten Gegenstands mit einem Gewicht bis 10 Stein werden unsichtbar."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Menetekel.html","properties":{"Wirkungsdauer":"QS x 30 Minuten","Merkmal":"Illusion","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"KL/CH/FF","Wirkung":"Der Zauber ermöglicht es, illusionäre Schriftzüge erscheinen zu lassen. Alles, was der Zauberer sagt, erscheint an einer imaginären Wand von bis zu 10 Rechtschritt Fläche, die sich beliebig in Breite und Länge ausformen kann. Die Buchstaben können in beliebiger Größe und Anzahl dargestellt werden, gelten aber nicht als Sichthindernis. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":null,"AsP-Kosten":"2 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Menetekel","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 1 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 2 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 3 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Nuntiovolo.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Sphären","Zielkategorie":"Dämonen","Reichweite":"4 Schritt","Probe":"MU/KL/CH","Wirkung":"Der Zauberer beschwört eine Rauchgestalt in Form eines Vogels oder einer Fledermaus herauf, die ein maximal 250 Gramm wiegendes Objekt an einen vom Zauberer bestimmten Ort bringen kann. Die Entfernung zum Zielort darf höchstens 20 Meilen + 10 Meilen pro 1 AsP betragen. Sobald die Maximalentfernung überschritten wird, löst sich die Gestalt auf und lässt den Gegenstand fallen. Die Kreatur bewegt sich mit einer GS von 10 fort, ohne dabei vom Wetter beeinflusst zu werden. Sie besitzt eine LE in Höhe der QS und verfügt über keinen Verteidigungswert, allerdings ist sie von profanen Waffen nicht zu verletzen.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Nuntiovolo","spellextensions":{"0":["Höhere Grundreichweite ","(FW 8, 2 AP): Die Grundreichweite der Kreatur beträgt 50 statt 20 Meilen."],"1":["Höhere Flugreichweite ","(FW 12, 4 AP): Die Kreatur kann pro investiertem 1 AsP 25 Meilen statt 10 zurücklegen."],"2":["Schnelle Kreatur ","(FW 16, 6 AP): Die Kreatur besitzt eine GS von 20 statt von 10."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Nebelwand.html","properties":{"Wirkungsdauer":"QS x 15 Minuten, danach verweht der Nebel","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"16 Schritt","Probe":"MU/KL/CH","Wirkung":"Der Zaubernde erschafft eine Zone aus dichtem Nebel, der sich von Wind nicht fortwehen lässt. Je investiertem AsP kann er 200 Kubikmeter Nebel erschaffen (was etwa einer kleineren Hütte entspricht). Die Nebelwolke kann nach Belieben geformt werden, ob als kompakte Kugel, als lang gezogenes Band oder gar in Form eines Drachen. Der Nebel erzeugt einen Sichtmodifikator der Stufe 2.","Publikation":"Regelwerk Seite 295","AsP-Kosten":"mindestens 4 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Elfen"},"spellclass":"Zauberspruch","name":"Nebelwand","spellextensions":{"0":["Wandernder Nebel ","(FW 8, 3 AP): Die Nebelwolke kann sich mit GS 4 nach dem Willen des Zauberers fortbewegen und dabei auch ihre Form ändern."],"1":["Lange Wirkungsdauer ","(FW 12, 6 AP): Die Wirkungsdauer beträgt QS Stunden."],"2":["Dichter Nebel ","(FW 16, 9 AP): Der Nebel ist so dicht, dass er als ein Sichtmodifikator Stufe 3 zählt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Manus_Miracula.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Keines","Reichweite":"selbst","Probe":"KL/FF/KK","Wirkung":"Der Zaubernde kann auf QS Schritt Distanz unbelebte Gegenstände verschieben, drehen, verformen und biegen, als würde er sie mit einer Hand ergreifen. Er kann jedoch maximal 1 Kilogramm damit anheben. Bei Proben auf Talente, beispielsweise um mit einem telekinetisch gehaltenen Pinsel etwas zu malen oder mit dem Zauberspruch eine Figur aus Ton zu formen, haben die dazu nötigen körperlichen Eigenschaften einen Wert von QS + 7. Die Werte können auch die des Helden übersteigen. Die Krafteinwirkungen wirken punktuell und recht langsam. Daher können mit den so manipulierten Gegenständen keine Attacken oder Paraden ausgeführt werden.","Publikation":"Regelwerk Seite 294","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Zauberdauer":"2 Aktionen","Verbreitung":"Allgemein"},"spellclass":"Zauberspruch","name":"Manus Miracula","spellextensions":{"0":["Mehr Gewicht (","FW 8, 1 AP): Der Zauber kann ein Gewicht von maximal 3 Stein bewegen."],"1":["Bessere Werte ","(FW 12, 2 AP): Die körperlichen Eigenschaften verbessern sich auf QS + 9."],"2":["Noch bessere Werte ","(FW 16, 3 AP): Die körperlichen Eigenschaften verbessern sich auf QS + 11. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Memorans.html","properties":{"Wirkungsdauer":"1 Jahr","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende (selbst)","Reichweite":"8 Schritt","Probe":"KL/KL/IN","Wirkung":"Durch den Zauberspruch kann sich der Zauberer alles einprägen, was er in den nächsten QS x 5 Minuten wahrnimmt, als besäße er ein fotografisches Gedächtnis. Dies kann ein Gespräch, gelesene Seiten, Wegstrecken, optische Eindrücke oder Gerüche umfassen.","Publikation":"Die Streitenden Königreiche Seite 166","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Memorans","spellextensions":{"0":["Merk dir mehr ","(FW 8, 1 AP): Der Zauberer kann sich Eindrücke für QS x 15 Minuten einprägen."],"1":["Merk dir noch mehr ","(FW 12, 2 AP): Der Zauberer kann sich Wahrnehmungen für QS x 1 Stunde einprägen. Voraussetzung: Zaubererweiterung "],"2":["Längere Wirkungsdauer ","(FW 16, 3 AP): Die Wirkungsdauer beträgt 5 Jahre."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Manifesto.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar</strong>","Zielkategorie":"Objekt","Reichweite":"1 Schritt","Probe":"MU/KL/CH","Wirkung":"Es manifestiert sich eine Handvoll eines bestimmten Elements nach Wahl des Zaubernden. Es handelt sich um einfache Manifestationen wie eine kleine Flamme, einen Kieselstein oder ein Stück Eis. Elementaristen erlernen diesen Zauberspruch früh in ihrer Ausbildung, um die Grundzüge der Herbeirufung zu erlernen, ohne Elementare zu belästigen (Insgesamt erscheint etwa eine Menge bvon 0,05 l pro QS).","Publikation":"Regelwerk Seite 294","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Manifesto","spellextensions":{"0":["Größere Menge ","(FW 8, 1 AP): Die Menge des Elements steigt auf 0,1 l pro QS."],"1":["Noch größere Menge ","(FW 12, 2 AP): Die Menge des Elements steigt auf 0,2 l pro QS. Voraussetzung: Zaubererweiterung "],"2":["Noch viel größere Menge ","(FW 16, 3 AP): Die Menge des Elements steigt auf 0,4 l pro QS. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_LastdesAlters.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Dämonisch","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"IN/CH/KO (modifiziert um ZK)","Wirkung":"Das Ziel altert körperlich stark und wird zu einem Greis. Es wird jedoch nicht an Altersschwäche sterben und erhält am Ende der Wirkungsdauer sein ursprüngliches Alter zurück. Alle körperlichen Handlungen des Opfers sind aufgrund akuter Altersschwäche um QS/2 erschwert (z. B. Körpertalente, Handwerkstalente). Dies umfasst auch Kampfhandlungen und somit AT, PA, AW und FK. Der Zaubernde hat nach dem Wirken des Zaubers QS Kampfrunden Zeit, um sein Ziel zu berühren, damit der Spruch seine Wirkung entfalten kann. Ansonsten verpufft der Zauberspruch wirkungslos. Will ein Ziel nicht berührt werden, ist eine gelungene, nicht verteidigte Raufen-AT +2 oder eine AT +2 mit dem Traditionsartefakt notwendig.","Publikation":null,"AsP-Kosten":"8 AsP (Kosten nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Last des Alters","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 3 AP): Der Zauber wirkt QS x 5 Minuten."],"1":["Längere Berührungszeit ","(FW 16, 6 AP): Nach dem Wirken des Zaubers hat der Zauberer QS x 2 KR Zeit, um sein Opfer zu berühren."],"2":["Längere Wirkungsdauer 2 ","(FW 16, 9 AP): Der Zauber wirkt QS Stunden. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_ManusIllusionis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Zone (max. 3 x 3 Schritt)","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Mit diesem Illusionszauber ist die Täuschung des Tastsinnes möglich. Die Illusion muss dabei auf eine bereits vorhandene Oberfläche gelegt werden, da sie lediglich die Art des Fühlens ändert, jedoch nicht etwas Fassbares aus dem Nichts zu erschaffen vermag. Nur die Oberfläche fühlt sich anders an, nicht jedoch das gesamte Objekt. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":null,"AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Manus Illusionis","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 1 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 2 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 3 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Motoricus.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Telekinese","Zielkategorie":"Objekt","Reichweite":"8 Schritt","Probe":"KL/FF/KK","Wirkung":"Der Zaubernde kann unbelebte Gegenstände telekinetisch anheben und bewegen. Sie bewegen sich mit einer Geschwindigkeit von maximal QS + 2 Schritt pro Aktion und dürfen maximal QS x 20 Stein wiegen. Pro 5 Stein Gewicht muss der Zaubernde dafür 1 AsP aufbringen. Befinden sich andere Objekte auf dem zu bewegenden Gegenstand, zählt deren Gewicht mit hinzu. Die Bewegungen des Objekts sind träge, weshalb sie für Angriffe oder Paraden ungeeignet sind. Um ein so bewegtes Objekt durch Festhalten oder Drücken aufzuhalten, ist eine um QS erschwerte Probe auf <em>Kraftakt (Ziehen &amp; Zerren) </em>nötig.","Publikation":"Regelwerk Seite 294","AsP-Kosten":"mindestens 4 AsP (Aktivierung) + Hälfte der notwendigen AsP pro 5 Minuten (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Motoricus","spellextensions":{"0":["Mehr Gewicht (","FW 8, 2 AP): Pro 10 Stein Gewicht muss der Zauberer 1 AsP aufwenden."],"1":["Noch mehr Gewicht ","(FW 12, 4 AP): Pro 15 Stein Gewicht muss der Zauberer 1 AsP aufwenden. Voraussetzung: Zaubererweiterung "],"2":["Mehrere Objekte ","(FW 16, 6 AP): Mit dem Zauber können mehrere Objekte gleichzeitig bewegt werden, deren Gesamtgewicht aber nicht über dem zulässigen Maximum liegen darf."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Kusch.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Einfluss","Zielkategorie":"Tiere","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Das verzauberte Tier wird von Angst vor dem Zauberer erfüllt. Die genauen Auswirkungen hängen von der QS ab:</li><li><strong>QS 1: </strong>1 Stufe <em>Furcht</em>, aber nur für 2 KR</li><li><strong>QS 2: </strong>1 Stufe <em>Furcht</em></li><li><strong>QS 3: </strong>2 Stufen <em>Furcht</em></li><li><strong>QS 4: </strong>3 Stufen <em>Furcht</em></li><li><strong>QS 5: </strong>4 Stufen <em>Furcht</em></li><li><strong>QS 6: </strong>4 Stufen <em>Furcht</em>, aber für die doppelte Wirkungsdauer","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"Druiden, Scharlatane"},"spellclass":"Zauberspruch","name":"Kusch","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 1 AP): Die Zustandsstufen können auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Werden z. B. insgesamt 3 Stufen "],"1":["Längere Wirkung ","(FW 12, 2 AP): Die Wirkungsdauer steigt auf QS x 5 Minuten."],"2":["Zwei Ziele ","(FW 16, 3 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 2 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_LungedesLeviatan.html","properties":{"Wirkungsdauer":"QS Kampfrunden","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"MU/IN/KO","Wirkung":"Während der Wirkungsdauer kann der Zauberer sprinten, ohne dass ihn dies erschöpft. Sein FW in <em>Körperbeherrschung (Laufen) </em>wird für die Wirkungsdauer verdoppelt. Bei Verfolgungsjagden gilt diese Verdopplung jedoch vor der Einberechnung der effektiven GS.","Publikation":null,"AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Elfen, Gildenmagier"},"spellclass":"Zauberspruch","name":"Lunge des Leviatan","spellextensions":{"0":["Reichweite Berührung ","(FW 8, 1 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"1":["Andere Tätigkeiten ","(FW 12, 2 AP): Die Wirkung gilt auch für andere Fertigkeitsproben bei Verfolgungsjagden, z. B. "],"2":["Längere Wirkungsdauer ","(FW 16, 3 AP): Die Wirkungsdauer beträgt QS x 3 KR."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Katzenaugen.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/KO","Wirkung":"Die Lichtempfindlichkeit der Augen des Zieles erhöht sich ungemein. So kann es auch in fast völliger Dunkelheit deutlich besser sehen. Pro QS sinkt die Stufe der Sichterschwernis um 1. In völliger Dunkelheit nützt dieser Zauberspruch nichts.","Publikation":"Regelwerk Seite 294","AsP-Kosten":"2 AsP (Aktivierung) + 1 AsP pro 10 Minuten","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Katzenaugen","spellextensions":{"0":["Längere Aufrechterhaltung ","(FW 8, 1 AP): Pro 1 AsP kann der Spruch 1 Stunde aufrechterhalten werden."],"1":["Reichweite Berührung ","(FW 12, 2 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"2":["Mehrere Lebewesen ","(FW 16, 3 AP): Der Zauber kann auch auf Lebewesen gewirkt werden, sogar auf mehrere gleichzeitig bis zur Anzahl der QS. Pro Lebewesen nach dem ersten entstehen zusätzliche Kosten von 1 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Kr%C3%B6tensprung.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/KO","Wirkung":"Der Verzauberte kann einmalig unnatürlich weit und hoch springen. Die Weite des Sprungs wird um QS + 3 in Schritt erhöht, die Höhe um QS. Mehr zu den Regeln des Springens siehe Regelwerk Seite 349.","Publikation":"Regelwerk Seite 294","AsP-Kosten":"2 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Krötensprung","spellextensions":{"0":["Weiter und höher 1 ","(FW 8, 1 AP): Die Weite des Sprungs steigt auf QS +5, die Höhe bis auf QS +1 Schritt."],"1":["Weiter und höher 2 ","(FW 12, 2 AP): Die Weite des Sprungs steigt auf QS +7, die Höhe bis auf QS +3 Schritt. Voraussetzung: Zaubererweiterung "],"2":["Krötenflink ","(FW 16, 3 AP): Der Verzauberte kann QS KR lang beliebig viele Sprünge ausführen. Pro KR kann ein Sprung durchgeführt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Karnifilo.html","properties":{"Wirkungsdauer":"QS x 2 KR (statt der üblichen Zeit von 2W20 KR des Status’ <em>Blutrausch</em>)","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Das Ziel erhält den Status <em>Blutrausch</em>.","Publikation":"Klingen der Nacht Seite 109","AsP-Kosten":"16 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Karnifilo","spellextensions":{"0":["Wüterich ","(FW 8, 2 AP): Die Boni auf AT und TP steigen zusätzlich um +1."],"1":["Vorhersehbares Ende ","(FW 12, 4 AP): Der Zauberer kann zu Beginn des Zaubers festlegen, dass der "],"2":["Nur bestimmte Ziele ","(FW 16, 6 AP): Der Zauberer kann Ziele ausschließen, die der Verzauberte nicht angreift."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Invercano.html","properties":{"Wirkungsdauer":"QS x 2 Kampfrunden","Merkmal":"Antimagie","Zielkategorie":"selbst","Reichweite":"selbst","Probe":"MU/IN/FF","Wirkung":"Wird der Zauberer Ziel eines Angriffszaubers, kann er diesen mittels Raufen oder seinem Traditionsartefakt auf seinen Gegner zurückwerfen. Hierzu ist eine Parade erforderlich, die um 4 Punkte erschwert ist. Gelingt diese, wird der Zauber auf den Anwender zurückgeworfen und erreicht sein neues Ziel zu Beginn der nächsten Kampfrunde. Als Angriffszauber gelten alle Zauber, die TP oder SP verursachen und bei denen mindestens 1 Würfel geworfen wird. Zauber mit Zonenwirkung können nicht zurückgeworfen werden. Weisen das ursprüngliche und das neue Ziel eine unterschiedliche SK bzw. ZK auf, wird dies ignoriert. Auch die Reichweite spielt für das Zurückwerfen des Zaubers keine Rolle. Jedes Mal, wenn ein Zauber durch einen Invercano zurückgeworfen wird, sinkt seine QS um 1. Sinkt die QS dadurch auf 0, löst sich der Zauber auf.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Invercano","spellextensions":{"0":["Verbesserte Reaktion 1 ","(FW 8, 3 AP): Die Parade, um den Zauber zurückzuwerfen, ist nur um 2 erschwert."],"1":["Verbesserte Reaktion 2 ","(FW 12, 6 AP): Die Parade, um den Zauber zurückzuwerfen, ist nicht erschwert. Voraussetzung: Zaubererweiterung "],"2":["Zurückspiegeln auf einen Dritten ","(FW 16, 9 AP): Mittels einer zusätzlich um 4 erschwerten PA kann der Zauber auch auf eine dritte Person zurückgeworfen werden. Diese darf maximal so weit entfernt sein, wie der Zauberer, der den Angriffszauber gewirkt hat und muss unter dieselbe Zielkategorie fallen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Klarum_Purum.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"4 Schritt","Probe":"KL/IN/CH","Wirkung":"Der Zauberspruch hebt die Wirkung eines Giftes auf. Die Giftstufe darf die QS nicht übersteigen, sonst wirkt der Zauber nicht und gilt als misslungen.","Publikation":"Die Streitenden Königreiche Seite 166","AsP-Kosten":"3 AsP pro Giftstufe (Kosten nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Klarum Purum","spellextensions":{"0":["Ausnahme ","(FW 8, 2 AP): Der Zauberer kann entscheiden, dass Alkohol neutralisiert werden. Er kann einzelne Substanzen benennen, die nicht herausgefiltert werden sollen."],"1":["Tadelloser Entgifter ","(FW 12, 4 AP): Für jeweils 4 zusätzliche AsP kann die QS bei einer gelungenen Probe um 1 erhöht werden (bis zu einem Maximum von QS 6)."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Incendio.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Lebewesen","Reichweite":"16 Schritt","Probe":"KL/CH/KO (modifiziert um ZK)","Wirkung":"Der Incendio lässt das ausgewählte Lebewesen in Flammen aufgehen.<ul><li><strong>QS 1: </strong>1W6 TP</li><li><strong>QS 2: </strong>1W6+2 TP</li><li><strong>QS 3: </strong>1W6+2 TP, <em>Brennend </em>auf kleiner Fläche</li><li><strong>QS 4: </strong>1W6+4 TP, <em>Brennend </em>auf kleiner Fläche</li><li><strong>QS 5: </strong>1W6+6 TP, <em>Brennend </em>auf kleiner Fläche</li><li><strong>QS 6: </strong>1W6+6 TP, <em>Brennend </em>auf großer Fläche</li></ul>Der Zauberspruch trifft sein Ziel automatisch. Der Gegner kann weder ausweichen noch parieren.","Publikation":null,"AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Incendio","spellextensions":{"0":["Zielkategorie Objekt ","(FW 8, 2 AP): Der Zauber ist auch gegen Objekte einsetzbar. Sie erleiden Schaden ohne den Status "],"1":["Erhöhter Schaden ","(FW 12, 4 AP): Der Zauber verursacht zum üblichen Schaden +1 TP."],"2":["Wasser-Zerstörer ","(FW 16, 6 AP): Gegen Ziele des Elements Wasser können die TP verdoppelt werden. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Invominima.html","properties":{"Wirkungsdauer":"<strong> sofort</strong>","Merkmal":"Sphären","Zielkategorie":"Objekt","Reichweite":"1 Schritt","Probe":"MU/CH/KO","Wirkung":"Es manifestiert sich eine Handvoll Substanz aus den Niederhöllen. Meist handelt es sich um Schleim, Säure, schweflige Dämpfe oder flüssiges Feuer. Das genaue Ergebnis der Beschwörung lässt sich nicht beeinflussen. Der Zauberspruch gilt als einfacher und risikoarmer Übungszauber für Beschwörer (Insgesamt erscheint etwa eine Menge von 0,05 l pro QS).","Publikation":"Regelwerk Seite 293","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Invocatio Minima","spellextensions":{"0":["Größere Menge ","(FW 8, 1 AP): Die Menge der dämonischen Essenz steigt auf 0,1 l pro QS."],"1":["Noch größere Menge ","(FW 12, 2 AP): Die Menge der dämonischen Essenz steigt auf 0,2 l pro QS. Voraussetzung: Zaubererweiterung "],"2":["Noch viel größere Menge ","(FW 16, 3 AP): Die Menge der dämonischen Essenz steigt auf 0,4 l pro QS. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Impersona.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"IN/CH/FF","Wirkung":"Der Zauberer kann sein Gesicht als ein beliebiges anderes erscheinen lassen. Soll es sich dabei um das Gesicht einer speziellen Person handeln, muss der Zauberer diese gut kennen oder während der Zauberdauer sehen können. Körper, Stimme und Kleidung sind nicht vom Zauber betroffen. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":null,"AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Impersona","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Illusionsbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Ein Zauber des Merkmals Illusion wird aufgehoben, wenn dessen QS nicht höher ist als die QS des Illusionsbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt. Auch gegen Zauber, deren Wirkung bereits vollendet ist oder permanent wirkende Sprüche hilft er nicht. Wird der Illusionsbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":null,"AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Illusionsbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Ignisphaero.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"32 Schritt","Probe":"MU/KL/KO","Wirkung":"Aus den Händen des Zaubernden schießt ein Feuerball, der in gerader Linie sein Ziel trifft. Der Treffer des Feuerballs erfolgt am Ende der Zauberdauer, der Magier muss keine zusätzliche Aktion dafür aufwenden. Der Feuerball explodiert, wenn er sein Ziel oder auf ein großes, festes Hindernis trifft (z. B. eine Wand oder einen Schild). Verlässt der Feuerball die Reichweite, ohne ein Ziel zu treffen, so löst er sich auf. Auch die Gefährten des Zauberers können von dem Feuerball getroffen werden, wenn er explodiert. Der Radius der Explosion beträgt 5 Schritt. Der Schaden beträgt bis zu einem Schritt vom Zentrum entfernt 2W6+(QS x 3) Trefferpunkte (auch gegen leblose Objekte als Strukturschaden).<br/>Pro Schritt Entfernung von diesem Bereich verringert sich der Schaden um jeweils 3 TP. Der Feuerball trifft automatisch, wenn man ihm nicht aktiv zu entgehen versucht. Dazu muss eine Verteidigung eingesetzt und eine Probe auf Körperbeherrschung (Kampfmanöver) abgelegt werden. Pro QS hat der Held sich 1 Schritt vom Zentrum der Explosion entfernt. Trifft der Feuerball sein Ziel, werden die TP durch den RS des Ziels vermindert. Entflammbare Ziele fangen bei 1-3 auf 1W6 Feuer.","Publikation":"Die Streitenden Königreiche Seite 165","AsP-Kosten":"32 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Ignisphaero","spellextensions":{"0":["Intensive Explosion ","(FW 8, 4 AP): Es muss nicht gewürfelt werden, ob die Ziele den Status "],"1":["Schneller Ball ","(FW 12, 8 AP): Die Ziele können schlechter ausweichen. Das Ausweichen ist um zusätzlich 1´erschwert. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Großer Radius ","(FW 16, 12 AP): Der Explosionsradius beträgt 7 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Ignorantia.html","properties":{"Wirkungsdauer":"QS Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"16 Schritt","Probe":"IN/CH/GE (modifiziert um SK)","Wirkung":"Das Ziel ignoriert den Zauberer und seine Handlungen. Dadurch kann dieser unbemerkt an seinem Opfer vorbeilaufen oder vor seinen Augen einen Diebstahl begehen. Interagiert der Zaubernde während der Wirkungsdauer mit dem Opfer oder greift er dieses an, endet der Zauber sofort. Achtet das Ziel explizit auf eine Sache, etwa eine Tür oder einen Gegenstand, bemerkt es alle diesbezüglichen Veränderungen, jedoch ohne die Quelle der Veränderung ausmachen zu können. So bemerkt es z. B. das Öffnen oder Schließen einer bewachten Tür, vermag aber nicht zu sagen, wer genau dafür verantwortlich war.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Ignorantia","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."],"1":["Höhere Wirkungsdauer ","(FW 12, 4 AP): Die Wirkungsdauer erhöht sich auf QS x 5 Minuten."],"2":["Jemand anderen benennen ","(FW 16, 6 AP): Der Zauberer kann die Wirkung des Spruchs auf jemand anderen übertragen, statt selbst Nutznießer zu sein. Diese andere Person muss er beim Wirken des Zaubers berühren."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Imperavi.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/KL/CH (modifiziert um SK)","Wirkung":"Das Ziel befolgt einen einzigen Befehl des Zauberers, der seinem Selbsterhaltungstrieb nicht offensichtlich zuwiderläuft. Es wird also einen Diebstahl begehen, eine einzige Frage beantworten oder dem Zauberer in einem Kampf beistehen, nicht aber von einer Klippe springen oder seine eigenen Hände verspeisen. Der Zauber kann nicht dazu eingesetzt werden, dass das Opfer während der kompletten Wirkungsdauer Befehle entgegennimmt („Ich befehle dir, allen meinen Befehlen zu gehorchen.“) oder mehrere Fragen wahrheitsgemäß zu beantworten. Auch lange Befehlsketten sind nicht erlaubt („Öffne die Tür, gehe hinein, mache das Licht an und zerschlage die Kiste.“), sondern nur einzelne, knappe Befehle. Der Meister hat hierbei das letzte Wort, ob ein Befehl möglich ist oder nicht.","Publikation":null,"AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"16 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Imperavi","spellextensions":{"0":["Ohne Berührung ","(FW 8, 3 AP): Der Zauber wirkt auf 1 Schritt Reichweite."],"1":["Mehr Befehle ","(FW 12, 6 AP): Der Zauberer kann dem Opfer QS/2 Befehle geben."],"2":["Ohne Erinnerung ","(FW 16, 9 AP): Der Zauberer kann entscheiden, ob das Opfer sich an sein Handeln unter Einfluss des Zaubers erinnern kann oder nicht. Es ist dabei möglich, dass er ihm seine Erinnerung ganz oder teilweise nimmt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Horriphobus.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Dem Verzauberten gegenüber wirkt der Zaubernde wie eine Angst einflößende Gestalt aus seinen Alpträumen. Die Stärke dieses Eindrucks variiert mit der QS:<br/>QS 1: 1 Stufe Furcht, aber nur für 2 KR<br/>QS 2: 1 Stufe Furcht<br/>QS 3: 2 Stufen Furcht<br/>QS 4: 3 Stufen Furcht<br/>QS 5: 4 Stufen Furcht<br/>QS 6: 4 Stufen Furcht, aber für die doppelte Wirkungsdauer","Publikation":"Regelwerk Seite 293","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Horriphobus","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 2 AP): Die Zustandsstufen können auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Kann der Zauberer z. B. 3 Stufen "],"1":["Längere Wirkung ","(FW 12, 4 AP): Die Wirkungsdauer steigt auf QS x 4 Minuten."],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauberer kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Ignifaxius.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"alle","Reichweite":"16 Schritt","Probe":"MU/KL/CH","Wirkung":"Aus den Fingern des Zaubernden schießt einen Flammenstrahl, der in gerader Linie sein Ziel trifft. Der Magier muss keine zusätzliche Aktion aufwenden, um nach dem Wirken des Zaubers zu treffen. Das Treffen ist in der Zauberdauer inbegriffen. Das getroffene Ziel erleidet 2W6+(QS x 2) Trefferpunkte Schaden. Der Flammenstrahl zählt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist möglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Der Strahl trifft automatisch, wenn man sich nicht verteidigt. Trifft der Flammenstrahl sein Ziel, werden die TP durch den RS des Ziels vermindert. Entflammbare Ziele werden bei 1-3 auf 1W6 angezündet.","Publikation":"Regelwerk Seite 293","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Ignifaxius","spellextensions":{"0":["Intensiver Strahl ","(FW 8, 3 AP): Es muss nicht gewürfelt werden, ob das Ziel den Status "],"1":["Windender Strahl ","(FW 12, 6 AP): Das Ziel kann schlechter ausweichen. Das Ausweichen ist zusätzlich um 2 erschwert. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauberer kann bis zu 2 Ziele gleichzeitig treffen. Die AsP-Kosten des Zaubers verdoppeln sich."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_H%C3%B6llenpein.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Verzauberte erleidet Schmerzen, die sich oft in Krämpfen manifestieren. Der Höllenpein ersetz bei Folterungen das Talent Einschüchtern (Folter). Das Opfer muss eine Vergleichsprobe mit Selbstbeherrschung (Folter widerstehen) gegen den Höllenpein bestehen, die um die Schmerzstufe des Zauberspruchs erschwert ist (zusätzlich<br/>zu allen sonstigen Modifikatoren). Der Zaubernde hat nach dem Wirken des Zaubers QS Kampfrunden Zeit, um sein Ziel zu berühren, damit der Spruch seine Wirkung entfalten kann. Ansonsten verpufft der Zauber wirkungslos. Will ein Ziel nicht berührt werden, ist eine gelungene, nicht verteidigte Raufen-AT notwendig.<br/>QS 1: keine zusätzliche Wirkung<br/>QS 2: 1 Stufe Schmerz, 0 SP<br/>QS 3: 1 Stufe Schmerz, 1 SP<br/>QS 4: 2 Stufen Schmerz, 1 SP<br/>QS 5: 2 Stufen Schmerz, 2 SP<br/>QS 6: 3 Stufen Schmerz, 2 SP","Publikation":"Die Streitenden Königreiche Seite 165","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Höllenpein","spellextensions":{"0":["Geistige Umnachtung ","(FW 8, 2 AP): Nach dem Ende der Wirkungsdauer erhält das Ziel 1 Stufe "],"1":["Krämpfe ","(FW 12, 4 AP): Nach dem Ende der Wirkungsdauer erhält das Ziel 1 Stufe "],"2":["Längere Berührungszeit ","(FW 16, 6 AP): Nach dem Wirken des Zaubers hat der Zauberer QS x 2 KR Zeit, um sein Opfer zu berühren."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hexenkrallen.html","properties":{"Wirkungsdauer":"QS x 3 in Minuten","Merkmal":"Verwandlung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/KO","Wirkung":"Der Hexe wachsen an beiden Händen scharfe Krallen. Sie verursacht damit 1W6+3 TP im Nahkampf und verwendet dafür die Kampftechnik Raufen. Die Hexe gilt damit weiterhin als unbewaffnet. Hexenkrallen gelten als magische Waffen.","Publikation":"Regelwerk Seite 292","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Hexenkrallen","spellextensions":{"0":["Ein- und ausfahrbare Krallen ","(FW 8, 1 AP): Die Hexe kann die Krallen aus- oder einfahren. Dies erfordert jeweils 1 freie Aktion."],"1":["Kletterhilfe ","(FW 12, 2 AP): Die Krallen erleichtern Probe auf "],"2":["Scharfe Krallen ","(FW 16, 3 AP): Die Krallen richten 1W6+4 TP an."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hexengalle.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/KO","Wirkung":"Der Speichel der Hexe verwandelt sich augenblicklich in ätzende Säure, die für die Hexe selbst jedoch harmlos ist. Der Speichel verursacht 1W6+(QS) Trefferpunkte an dem nächsten Ziel, das die Hexe anspuckt. Die Spuckreichweite beträgt 3 Schritt. Die Hexe muss keine zusätzliche Aktion aufwenden, um nach dem Wirken des Zaubers zu spucken. Das Spucken ist in der Zauberdauer inbegriffen. Die Spucke trifft automatisch, wenn der Gegner sich nicht verteidigt. Der Spuckangriff zählt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist möglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Trifft die Spucke ihr Ziel, werden die TP durch den RS des Ziels vermindert.","Publikation":"Regelwerk Seite 292","AsP-Kosten":"4 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Hexengalle","spellextensions":{"0":["Zerstäuber ","(FW 8, 2 AP): Eine Verteidigung gegen den Spuckangriff ist um 2 erschwert."],"1":["Höhere Spuckreichweite ","(FW 12, 4 AP): Die Spuckreichweite beträgt 6 Schritt."],"2":["Nachwirkung ","(FW 16, 6 AP): Am Ende der nachfolgenden KR erleidet das Opfer eines erfolgreichen Hexengallen-Angriffs noch einmal 1W3 + QS/2 TP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hexenholz.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Telekinese","Zielkategorie":"Objekt (Holz)","Reichweite":"16 Schritt","Probe":"KL/FF/KK","Wirkung":"Die Zaubernde kann hölzerne Gegenstände telekinetisch anheben und bewegen. Der Gegenstand kann auch andere Materialien beinhalten, die dann zum Gewicht dazu zählen, aber zumindest ein Teil muss aus Holz bestehen. Die Gegenstände bewegen sich mit einer Geschwindigkeit von maximal QS + 2 Schritt pro Aktion und dürfen maximal QS x 20 Stein wiegen. Pro 5 Stein Gewicht muss die Zaubernde dafür 1 AsP aufbringen. Befinden sich andere Objekte an oder auf dem zu bewegenden Gegenstand, zählt deren Gewicht mit hinzu. Die Bewegungen des Objekts sind träge, weshalb sie für Angriffe oder Paraden ungeeignet sind. Um ein so bewegtes Objekt durch Festhalten oder Drücken aufzuhalten, ist eine um die QS erschwerte Probe auf Kraftakt (Ziehen &amp; Zerren) nötig.","Publikation":"Die Streitenden Königreiche Seite 165","AsP-Kosten":"mindestens 4 AsP (Aktivierung) + Hälfte der Aktivierungskosten pro 5 Minuten (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Hexenholz","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Herzschlagruhe.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Dämonisch","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"MU/CH/KK (modifiziert um ZK)","Wirkung":"Der Herzschlag des Ziels wird ausgesetzt, wodurch es jede Kampfrunde 1W3 LeP verliert. Mit Hilfe einer Probe auf <em>Selbstbeherrschung </em>erschwert um die QS oder auf <em>Heilkunde Wunden (Stabilisieren) </em>erschwert um QS/2 kann die Wirkung des Zaubers gestoppt werden. Eine Probe zählt als Aktion. Die <em>Heilkunde Wunden</em>- Probe kann sowohl das Opfer des Zaubers selbst als auch eine dritte Person einsetzen.","Publikation":null,"AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Herzschlag ruhe","spellextensions":{"0":["Größerer Schmerz ","(FW 8, 3 AP): Der Zauber richtet pro KR 1W3+1 SP an."],"1":["Noch größerer Schmerz ","(FW 12, 6 AP): Der Zauber richtet pro KR 1W3+2 SP an. Voraussetzung: Zaubererweiterung "],"2":["Noch viel größerer Schmerz ","(FW 16, 9 AP): Der Zauber richtet pro KR 1W3+3 SP an. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hexenknoten.html","properties":{"Wirkungsdauer":"QS x 10 in KR","Merkmal":"Einfluss","Zielkategorie":"Zone","Reichweite":"16 Schritt","Probe":"MU/IN/CH","Wirkung":"Der Hexenknoten erzeugt eine imaginäre Barriere für alle Kulturschaffenden, die sich der Barriere bis auf 16 Schritt nähern. Jedes Opfer nimmt die Barriere anders war, z. B. als Abgrund oder Feuerwand. Auch wenn mehreren Opfern auffällt, dass sie jeweils etwas anderes wahrnehmen, können sie dieses Hindernis nicht überwinden. Die Hexe muss einen Mittelpunkt innerhalb der Reichweite des Zauberspruchs benennen. Von dort aus wirkt gradlinig eine Barriere, die QS x 2 Schritt lang ist. Den exakten Verlauf durch den Mittelpunkt kann die Hexe festlegen. Ein Held, der die Barriere des Hexenknotens dennoch überschreiten will, muss eine Probe auf Willenskraft (Bedrohungen standhalten) erschwert um 2+QS des Zauberspruchs bestehen. Gelingt diese, kann er passieren und die Barriere übt während ihrer Wirkungsdauer keinen Einfluss mehr auf ihn aus. Misslingt die Probe, bleibt der Hexenknoten für ihn ein unüberwindbares Hindernis. Ein Held kann 1 Aktion aufwenden und mit einer Wiederholungsprobe erneut versuchen, die Barriere zu durchdringen.","Publikation":"Die Streitenden Königreiche Seite 165","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Hexenknoten","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Heptagramma.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"Beseelte, Geister","Reichweite":"8 Schritt","Probe":"MU/KL/CH","Wirkung":"Ein niederer Geist wird gebannt, wenn der Zauberer mehr QS erzielt, als die Anrufungsschwierigkeit beträgt (Anrufungsschwierigkeiten von 0 oder höher zählen als 0). Es kann sich dabei sowohl um einen beschworenes als auch um ein freies Geisterwesen handeln. Ebenso ist es möglich, Geister zu bannen, die an Objekte oder Wesen gebunden bzw. in diese eingefahren sind. Bei der Bannung wird der Geist in das Heptagramm gezogen und wechselt in eines der Totenreiche über. Anschließend kann er nicht mehr eigenständig in der Dritten Sphäre in Erscheinung treten, jedoch weiterhin beschworen werden.","Publikation":null,"AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen (Zauberdauer ist nicht modifizierbar)","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Heptagramma","spellextensions":{"0":["Zauberdauer modifizierbar ","(FW 8, 2 AP): Die Zauberdauer ist modifizierbar."],"1":["Zwei Geister ","(FW 12, 4 AP): Der Zauber kann gegen zwei Geister gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit beider Geister zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Drei Geister ","(FW 16, 6 AP): Der Zauber kann gegen drei Geister gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit der Geister zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 4 AsP pro Geist nach dem ersten. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Herr_%C3%BCber_das_Tierreich.html","properties":{"Wirkungsdauer":"QS Tage","Merkmal":"Einfluss","Zielkategorie":"Tiere (domestizierte Tiere oder Wildtiere der Größenkategorie groß oder kleiner)","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Druide kann mit diesem Zauberspruch ein Tier der Größenkategorie groß und kleiner beherrschen. Es hört auf jeden Befehl des Druiden und ist auch bereit, für ihn bis zum Tod zu kämpfen. Allerdings muss das Tier auch in der Lage sein, den Befehl auszuführen. Es muss ein Wesen der Kategorie domestiziertes Tier oder Wildtier sein (kein Ungeheuer). Auch Salz- oder Süßwassertiere kommen in Frage. Mit dem Zauberspruch kann stets nur ein einzelnes Wesen beherrscht werden. Solange die Wirkungsdauer anhält, kann der Druide also kein weiteres Tier durch den Zauberspruch beherrschen.","Publikation":"Die Streitenden Königreiche Seite 164","AsP-Kosten":"KL (t) des Tieres in AsP (Kosten nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Herr über das Tierreich","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hexagramma.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"Elementare","Reichweite":"8 Schritt","Probe":"MU/KL/CH","Wirkung":"Ein Elementargeist wird gebannt, wenn der Zauberer mehr QS erzielt, als die Anrufungsschwierigkeit beträgt (Anrufungsschwierigkeiten von 0 oder höher zählen als 0). Es kann sich dabei sowohl um einen beschworenen als auch um einen freien Elementargeist handeln. Ebenso ist es möglich, Elementargeister zu bannen, die an Objekte oder Wesen gebunden bzw. in diese eingefahren sind. Bei der Bannung wird er in das Hexagramm gezogen und wechselt in die Sphäre der Elemente über. Er kann jedoch nach Ablauf von 24 Stunden in die Dritte Sphäre zurückkehren.","Publikation":null,"AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen (Zauberdauer ist nicht modifizierbar)","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Hexagramma","spellextensions":{"0":["Zauberdauer modifizierbar ","(FW 8, 3 AP): Die Zauberdauer ist modifizierbar."],"1":["Zwei Elementare ","(FW 12, 6 AP): Der Zauber kann gegen zwei Elementare gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit beider Elementare zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 8 AsP."],"2":["Drei Elementare ","(FW 16, 9 AP): Der Zauber kann gegen drei Elementare gleichzeitig eingesetzt werden. Allerdings darf die Anrufungsschwierigkeit der Elementare zusammen die QS nicht übersteigen. Es entstehen zusätzliche Kosten in Höhe von 8 AsP pro Elementar nach dem ersten. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Heilungsbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/CH/FF","Wirkung":"Ein Zauber mit dem Merkmal Heilung wird aufgehoben, wenn dessen QS nicht höher ist als die QS des Heilungsbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt (z. B. einmalige Heilung). Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche wirkt er nicht. Wird der Heilungsbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":"Aventurische Magie 130","AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Heilungsbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Hellsichtbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Ein Zauber des Merkmals Hellsicht wird aufgehoben, wenn dessen QS nicht höher ist als die QS des Hellsichtbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt. Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche hilft er nicht. Wird der Hellsichtbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":"Aventurische Magie 130","AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden, Gildenmagier, Hexen, Scharlatane"},"spellclass":"Zauberspruch","name":"Hellsichtbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_GrosseVerwirrung.html","properties":{"Wirkungsdauer":"QS x 3 KR","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"MU/KL/CH (modifiziert um SK)","Wirkung":"Das Opfer dieses Druidenzaubers erleidet je nach QS unterschiedliche Stufen Verwirrung:<br/>QS 1: 1 Stufe Verwirrung, aber nur für 2 KR<br/>QS 2: 1 Stufe Verwirrung<br/>QS 3: 2 Stufen Verwirrung<br/>QS 4: 3 Stufen Verwirrung<br/>QS 5: 4 Stufen Verwirrung<br/>QS 6: 4 Stufen Verwirrung, aber für die doppelte Wirkungsdauer","Publikation":"Die Streitenden Königreiche Seite 164","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Große Verwirrung","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Harmlose_Gestalt.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/CH","Wirkung":"Der Zaubernde nimmt die Gestalt einer unauffälligen Person wie eines Dieners oder eines Bettlers an, die an dem entsprechenden Ort nicht auffällt. Dies umfasst sowohl das Aussehen als auch die passende Stimme, jedoch nicht Kenntnisse wie Sprachen oder angemessenes Verhalten. Größere Gegenstände und Vertrautentiere werden nicht mit dem Zauberspruch verborgen. Der Zauberspruch wirkt intuitiv, daher kann die Hexe nicht explizit festlegen, welches Aussehen sie annimmt. Mehr zu Illusionen und wie man sie durchschaut siehe Regelwerk Seite 259.","Publikation":"Regelwerk Seite 292","AsP-Kosten":"8 AsP (Aktivierung) + 4 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Harmlose Gestalt","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_GrosseGier.html","properties":{"Wirkungsdauer":"QS x 15 Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"Berührung (Reichweite ist nicht modifizierbar)","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Verzauberte verspürt eine unbändige Gier nach einem Objekt oder einer Handlung, die der Zaubernde ihm nennt. Er wird alles ihm Mögliche tun, um dieses Ziel zu erreichen, solange er Leib und Leben damit nicht gefährdet. Er erhält eine Schlechte Eigenschaft Gier nach … für die Wirkungsdauer des Zauberspruchs.","Publikation":"Regelwerk Seite 291","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Hexen"},"spellclass":"Zauberspruch","name":"Große Gier","spellextensions":{"0":["Gelungene Beeinflussung 1 ","(FW 8, 2 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "],"1":["Gelungene Beeinflussung 2 ","(FW 12, 4 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "],"2":["Gelungene Beeinflussung 3 ","(FW 16, 6 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Gletscherform.html","properties":{"Wirkungsdauer":"QS x 10 Minuten","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"KL/FF/KK","Wirkung":"Innerhalb eines Radius’ von QS Schritt vermag der Zauberer alle Materie des Elements Eis zu verformen, solange sie nicht Teil eines Wesens ist. Auf diesem Wege können beispielsweise Eisbrocken zu einer Wand zusammengefügt oder eine Behausung aus gepresstem Schnee errichtet werden. Der Prozess geht dabei nur langsam vonstatten. Soll das Material in eine bestimmte Richtung geformt werden, dauert dies 1 KR pro Spann. Die Erschaffung von besonderen Objekten wie Kunstwerken oder nutzbaren Gegenständen erfordert es sogar, ein Drittel der normalen Herstellungszeit aufzuwenden. In diesem Fall ist es außerdem nicht möglich, an mehr als einem Objekt gleichzeitig zu arbeiten oder parallel andere Verformungen durchzuführen. Für die Ermittlung der Qualität solcher Objekte wird QS/2 des Zaubers als QS des entsprechenden Handwerkstalents herangezogen. Eine Änderung der Materialart oder der Materialqualität ist mit dem Ritual nicht möglich. Am Ende der Wirkungsdauer ist das Verformte wieder den normalen Gesetzen der Natur ausgeliefert.","Ritualdauer":"5 Minuten","Publikation":"Klingen der Nacht Seite 109","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Verbreitung":"Nachtalben u. a."},"spellclass":"Ritual","name":"Gletscherform","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Haselbusch.html","properties":{"Wirkungsdauer":"1 Tag","Merkmal":"Elementar","Zielkategorie":"Pflanze","Reichweite":"2 Schritt","Probe":"IN/CH/FF","Wirkung":"Der Zauber regt das Wachstum von Pflanzen an. Der Zauberer muss eine Pflanze oder Pflanzensamen an dem Objekt, das er erklettern will, ablegen und warten. Am Ende der Zauberdauer wächst die Pflanze pro KR 1 Schritt (bis maximal QS x 3 Schritt) hoch und erleichtert <em>Klettern</em>-Proben an der ausgewählten Stelle um QS/2 Punkte.","Publikation":"Aventurische Magie 130","AsP-Kosten":"4 AsP","Steigerungsfaktor":"B","Zauberdauer":"16 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Haselbusch","spellextensions":{"0":["Formwuchs ","(FW 8, 2 AP): Die Pflanze kann während des Wachsens im Rahmen ihrer Möglichkeiten eine vom Zauberer vorgegebene Form annehmen."],"1":["Längere Wirkungsdauer ","(FW 12, 4 AP): Die Wirkungsdauer hält 1 Woche an."],"2":["Viel längere Wirkungsdauer ","(FW 16, 6 AP): Die Wirkungsdauer hält 1 Monat an. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Gefunden.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Zielkategorie":"Objekte","Reichweite":"selbst","Probe":"KL/IN/GE","Wirkung":"Der Zauberer kann die Richtung bestimmen, in der sich ein Objekt befindet. Er muss dieses insgesamt mindestens einen Monat lang bei sich getragen haben oder über eine magische Verbindung zu ihm verfügen. Ist das Objekt weiter als QS x 3 Schritt von ihm entfernt, scheitert der Zauber.","Publikation":"Aventurische Magie 129","AsP-Kosten":"2 AsP","Steigerungsfaktor":"A","Zauberdauer":"8 Aktionen","Verbreitung":"Hexen, Scharlatane"},"spellclass":"Zauberspruch","name":"Gefunden","spellextensions":{"0":["Höhere Reichweite 1 ","(FW 8, 1 AP): Das gesuchte Objekt darf bis zu QS x 5 Schritt entfernt sein."],"1":["Höhere Reichweite 2 ","(FW 12, 2 AP): Das gesuchte Objekt darf bis zu QS x 10 Schritt entfernt sein. Voraussetzung: Zaubererweiterung "],"2":["Höhere Reichweite 3 ","(FW 16, 3 AP): Das gesuchte Objekt darf bis zu QS x 50 Schritt entfernt sein. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Gedankenbilder.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"4 Schritt","Probe":"IN/CH/KO","Wirkung":"Das Ziel vernimmt in seinem Geist vom Zauberer gewählte Bilder, beispielsweise dessen Sinneseindrücke, eine Warnung vor drohender Gefahr oder den Plan für ein gemeinsames Vorgehen. Dem Empfänger ist bewusst, dass ihm diese Bilder eingegeben werden. Zudem kann er auf diesem Wege nicht unfreiwillig abgelenkt werden. Das Ziel des Zaubers und der Zaubernde können sich nach Ende der Zauberdauer auch beliebig weit voneinander fortbewegen. Wichtig ist nur, dass der Zaubernde weiterhin Sichtkontakt zur Zielperson halten kann.","Publikation":"Aventurische Magie 129","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Gedankenbilder","spellextensions":{"0":["Kurze Rücksprache ","(FW 8, 1 AP): Das Ziel kann in Gedanken eine kurze Rückantwort in Form eines Bildes an den Zauberer senden."],"1":["Mehr Personen ","(FW 12, 2 AP): Der Zauberer kann bis zu QS Personen verzaubern. Voraussetzung: Zaubererweiterung "],"2":["Kommunikationsverbindung ","(FW 16, 3 AP): Alle Ziele können in Gedanken mit dem Zauberer Bilder austauschen, aber nicht untereinander. Es entstehen einmalig zusätzliche Kosten in Höhe von 4 AsP. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Gardianium.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Antimagie","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Der Zauberspruch erschafft eine unsichtbare Kuppel um den Magier herum, die von außen kommende magische Angriffe absorbiert. Die Kuppel ist eigentlich eine Kugel, da jedoch der Magier meistens auf dem Boden steht, ist die Kugelform nicht so wichtig. Der Radius der Kuppel beträgt 3 Schritt, ihre Schildstärke die investierten AsP + QS x 3. Sie kann ihre Schildstärke in Trefferpunkten an Schaden absorbieren, die von Schaden verursachenden Zaubern erzeugt werden, hält jedoch weder magische Wesen noch magische Waffen ab. Fällt die Schildstärke unter 1, bricht die Kuppel zusammen. Schaden, der nicht mehr absorbiert werden kann, richtet Schaden an. Die Kuppel bewegt sich stets mit dem Zaubernden. Geschützt durch die Wirkung sind auch alle, die innerhalb der Kuppel stehen.","Publikation":"Regelwerk Seite 291","AsP-Kosten":"mindestens 4 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Gardianum","spellextensions":{"0":["Stärkerer Schild ","(FW 8, 2 AP): Die Schildstärke beträgt AsP + (QS x 4)."],"1":["Großer Radius ","(FW 12, 4 AP): Der Radius der Kuppel beträgt 5 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Reichweite Berührung ","(FW 16, 6 AP): Als Reichweite gilt Berührung. Die Schildstärke beträgt jedoch nur AsP + (QS x 2). Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Fortifex.html","properties":{"Wirkungsdauer":"30 Kampfrunden","Merkmal":"Telekinese","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"MU/IN/KO","Wirkung":"Durch den Fortifex entsteht eine durchscheinende, allerdings leicht flimmernde (und damit sichtbare)Wand, die für physische Objekte undurchdringlich ist. Die Wand kann beispielsweise Pfeile, Bolzen, einen Ignifaxius oder Lebewesen aufhalten. Sie schützt nicht vor Hitze, Kälte, Licht, Gasen, körperlosen Wesen oder Zaubern und Liturgien, die keine physische Komponente aufweisen. Der Zauberer muss einen Mittelpunkt innerhalb der Reichweite des Zauberspruchs benennen. Von dort aus wirkt gradlinig eine Barriere, die QS x 2 Schritt lang ist. Den exakten Verlauf durch den Mittelpunkt kann der Zauberer festlegen. Die Höhe der Barriere beträgt 3 Schritt. Eine Tiefe besitzt die Wand nicht. Man kann sie nur aufgerichtet platzieren, nicht flach hinlegen oder schräg stellen. Die Wand besitzt 100 Strukturpunkte. Sollten die Strukturpunkte auf 0 oder darunter sinken, löst sie sich auf.","Publikation":"Die Streitenden Königreiche Seite 164","AsP-Kosten":"16 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Fortifex","spellextensions":{"0":["Höhere Wand ","(FW 8, 2 AP): Die Wand ist 5 Schritt hoch."],"1":["Zylinder ","(FW 12, 4 AP): Die Wand ist gewölbt und verläuft um den Zaubernden herum. Die Höhe beträgt 3 Schritt, der Radius QS Schritt."],"2":["Deckel ","(FW 16, 6 AP): Die Wand verläuft wie bei der Zaubererweiterung Zylinder, nur dass dieser nach oben hin geschlossen ist. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Foramen.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Objekt","Zielkategorie":"Objekte (Schlösser)","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Durch diesen Zauberspruch öffnen sich einfache Schlösser automatisch, bei komplizierten Schlössern wird es leichter, sie zu öffnen. Pro QS werden Proben auf Schlösserknacken (Bartschlösser oder Kombinationsschlösser) und Kraftakt (Eintreten &amp; Zertrümmern) gegen das Schloss um 1 erleichtert. Sollte der Gesamtmodifikator zum Knacken eines Schlosses dadurch zu einer Erleichterung werden, springt das Schloss augenblicklich auf. Die Erleichterung gilt binnen der Wirkungsdauer sowohl für den Zauberer als auch für andere Personen.","Publikation":"Die Streitenden Königreiche Seite 163","AsP-Kosten":"8 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Foramen","spellextensions":{"0":["Wiederverschließbar ","(FW 8, 3 AP): Wenn es nicht beschädigt wurde, verschließt sich das Schloss nach Ablauf der Wirkungsdauer wieder von selbst."],"1":["Fallengespür ","(FW 12, 6 AP): Der Zauberer kann spüren, ob ein Schloss durch eine Falle gesichert ist. Er kann die Falle aber nicht durch den Zauber entschärfen."],"2":["Die Priesterkaiser kommen! ","(FW 16, 9 AP): Der Zauberer kann alle Schlösser innerhalb der Wirkungsdauer öffnen oder Proben wie bei der Zauberwirkung beschrieben erleichtern. Dazu muss er die Schlösser berühren und pro Schloss 1 AsP zusätzlich zahlen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Fulminictus.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"KL/IN/KO (modifiziert um ZK)","Wirkung":"Der Verzauberte erleidet Schaden, der auf seine Aura einwirkt und eine Reihe kleiner innerer Verletzungen erzeugt. Er erleidet 2W6+(QS x 2) Trefferpunkte. Rüstungen schützen nicht vor diesem Zauber. Der Zauberspruch trifft sein Ziel automatisch. Der Gegner kann weder ausweichen noch parieren.","Publikation":"Regelwerk Seite 291","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"1 Aktion","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Fulminictus","spellextensions":{"0":["Verteilter Schaden 1 ","(FW 8, 3 AP): Der Zauberer wählt 2 Ziele in Reichweite aus. Der Schaden kann beliebig auf beide Ziele verteilt werden."],"1":["Verteilter Schaden 2 ","(FW 12, 6 AP): Der Zauberer wählt 3 Ziele in Reichweite aus. Der Schaden kann beliebig zwischen den Zielen verteilt werden. Voraussetzung: Zaubererweiterung "],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Die AsP-Kosten des Zaubers verdoppeln sich."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Flim_Flam.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"8 Schritt","Probe":"MU/KL/CH","Wirkung":"Der Zaubernde erzeugt eine kleine, schwebende Kugel aus Licht. Sie ist entweder stationär oder folgt dem Zaubernden. Dies muss er vor dem Zaubern festlegen, ebenso wie die Farbe des Lichtes. Die maximal erzeugbare Helligkeit variiert je nach QS. Der Zaubernde kann die Helligkeit jedoch nach Wunsch einmalig verringern. Die Zone des Lichts variiert je nach Helligkeit.<br/>QS 1: Das Licht hat die Helligkeit einer Kerze.<br/>QS 2: Das Licht hat die Helligkeit einer Fackel.<br/>QS 3: Das Licht hat die Helligkeit eines Lagerfeuers.<br/>QS 4: Die Helligkeit des Lichts reicht aus, um einen Raum von 5 mal 5 Schritt auszuleuchten.<br/>QS 5: Die Helligkeit des Lichts reicht aus, um einen großen Saal auszuleuchten.<br/>QS 6: Das Licht ist blendend hell. Es ist kaum möglich, direkt hineinzusehen.","Publikation":"Regelwerk Seite 291","AsP-Kosten":"2 AsP (Aktivierung des Zaubers) + 1 AsP pro Stunde","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Flim Flam","spellextensions":{"0":["Lichtregulation ","(FW 8, 1 AP): Der Zauberer kann die Flim Flam-Kugel innerhalb der Wirkungsdauer nach Belieben heller oder dunkler werden lassen (bis zu einem Maximum der erzielten QS)."],"1":["Keine Aufrechterhaltung ","(FW 12, 2 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Verankern ","(FW 16, 3 AP): Der Flim Flam kann an einer anderen Person verankert werden, sodass sich das Licht in bis zu 8 Schritt Entfernung zu dieser Person mitbewegt. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Favilludo.html","properties":{"Wirkungsdauer":"QS Kampfrunden","Merkmal":"Elementar","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"IN/FF/CH","Wirkung":"Das Ziel des Zaubers wird von einem Schwarm tanzender Funken umgeben. Ihre Farbe kann dabei beliebig gewählt werden. Alle Angriffe gegen den Zauberer sind um 1 erschwert.","Publikation":"Aventurische Magie 128","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Favilludo","spellextensions":{"0":["Kerzenschein ","(FW 8, 1 AP): Der Schein ist so hell wie eine Kerze, sodass der Zauberer als Lichtquelle zählt."],"1":["Heiße Aura ","(FW 12, 2 AP): Der Schein verursacht bei Gegnern, die Nahkampfangriffe gegen den Verzauberten richten, bei 1 auf 1W6 jeweils 1 SP."],"2":["Reichweite Berührung ","(FW 16, 3 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Firnlauf.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Elementar","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/KL/GE","Wirkung":"Um den Zauberer herum entsteht eine QS Schritt messende Zone, in der allen Wesen eine problemlose Überquerung von Oberflächen des Elements Eis möglich ist, seien es Schneefelder oder hauchdünn gefrorene Wasserflächen. Ihre GS wird durch die Beschaffenheit des Untergrunds nicht vermindert und sie werden weder einsinken, einbrechen noch ausrutschen. Zudem erleiden sie keinen Schaden durch Kälte oder natürliches Eis. Die Wirkung erstreckt sich nicht auf größere Objekte wie Fahrzeuge. ","Publikation":"Klingen der Nacht Seite 108","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Firnlauf","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Fischflosse.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/IN/GE","Wirkung":"Der Zauberer verwandelt sich in ein Wasserlebewesen seiner Wahl, das jedoch maximal die Größenkategorie klein aufweisen darf. Während der Wirkungsdauer verwendet der Zauberer die körperlichen Werte des Tieres, einschließlich seiner Eigenschaften, Talente und Kampfwerte. Eine niedrige LE wird bei der Verwandlung anteilsmäßig angerechnet, gleiches geschieht bei der Rückverwandlung. Die geistigen Werte des Zauberers bleiben ebenso wie sein Bewusstsein erhalten. Die Verwandlung betrifft nur den Zauberer selbst, nicht seine Kleidung oder Ausrüstung. Es ist keine Verwandlung in ein kulturschaffendes Wesen möglich.","Publikation":"Aventurische Magie 128","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro Stunde","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Fischflosse","spellextensions":{"0":["Mittlere Tiere ","(FW 8, 3 AP): Der Zauberer kann sich auch in Wasserlebewesen der Größenkategorie mittel verwandeln. Es entstehen zusätzliche Kosten in Höhe von 5 AsP."],"1":["Keine Aufrechterhaltung ","(FW 12, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Große Tiere ","(FW 16, 9 AP): Der Zauberer kann sich auch in Wasserlebewesen der Größenkategorie groß verwandeln. Es entstehen zusätzliche Kosten in Höhe von 6 AsP. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Exposami.html","properties":{"Wirkungsdauer":"10 Kampfrunden","Merkmal":"Hellsicht","Zielkategorie":"Lebewesen (außer Pflanzen, Pilze und Wesen, die kleiner als eine Ratte sind)","Reichweite":"QS x 20 Schritt","Probe":"KL/IN/CH","Wirkung":"Der Zauberer kann alle Lebewesen innerhalb der Reichweite des Zauberspruchs spüren, sofern sie mindestens so groß sind wie eine Ratte. Die Auren nimmt der Zauberer als grün-leuchtende Flecken wahr. Er ist jedoch nicht darauf angewiesen diese zu sehen, um Lebewesen zu spüren. Pflanzen, Pilze und Lebewesen, die kleiner als eine Ratte sind, können durch den Zauberspruch nicht erkannt werden. Bauwerke aus den Elementen Erz oder Eis stören die Wirkung des Spruches. Hinter einer Mauer aus Stein oder in einem Iglu kann der Zauberer also keine Lebewesen orten. Der Zauberer kann erkennen, um was für einen Typus von Lebewesen es sich handelt (siehe Regelwerk Seite 355).","Publikation":"Die Streitenden Königreiche Seite 163","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Elfen"},"spellclass":"Zauberspruch","name":"Exposami","spellextensions":{"0":["Durch Erz ","(FW 8, 1 AP): Der Zauber wirkt auch durch Erz bis zu einer maximalen Dicke von 1 Schritt hindurch."],"1":["Durch Eis ","(FW 12, 2 AP): Der Zauber wirkt auch durch Eis bis zu einer maximalen Dicke von 1 Schritt hindurch. Voraussetzung: Zaubererweiterung "],"2":["Spezielles Lebewesen ","(FW 16, 3 AP): Der Zauberer kann eine spezielle Art von Lebewesen benennen und mittels des Zaubers suchen (z. B. Hunde, Menschen, Pferde)."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Erinnerungverlassedich.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/IN/CH (modifiziert um die SK)","Wirkung":"Der Zauber unterdrückt alle persönlichen Erinnerungen des Opfers an die letzten QS Minuten. Für die Wirkungsdauer kann es sich an diesen Zeitraum weder erinnern noch Proben auf entsprechend erlangtes Wissen ablegen. Seine Reflexe sind davon jedoch ebenso wenig betroffen wie zuvor erlangtes Wissen und Fähigkeiten, weshalb auf diese uneingeschränkt geprobt werden darf. Allerdings ist das Ziel in den ersten 5 KR etwas desorientiert und erleidet bis zum Ende der 5. KR eine Stufe <em>Verwirrung</em>.","Publikation":"Aventurische Magie 127","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Erinnerung verlasse dich","spellextensions":{"0":["Längere Wirkungsdauer 1 ","(FW 8, 3 AP): Die Wirkungsdauer erhöht sich auf QS Stunden."],"1":["Längere Wirkungsdauer 2 ","(FW 12, 6 AP): Die Wirkungsdauer erhöht sich auf QS Tage. Voraussetzung: Zaubererweiterung "],"2":["Längere Wirkungsdauer 3 ","(FW 16, 9 AP): Die Wirkungsdauer erhöht sich auf QS Wochen. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/SZ_Falkenauge.html","properties":{"Wirkungsdauer":"Bis zum nächsten Schuss, maximal QS x 2 Kampfrunden","Merkmal":"Hellsicht","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"MU/KL/IN","Wirkung":"Der Verzauberte baut eine geistige Verbindung zu seinem Ziel auf. Hierdurch wird für den Verzauberten der nächste Fernkampfangriff auf dieses Ziel um QS des Zaubers erleichtert.","Publikation":"Regelwerk Seite 291","AsP-Kosten":"4 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Falkenauge","spellextensions":{"0":["Aufteilung ","(FW 8, 2 AP): Die QS können auf 2 Personen aufgeteilt werden. Beide Personen müssen berührt werden."],"1":["Langanhaltend ","(FW 12, 4 AP): Die Wirkungsdauer beträgt QS x 2 Minuten."],"2":["Zweiter Schuss ","(FW 16, 6 AP): Ein zweiter Schuss auf das gleiche Ziel ist um QS/2 erleichtert, solange er noch innerhalb der Wirkungsdauer erfolgt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Elfenstimme.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"4 Schritt","Probe":"IN/CH/KO","Wirkung":"Das Ziel vernimmt in seinem Geist vom Zauberer gewählte akustische Signale, beispielsweise gesprochene Worte oder eine Melodie. Dem Empfänger ist bewusst, dass es sich nicht um natürliche Geräusche handelt. Zudem kann er auf diesem Wege nicht unfreiwillig abgelenkt werden. Das Ziel des Zaubers und der Zaubernde können sich nach Ende der Zauberdauer auch beliebig weit voneinander fortbewegen. Wichtig ist nur, dass der Zaubernde weiterhin Sichtkontakt zur Zielperson halten kann.","Publikation":"Aventurische Magie 127","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Elfenstimme","spellextensions":{"0":["Kurze Rücksprache ","(FW 8, 1 AP): Das Ziel kann in Gedanken eine kurze Rückantwort an den Zauberer senden, z. B. ein kurzes Ja oder Nein."],"1":["Mehr Personen ","(FW 12, 2 AP): Der Zauberer kann bis zu QS Personen verzaubern. Voraussetzung: Zaubererweiterung "],"2":["Kommunikationsverbindung ","(FW 16, 3 AP): Alle Ziele können in Gedanken mit dem Zauberer kommunizieren, aber nicht untereinander. Es entstehen einmalig zusätzliche Kosten in Höhe von 4 AsP. Voraussetzung: Zaubererweiterung"]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Elementarbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"IN/CH/KO","Wirkung":"Ein Zauber des Merkmals Elementar wird aufgehoben, wenn dessen QS nicht höher als die QS des Elementarbanns ist. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt (z. B. einmaliger Schaden). Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche hilft er nicht. Wird der Elementarbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":"Aventurische Magie 126","AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Elementarbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Eisenrost.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"Profane Objekte","Reichweite":"4 Schritt","Probe":"KL/CH/GE","Wirkung":"Metallene Gegenstände verlieren 20 Strukturpunkte. Sinken die Strukturpunkte dadurch unter 1, zerfrisst der Rost das Objekt so stark, dass es nicht mehr verwendbar ist. Um metallene Rüstungen zu zerstören, benötigt der Zauberer so viele QS, wie die Rüstung Rüstungsschutz hat. Bei metallenen Waffen der Kampftechniken Dolche und Raufen muss 1 QS erzielt werden, bei andere Waffen mindestens 2 QS. Zweihandwaffen können nur mit mindestens 3 QS zerstört werden. Erzielte QS gegen Waffen summieren sich auf.","Publikation":"Aventurische Magie 126","AsP-Kosten":"8 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen (kann nicht modifiziert werden)","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Eisenrost","spellextensions":{"0":["Metallgegenstände ","(FW 8, 3 AP): Der Zauber richtet bei Metallgegenständen, nicht aber Waffen und Rüstungen, zusätzlich QS x 10 Strukturpunkte Schaden an."],"1":["Zauberdauer modifizierbar ","(FW 12, 6 AP): Die Zauberdauer des Zaubers darf doch modifiziert werden."],"2":["Ausgeweitete Zerstörung ","(FW 16, 9 AP): Auch weitere metallene Objekte in bis zu 2 Schritt Abstand vom eigentlichen Ziel sind betroffen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_EigeneAengste.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"4 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Alle Fertigkeitsproben, die unter anderem auf Mut gewürfelt werden, sind um die QS erschwert. ","Publikation":"Klingen der Nacht Seite 108","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Eigne Ängste","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Einflussbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Ein Zauber des Merkmals Einfluss wird aufgehoben, solange dessen QS nicht höher ist als die QS des Einflussbanns. Es ist nicht möglich, den Bann auf die Auswirkungen eines Zaubers anzuwenden, dessen Wirkungsdauer bereits abgelaufen ist oder sofortiger Natur war. Ebenso können keine Zauber mit der Wirkungsdauer permanent gebannt werden. Handelt es sich um einen Zauber der Zielkategorie Zone, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":"Aventurische Magie 126","AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden, Gildenmagier, Hexen, Scharlatane"},"spellclass":"Zauberspruch","name":"Einflussbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Ecliptifactus.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Dämonisch","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"MU/IN/CH","Wirkung":"Der Schatten des Zauberers wird durch diesen Zauberspruch belebt und kämpft für ihn. Mit dem Ecliptifactus kann der Magier immer nur einen Schatten heraufbeschwören. Erst nach Ablauf der Wirkungsdauer kann er den Zauber erneut wirken. Der Schatten besitzt die unten aufgeführten Werte. Zusätzlich erhält der Schatten folgende Boni:<br/>QS 1: keine Änderungen<br/>QS 2: +1 AT, +5 LeP, +1 SK<br/>QS 3: +1 AT, +1 PA, +1 AW, +1 TP, +5 LeP, +1 SK, Finte I<br/>QS 4: +2 AT, +1 PA, +1 AW, +1 TP, +1 RS, +10 LeP, +1 SK, Finte I<br/>QS 5: +2 AT, +2 PA, +2 AW, +1 TP, +1 RS, +10 LeP, +2 SK, Finte I<br/>QS 6: +3 AT, +2 PA, +2 AW, +2 TP, +1 RS, +15 LeP, +2 SK, Finte I+II<br/>Der Schatten ist außer durch Zauber mit dem Merkmal Antimagie nicht durch Magie beeinflussbar und erleidet keine Zustände. Sinken seine LeP auf 0, so verliert der Zaubernde schlagartig seine gesamte Astralenergie, davon 2 AsP permanent. Es dauert 7 Wochen, bis der Schatten sich regeneriert hat. Bis dahin besitzt der Zaubernde keinen Schatten und kann den Zauberspruch entsprechend auch nicht anwenden. Der fehlende Schatten wird in vielen Kulturen, besonders in den Ländern der Zwölfgöttergläubigen, als ein Stigma betrachtet. Ein fehlender Schatten wird schnell als ein Anzeichen gesehen, dass man es mit einem Dämonenbündler oder gar einem Geweihten des Namenlosen zu tun hat. Der Held bekommt für die Zeit ohne Schatten den Nachteil Stigma (siehe Regelwerk Seite 178).","Publikation":"Die Streitenden Königreiche Seite 162","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro Kampfrunde","Steigerungsfaktor":"C","Schatten":"<ul><li><strong>MU</strong> 15 <strong>KL</strong> 12 <strong>IN</strong> 13 <strong>CH</strong> 12</li><li><strong>FF</strong> 12 <strong>GE</strong> 13 <strong>KO</strong> 13 <strong>KK</strong> 14</li><li><strong>LeP</strong> 25 <strong>AsP</strong> – <strong>KaP</strong> – <strong>INI</strong> 14+1W6</li><li><strong>AW</strong> 7 <strong>SK</strong> 3 <strong>ZK</strong> 1 <strong>GS</strong> 8</li><li><strong>Angriff*: AT</strong> 13 <strong>PA</strong> 8 <strong>TP</strong> 1W6+3 <strong>RW</strong> mittel</li><li><strong>RS/BE</strong> 2/0</li><li><strong>Aktionen:</strong> 1</li><li><strong>Sonderfertigkeiten:</strong> keine</li><li><strong>Talente:</strong> Klettern 6, Körperbeherrschung 7, Kraftakt 7, Selbstbeherrschung 10, Sinnesschärfe 7, Verbergen 12, Einschüchtern 10, Willenskraft 7</li><li><strong>Größenkategorie:</strong> mittel</li><li><strong>Typus:</strong> übernatürliches Wesen, humanoid</li><li><strong>Kampfverhalten:</strong> Der Schatten verteidigt den Zaubernden, so gut er kann und geht, wenn dieser nicht direkt angegriffen wird, auch von sich aus zum Angriff auf potentielle Feinde über oder folgt dem Zauberer. Er führt jedoch keine Befehle des Zaubernden aus. Der Schatten versucht sich immer in einem Radius von 13 Schritten um den Zauberer herum aufzuhalten. Wird er durch irgendwelche Handlungen aus diesem Bereich gedrängt, versucht er den Radius wieder zu erreichen.</li><li><strong>Flucht:</strong> Der Schatten flieht nie.)<li></ul> Unabhängig von der geführten Waffe des Schattens, gelten die hier angegebenen Werte (plus eventuelle Boni aus erzielten QS).","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Ecliptifactus","spellextensions":{"0":["Weite Entfernung ","(FW 8, 3 AP): Der Schatten kann sich bis zu 20 Schritt weit entfernen."],"1":["Keine Aufrechterhaltung ","(FW 12, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt"],"2":["Verbesserter Schatten ","(FW 16, 9 AP): Alle Eigenschaften des Schattens steigen um 1."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Disruptivo.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Antimagie","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Dieser Grundzauber der Antimagie beeinträchtigt alle Arten von Zauberei in QS x 2 Schritt Radius um den Zaubernden herum, indem störende Schwingungen in die umgebenden astralen Muster gesendet werden. Alle Proben auf Zaubersprüche, die in dieser Zone gewirkt werden oder dort ihre Wirkung entfalten sollen, sind erschwert. Dies gilt ebenso für Zauber, die der Zaubernde selbst wirkt. Der Zaubernde muss vor dem Wirken festlegen, ob die Zone sich mit ihm mit bewegen oder an Ort und Stelle bleiben soll.<br/>QS 1: Probenerschwernis –1<br/>QS 2: Probenerschwernis –2<br/>QS 3: Probenerschwernis –3<br/>QS 4: Probenerschwernis –4<br/>QS 5: Probenerschwernis –5<br/>QS 6: Probenerschwernis –6","Publikation":"Regelwerk Seite 289","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Disruptivo","spellextensions":{"0":["Großer Radius ","(FW 8, 2 AP): Der Radius beträgt QS x 3 Schritt."],"1":["Reichweite Berührung ","(FW 12, 4 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"2":["Unbeeindruckter Zauberwirker ","(FW 16, 6 AP): Der Zauberer selbst erleidet beim Zaubern innerhalb der von ihm erschaffenen Zone nur die halbe Erschwernis."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Duplicatus.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Illusion","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/CH","Wirkung":"Der Zaubernde erzeugt einen oder mehrere illusionäre Doppelgänger des Ziels, die sich synchron zu ihm bewegen und ständig mit ihm verschmelzen und sich wieder von ihm trennen. Je nach QS erscheinen mehrere Doppelgänger. Der Zaubernde kann sich entscheiden, weniger Doppelgänger als maximal möglich erscheinen zu lassen:<br/>QS 1: 1 Doppelgänger, aber nur 2 Kampfrunden<br/>QS 2: 1 Doppelgänger<br/>QS 3: 2 Doppelgänger<br/>QS 4: 3 Doppelgänger<br/>QS 5: 4 Doppelgänger<br/>QS 6: 4 Doppelgänger, aber doppelte Wirkungsdauer<br/>Da die Doppelgänger nicht vom Zaubernden zu unterscheiden sind, haben Angreifer mit Nah- und Fernkampfangriffen oder Zaubern eine entsprechende Chance, einen der Doppelgänger statt des Zaubernden zu treffen. Sollte der Zaubernde statt eines Doppelgängers getroffen werden, muss er den Angriff parieren oder ihm ausweichen. Flächenangriffe sind hiervon nicht betroffen. Mehr zu den Illusionen und wie man sie durchschaut siehe Seite 259.","Publikation":"Regelwerk Seite 290","AsP-Kosten":"4 AsP pro Doppelgänger (bei Misslingen entsprechend 2 AsP)","Steigerungsfaktor":"C","Trefferchancen":"Zahl der Doppelgänger Chance<br/>1 Doppelgänger 50 % (1-10 auf W20)<br/>2 Doppelgänger 33 % (1-7 auf W20)<br/>3 Doppelgänger 25 % (1-5 auf W20)<br/>4 Doppelgänger 20 % (1-4 auf W20)","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Duplicatus","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 3 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 6 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 9 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Dunkelheit.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Um den Zaubernden herum bildet sich eine Kugel aus Dunkelheit mit einem Durchmesser von QS x 2 in Schritt. Pro QS erschweren sich die Sichtverhältnisse um eine Stufe. Weder natürliche noch übernatürliche Lichtquellen können diese Dunkelheit erhellen. Für den Zaubernden werden die Sichtverhältnisse durch den Zauber nicht erschwert, bestehende Sichtmodifikatoren gelten allerdings weiterhin. Der Zaubernde muss vor dem Wirken des Zaubers entscheiden, ob die Zone der Dunkelheit an Ort und Stelle verbleiben oder sich mit ihm als Zentrum bewegen soll.","Publikation":"Die Streitenden Königreiche Seite 162","AsP-Kosten":"16 AsP (Aktivierung des Zaubers) + 8 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Dunkelheit","spellextensions":{"0":["Großer Radius ","(FW 8, 2 AP): Der Radius beträgt QS x 3 Schritt."],"1":["Angsteinflößende Dunkelheit ","(FW 12, 4 AP): Die Dunkelheit ist so furchteinflößend, dass alle Lebewesen in der Zone eine Probe auf "],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Desintegratus.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"Profane Objekte","Reichweite":"8 Schritt","Probe":"KL/CH/KK","Wirkung":"Das verzauberte Objekt verliert QS x 10 Strukturpunkte. Sinken die Strukturpunkte dadurch unter 1, zerfällt das Objekt zu Staub. Um Rüstungen zu zerstören, benötigt der Zauberer so viele QS, wie die Rüstung Rüstungsschutz hat. Bei Waffen der Kampftechniken Dolche und Raufen muss 1 QS erzielt werden, bei andere Waffen mindestens 2 QS. Zweihandwaffe können nur mit mindestens 3 QS zerstört werden. Erzielte QS gegen Waffen summieren sich auf.","Publikation":"Aventurische Magie 124","AsP-Kosten":"16 AsP","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Desintegratus","spellextensions":{"0":["Höhere Zerstörungskraft 1 ","(FW 8, 2 AP): Der Schaden steigt auf QS x 20 Strukturpunkte."],"1":["Höhere Zerstörungskraft 2 ","(FW 12, 4 AP): Der Schaden steigt auf QS x 30 Strukturpunkte. Voraussetzung: Zaubererweiterung "],"2":["Ausgeweitete Zerstörung ","(FW 16, 6 AP): Auch kleinere Gegenstände in bis zu 2 Schritt Abstand vom eigentlichen Ziel erleiden Schaden, z. B. Stühle in der Nähe eines betroffenen Tisches, nicht aber einzelne Steine einer Mauer. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Debilitatio.html","properties":{"Wirkungsdauer":"QS x 3 Kampfrunden","Merkmal":"Sphären","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Der Spruch erschafft eine unbewegliche kugelförmige Zone, die darin befindlichen Dämonen und Elementaren eine Erschwernis in Höhe der QS/2 auf alle Proben einbringt. Der Zauberer muss einen Mittelpunkt innerhalb der Reichweite des Zauberspruchs benennen, um den herum sich die Zone mit einem Radius von QS x 2 Schritt ausbreitet.","Publikation":"Aventurische Magie 124","AsP-Kosten":"16 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Debilitatio","spellextensions":{"0":["Geisterzone ","(FW 8, 2 AP): Der Zauber wirkt auch gegen Geister."],"1":["Feenzone ","(FW 12, 4 AP): Der Zauber wirkt auch gegen Feenwesen. Voraussetzung: Zaubererweiterung "],"2":["Durchdringung ","(FW 16, 6 AP): Die betroffenen Wesen erhalten pro KR in der Zone 1 SP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Daemonenbann.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Antimagie","Zielkategorie":"alle","Reichweite":"8 Schritt","Probe":"MU/CH/KO","Wirkung":"Ein Zauber des Merkmals Dämonisch wird aufgehoben, solange dessen QS nicht höher ist als die QS des Dämonenbanns. Der Bann entfaltet keine Wirkung gegen Zauber, deren Wirkung sofort eintritt (z. B. einmaliger Schaden). Auch gegen Zauber, deren Wirkung bereits vollendet ist, oder permanent wirkende Sprüche hilft er nicht. Wird der Dämonenbann gegen einen Zauber der Zielkategorie Zone eingesetzt, genügt es, wenn sich ein Teil von dieser innerhalb der Reichweite befindet.","Publikation":"Aventurische Magie 123","AsP-Kosten":"8 AsP bzw. 16 AsP für Zauber mit Zielkategorie Zone","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Dämonenbann","spellextensions":{"0":["Reichweite erhöhen ","(FW 8, 2 AP): Die Zaubermodifikation Reichweite erhöhen ist nicht erschwert."],"1":["Zauberdauer senken ","(FW 12, 4 AP): Die Zaubermodifikation Zauberdauer senken ist nicht erschwert."],"2":["Kosten senken ","(FW 16, 6 AP): Die Zaubermodifikation Kosten senken ist nicht erschwert."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Claudibus.html","properties":{"Wirkungsdauer":"30 Minuten","Merkmal":"Objekt","Zielkategorie":"Objekte (Schlösser)","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Durch diesen Zauberspruch verschließen sich Schlösser automatisch und es wird schwerer, sie wieder zu öffnen oder zu knacken. Pro QS werden Proben auf Schlösserknacken (Bartschlösser oder Kombinationsschlösser) und Kraftakt (Eintreten &amp; Zertrümmern) gegen das Schloss um 1 erschwert.","Publikation":"Die Streitenden Königreiche Seite 162","AsP-Kosten":"4 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Claudibus","spellextensions":{"0":["Längere Wirkungsdauer 1 (","FW 8, 2 AP): Die Wirkungsdauer erhöht sich auf 2 Stunden."],"1":["Längere Wirkungsdauer 2 (","FW 12, 4 AP): Die Wirkungsdauer erhöht sich auf 8 Stunden. Voraussetzung: Zaubererweiterung "],"2":["Längere Wirkungsdauer 3 (","FW 16, 6 AP): Die Wirkungsdauer erhöht sich auf 24 Stunden. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Chamaelioni.html","properties":{"Wirkungsdauer":"QS Minuten","Merkmal":"Illusion","Zielkategorie":"Lebewesen","Reichweite":"selbst","Probe":"IN/CH/GE","Wirkung":"Steht der Zauberer still, verschmelzen Körper und Kleidung optisch mit der Umgebung (jedoch nicht Rüstungen , Waffen oder andere Ausrüstung). Eine Entdeckung ist nur dann möglich, wenn eine Vergleichsprobe zwischen <em>Sinnesschärfe (Hinterhalte</em> <em>entdecken, Suchen </em>oder <em>Wahrnehmen) </em>erschwert um QS gegen <em>Verbergen </em>gelingt. Sobald er sich bewegt, verfällt die Tarnung.","Publikation":"Klingen der Nacht Seite 107","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Nachtalben u. a."},"spellclass":"Zauberspruch","name":"Chamaelioni","spellextensions":{"0":["Ausrüstung tarnen ","(FW 8, 2 AP): Auch Waffen bis zur Reichweite mittel, Rüstungen und Ausrüstungsstücke, die am Körper getragen werden, sind von der Tarnwirkung eingeschlossen."],"1":["Unsichtbarer Jäger ","(FW 12, 4 AP): Selbst bei Bewegung, also auch im Kampf, wirkt die Tarnung noch. Im Kampf sorgt dies für Erschwernisse von 2 auf AT gegen das Ziel."],"2":["Reichweite Berührung ","(FW 16, 6 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Daemonenschild.html","properties":{"Wirkungsdauer":"5 Minuten","Merkmal":"Antimagie","Zielkategorie":"Zone","Reichweite":"selbst","Probe":"MU/KL/CH","Wirkung":"Der Spruch erschafft eine unsichtbare, 3 Schritt im Radius messende Kuppel um den Magier herum. Die Kuppel ist eigentlich eine Kugel, da jedoch der Magier meistens auf dem Boden steht, ist die Kugelform nur selten relevant. Dämonen können in sie eindringen, aber alle ihre Angriffe (und somit die TP) werden innerhalb des Radius von der Magie der Kuppel absorbiert. Dämonen werden teilweise halbmateriell und schlagen durch ihre Gegner hindurch oder ihre Angriffe werden von der Kuppel aufgesaugt wie Wasser von einem Schwamm. Zu diesem Zweck verfügt die Kuppel über eine Schildstärke in Höhe von AsP + (QS x 3), die TP in gleicher Höhe zu absorbieren vermag. Fällt die Schildstärke unter 1, bricht die Kuppel zusammen und der Zauber endet.","Publikation":"Aventurische Magie 123","AsP-Kosten":"mindestens 4 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Dämonenschild","spellextensions":{"0":["Schutzhaut ","(FW 8, 2 AP): Die Schildstärke beträgt AsP + (QS x 4), aber nur der Zauberer selbst ist von der Schutzwirkung betroffen."],"1":["Großer Radius ","(FW 12, 4 AP): Der Radius beträgt 5 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP, die jedoch nicht die Schildstärke erhöhen."],"2":["Reichweite Berührung ","(FW 16, 6 AP): Als Reichweite gilt Berührung. Die Schildstärke beträgt nur QS x 2. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Corpofesso.html","properties":{"Wirkungsdauer":"QS x 2 in Kampfrunden","Merkmal":"Verwandlung","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"KL/IN/KO (modifiziert um ZK)","Wirkung":"Der Verzauberte erleidet schmerzhafte Muskelschwäche. Während der Wirkungsdauer erleidet er den Zustand Schmerz.<br/>QS 1: 1 Stufe Schmerz, aber nur für 1 KR<br/>QS 2: 1 Stufe Schmerz<br/>QS 3: 2 Stufen Schmerz<br/>QS 4: 3 Stufen Schmerz<br/>QS 5: 4 Stufen Schmerz<br/>QS 6: 4 Stufen Schmerz, aber für die doppelte Wirkungsdauer","Publikation":"Regelwerk Seite 289","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Corpofesso","spellextensions":{"0":["Verteilte Wirkung ","(FW 8, 3 AP): Die Zustandsstufen können auf mehrere Ziele verteilt werden, die vor dem Wirken des Zaubers benannt werden müssen. Kann der Zauberer z. B. 3 Stufen "],"1":["Längere Wirkung ","(FW 12, 6 AP): Die Wirkungsdauer steigt auf QS x 3 KR."],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 8 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Corpofrigo.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Lebewesen","Reichweite":"16 Schritt","Probe":"KL/CH/KO (erschwert um ZK)","Wirkung":"Der Corpofrigo erzeugt je nach QS Schaden und Stufen von Paralyse.<br/>QS 1: 1W6 TP<br/>QS 2: 1W6+2 TP<br/>QS 3: 1W6+2 TP, 1 Stufe Paralyse<br/>QS 4: 1W6+4 TP, 1 Stufe Paralyse<br/>QS 5: 1W6+6 TP, 1 Stufe Paralyse<br/>QS 6: 1W6+6 TP, 2 Stufen Paralyse<br/>Rüstungen schützen nicht vor diesem Zauber. Der Zauberspruch trifft sein Ziel automatisch und der Gegner kann weder ausweichen noch parieren.","Publikation":"Die Streitenden Königreiche Seite 162","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Corpofrigo","spellextensions":{"0":["Zielkategorie Objekt ","(FW 8, 2 AP): Der Zauber ist auch gegen Objekte einsetzbar. Sie erleiden Schaden, erhalten aber keine Zustandsstufen "],"1":["Fixiert ","(FW 12, 4 AP): Das Ziel erleidet bis zum Ende der nächsten KR den Status "],"2":["Humus-Zerstörer ","(FW 16, 6 AP): Gegen Ziele des Elements Humus können die TP verdoppelt werden. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Bannbaladin.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende, übernatürliche Wesen","Reichweite":"4 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Dieser ursprünglich elfische Freundschaftszauber weckt in seinem Ziel Sympathien und Freundschaft für den Zaubernden. Die genaue Wirkung ist davon abhängig, wie das Ziel zu dem Zaubernden steht. Pro QS verbessert sich die Verbindung zwischen den beiden um eine Stufe. Ist der Zaubernde dem Ziel vorher unbekannt, bewegt sich die Einstellung des Ziels meist zwischen Stufe 4 (Abneigung) und Stufe 6 (Zuneigung). Vorteile wie Gutaussehend oder Nachteile wie Vorurteile gegenüber dem Zaubernden können diesen ersten Eindruck je nach Situation um eine oder mehr Stufen modifizieren.<br/>Stufe 1: Das Ziel steht dem Zaubernden voller unstillbarem Hass gegenüber.<br/>Stufe 2: Das Ziel ist der erklärte Feind des Zaubernden.<br/>Stufe 3: Das Ziel verspürt dem Zaubernden gegenüber Abscheu und Ablehnung.<br/>Stufe 4: Das Ziel verspürt eine gewisse Abneigung für den Zaubernden.<br/>Stufe 5: Das Ziel ist dem Zaubernden gegenüber neutral.<br/>Stufe 6: Das Ziel verspürt eine gewisse Zuneigung für den Zaubernden.<br/>Stufe 7: Das Ziel empfindet Vertrauen und Freundschaft zum Zaubernden.<br/>Stufe 8: Das Ziel ist dem Zaubernden eng und loyal verbunden.<br/>Stufe 9: Das Ziel ist dem Zaubernden verfallen (geht für ihn jedoch nicht in den Tod).<br/>Der Zauberspruch verändert nicht das Erinnerungsvermögen. Dem Verzauberten ist nach Ende des Zaubers bewusst, was er empfunden und getan hat. Während der Zauberwirkung mag er sich des Zaubers bewusst sein, das beeinflusst aber seine neue Einstellung nicht. Handlungen des Zauberers hingegen können während der Wirkung das Verhältnis zum Verzauberten positiv oder negativ beeinflussen.","Publikation":"Regelwerk Seite 288","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Bannbaladin","spellextensions":{"0":["Gelungene Beeinflussung 1 ","(FW 8, 2 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "],"1":["Gelungene Beeinflussung 2 ","(FW 12, 4 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "],"2":["Gelungene Beeinflussung 3 ","(FW 16, 6 AP): Der Verzauberte kann nach dem Ende der Wirkungsdauer eine Probe auf "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Axxeleratus.html","properties":{"Wirkungsdauer":"QS x 5 Kampfrunden","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Die Bewegungen des Verzauberten werden übernatürlich beschleunigt. Dies bringt folgende Erleichterungen: +1 Verteidigung, GS x 2, INI-Basiswert x 2. Gegner können Attacken und Fernkampfangriffe des Beschleunigten schlechter abwehren und erhalten auf Verteidigung eine Erschwernis von 2.","Publikation":"Regelwerk Seite 288","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Elfen"},"spellclass":"Zauberspruch","name":"Axxeleratus","spellextensions":{"0":["Schneller ","(FW 8, 2 AP): Die GS erhöht sich auf GS x 3. Wann immer der Verzauberte sich mit mehr als GS x 2 fortbewegt, muss eine Probe auf "],"1":["Noch schneller ","(FW 12, 4 AP): Die GS erhöht sich auf GS x 4. Wann immer der Verzauberte sich mit mehr als GS x 2 fortbewegt, muss eine Probe auf "],"2":["Noch viel schneller ","(FW 16, 6 AP): Die GS erhöht sich auf GS x 5. Wann immer der Verzauberte sich mit mehr als GS x 2 fortbewegt, muss eine Probe auf "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Balsam.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Der Verzauberte erhält innerhalb von 6 Minuten nach dem Wirken des Zaubers verlorene LeP in Höhe der verwendeten AsP zurück. Der Zauberer kann maximal so viele AsP einsetzen, wie er FW hat. Für jede QS sinkt die Zeit um 1 Minute. Wird der Zauber vor dem Ablauf der durch den Konstitutionswert angegebenen Frist für den Tod eines Helden begonnen, kann er gerettet werden. Wird der Zauber jedoch unterbrochen, überlebt der Patient danach nur noch die verbliebenen Kampfrunden.","Publikation":"Regelwerk Seite 288","AsP-Kosten":"1 AsP pro LeP, mindestens jedoch 4 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"16 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Balsam Salabunde","spellextensions":{"0":["Zwei Ziele ","(FW 8, 2 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig heilen. Die AsP-Kosten müssen entsprechend für jedes Ziel einzeln entrichtet werden."],"1":["Vier Ziele ","(FW 12, 4 AP): Der Zauber kann bis zu 4 Ziele gleichzeitig heilen. Die AsP-Kosten müssen entsprechend für jedes Ziel einzeln entrichtet werden."],"2":["Zielkategorie Lebewesen ","(FW 16, 6 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Blitz.html","properties":{"Wirkungsdauer":"QS in Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"MU/IN/CH (modifiziert um SK)","Wirkung":"Der Verzauberte wird geblendet. Er erhält eine Stufe des Zustands Verwirrung.","Publikation":"Regelwerk Seite 289","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Blitz dich find","spellextensions":{"0":["Zwei Ziele ","(FW 8, 1 AP): Der Zauberer kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 2 AsP extra."],"1":["Vier Ziele ","(FW 12, 2 AP): Der Zauberer kann bis zu 4 Ziele gleichzeitig treffen. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "],"2":["Acht Ziele ","(FW 16, 3 AP): Der Zauberer kann bis zu 8 Ziele gleichzeitig treffen. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Aureolus.html","properties":{"Wirkungsdauer":"QS Stunden","Merkmal":"Illusion","Zielkategorie":"Objekte","Reichweite":"Berührung","Probe":"IN/CH/FF","Wirkung":"Das Objekt wird von einem Glanz überzogen, der es wertvoller und prachtvoller erscheinen lässt. Der Glanz stellt jedoch keine Lichtquelle dar. Maximal kann eine Fläche von einem Rechtschritt überzogen werden. Proben auf <em>Handel (Feilschen)</em>, um das verzauberte Objekt zu verkaufen, sind um QS/2 Punkte erleichtert. Dem Käufer steht nach dem Ende der Wirkungsdauer eine Probe auf <em>Willenskraft </em>zu. Gelingt die Probe, erkennt er, dass er betrogen wurde. Bei Misslingen empfindet er den Handel vielleicht als schlechtes Geschäft, aber nicht als Betrug. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":"Aventurische Magie 121","AsP-Kosten":"2 AsP (Kosten nicht modifizierbar)","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Aureolus","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 1 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 2 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 3 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AurisIllusionis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Zone (Radius von 5 x 5 Schritt)","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Mit diesem Illusionszauber ist die Täuschung des Gehörsinnes möglich. Es lassen sich Geräusche beliebiger Art erzeugen, die von allen Wesen in Hörreichweite wahrgenommen werden können. Die Lautstärke kann frei gewählt werden, vermag jedoch niemals ein Maß anzunehmen, das Schmerzen oder gar Schaden verursacht. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":"Aventurische Magie 121","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier, Scharlatane"},"spellclass":"Zauberspruch","name":"Auris Illusionis","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 2 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 4 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 6 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AugedesLimbus.html","properties":{"Wirkungsdauer":"QS Kampfrunden","Merkmal":"Sphären","Zielkategorie":"Zone","Reichweite":"32 Schritt","Probe":"MU/IN/KK","Wirkung":"Der Spruch erschafft einen Strudel in den angrenzenden Limbus. Der Zauberer muss einen Mittelpunkt innerhalb der Reichweite des Zauberspruchs benennen, um den herum sich die Zone mit einem Radius von QS Schritt ausbreitet. Alle darin befindlichen Wesen werden in den Limbus gezogen, und auch unbefestigte Objekte sind betroffen. Jede KR muss eine Probe auf <em>Kraftakt (Ziehen &amp; Zerren) </em>gelingen, ansonsten wird man 2 Schritt näher zum Zentrum gesogen. Sucht man Halt an einem befestigten Gegenstand, ist die <em>Kraftakt</em>-Probe um 1 erleichtert. Wer bei der Öffnung des Strudels direkt im Zentrum steht, kann eine Probe auf <em>Sinnesschärfe (Wahrnehmen)</em> erschwert um 3 ablegen. Bei Gelingen hat man sich intuitiv vom Sog wegbewegt und befindet sich 2 Schritt vom Zentrum entfernt. Wer sich im Radius des Sogs befindet, kann sich nicht normal von dem Zentrum wegbewegen.","Publikation":"Aventurische Magie 120","AsP-Kosten":"32 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"16 Aktionen","Verbreitung":"Gildenmagier, Kristallomanten"},"spellclass":"Zauberspruch","name":"Auge des Limbus","spellextensions":{"0":["Aus dem Limbus ","(FW 8, 4 AP): Der Zauber kann auch im Limbus gewirkt werden, sodass man stattdessen aus dem Limbus in die Dritte Sphäre gesogen wird."],"1":["Stärkerer Sog ","(FW 12, 8 AP): Die Proben auf "],"2":["Größerer Radius ","(FW 16, 12 AP): Der Radius des Zaubers wird verdoppelt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Aufwecken.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Heilung","Zielkategorie":"Lebewesen","Reichweite":"16 Schritt","Probe":"MU/IN/CH","Wirkung":"Ein schlafendes Ziel erwacht sofort aus seinen Träumen, und auch der Status <em>Bewusstlos </em>wird durch diesen Zauber aufgehoben. Das Ziel leidet aber danach für 6 – QS KR an 1 Stufe <em>Verwirrung</em>.","Publikation":"Aventurische Magie 119","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"2 Aktionen","Verbreitung":"Elfen, Gildenmagier, Hexen"},"spellclass":"Zauberspruch","name":"Aufwecken","spellextensions":{"0":["Zwei Ziele ","(FW 8, 1 AP): Der Zauberer kann bis zu 2 Ziele gleichzeitig wecken. Das zweite Ziel kostet 2 AsP extra."],"1":["Vier Ziele ","(FW 12, 2 AP): Der Zauberer kann bis zu 4 Ziele gleichzeitig wecken. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "],"2":["Massenerwachen ","(FW 16, 3 AP): Beliebig viele Ziele erwachen durch den Zauber. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Attributo_K%C3%B6rperkraft.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/IN/KK","Wirkung":"Durch den Attributo (Körperkraft) kann der Zauberer ein Ziel stärker und zäher machen. Für jede QS bekommt der Verzauberte einen Bonus. Die Boni sind kumulativ, das heißt bei QS 3 hat der Träger insgesamt KK +2 und ZK +1.<br/>1 QS: +1 KK<br/>2 QS: +1 ZK<br/>3 QS: +1 KK<br/>4 QS: +1 ZK<br/>5 QS: +1 KK<br/>6 QS: +1 ZK","Publikation":"Die Streitenden Königreiche Seite 162","AsP-Kosten":"8 (Aktivierung des Zaubers) + 4 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Attributo (Körperkraft)","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Eigenschaft steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Die SK steigt hingegen gar nicht, dafür dauert der Zauber nur 1 Aktion."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AttributoFingerfertigkeit.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/IN/FF","Wirkung":"Durch den Zauber wird das Ziel flinker und geschickter. Für jede erreichte QS bekommt der Verzauberte einen Bonus. Die Boni sind kumulativ, das heißt bei QS 3 hat der Träger insgesamt FF +2 und FK +1.<ul><li><strong>QS 1: </strong>+1 FF</li><li><strong>QS 2: </strong>+1 FK</li><li><strong>QS 3: </strong>+1 FF</li><li><strong>QS 4: </strong>+1 FK</li><li><strong>QS 5: </strong>+1 FF</li><li><strong>QS 6: </strong>+1 FK</li></ul>","Publikation":"Aventurische Magie 118","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Attributo (Fingerfertigkeit)","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Eigenschaft steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Die SK steigt hingegen gar nicht, dafür dauert der Zauber nur 1 Aktion."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zaubergilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AttributoCharisma.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/IN/CH","Wirkung":"Durch den Zauber wird das Ziel charismatischer und ansprechender. Für jede erreichte QS bekommt der Verzauberte einen Bonus. Die Boni sind kumulativ, das heißt, bei QS 3 hat der Träger insgesamt CH +2 und SK +1.<ul><li><strong>QS 1: </strong>+1 CH</li><li><strong>QS 2: </strong>+1 SK</li><li><strong>QS 3: </strong>+1 CH</li><li><strong>QS 4: </strong>+1 SK</li><li><strong>QS 5: </strong>+1 CH</li><li><strong>QS 6: </strong>+1 SK</li></ul>","Publikation":"Aventurische Magie 118","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Attributo (Charisma)","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Eigenschaft steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Die SK steigt hingegen gar nicht, dafür dauert der Zauber nur 1 Aktion."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zaubergilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AttributoKlugheit.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"KL/KL/IN","Wirkung":"Durch den Zauber wird das Ziel klüger und vernünftiger. Für jede erreichte QS bekommt der Verzauberte einen Bonus. Die Boni sind kumulativ, das heißt bei QS 3 hat der Träger insgesamt KL +2 und SK +1.<ul><li><strong>QS 1: </strong>+1 KL</li><li><strong>QS 2: </strong>+1 SK</li><li><strong>QS 3: </strong>+1 KL</li><li><strong>QS 4: </strong>+1 SK</li><li><strong>QS 5: </strong>+1 KL</li><li><strong>QS 6: </strong>+1 SK</li></ul>","Publikation":"Aventurische Magie 119","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro 10 Minuten","Steigerungsfaktor":"B","Zauberdauer":"8 Aktionen","Verbreitung":"Druiden, Gildenmagier"},"spellclass":"Zauberspruch","name":"Attributo (Klugheit)","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 2 AP): Der Zauber umfasst auch die Zielkategorie Lebewesen. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"1":["Schnellsteigerung ","(FW 12, 4 AP): Die Eigenschaft steigt bei QS 1-3 um 1, bei QS 4-6 um 2. Die SK steigt hingegen gar nicht, dafür dauert der Zauber nur 1 Aktion."],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Armatrutz.html","properties":{"Wirkungsdauer":"QS x 3 Minuten","Merkmal":"Heilung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/FF","Wirkung":"Die Haut des Zaubernden verhärtet sich, ohne ihre Flexibilität zu verlieren. So erhält er einen natürlichen Rüstungsschutz, der sich auf getragene Rüstung aufaddiert, ohne die Belastung zu erhöhen. Die Höhe des gewünschten zusätzlichen Rüstungsschutzes muss der Zaubernde vor der Probe festlegen. Maximal ist ein zusätzlicher RS von 3 möglich.","Publikation":"Regelwerk Seite 288","AsP-Kosten":"4 AsP für RS 1, 8 AsP für RS 2, 16 AsP für RS 3 (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"1 Aktion","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Armatrutz","spellextensions":{"0":["Zielkategorie Objekt ","(FW 8, 3 AP): Ein Objekt von maximal der Größe eines durchschnittlichen Hauses erhält RS, der vor Strukturschaden schützt."],"1":["Harte Schale ","(FW 12, 6 AP): Der Zauberer kann auch einen RS von 4 verleihen. Die Kosten dafür betragen 32 AsP (nicht modifizierbar)."],"2":["Reichweite Berührung ","(FW 16, 9 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden. Die Kosten steigen dabei um eine Kategorie (von 4 auf 8, von 8 auf 16 usw.)."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_AromatisIllusionis.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Illusion","Zielkategorie":"Zone (Radius 3 x 3 Schritt)","Reichweite":"8 Schritt","Probe":"KL/IN/CH","Wirkung":"Mit diesem Illusionszauber lassen sich beliebige Gerüche und Geschmäcker erzeugen, die jedoch niemals so intensiv sein können, dass sie bei betroffenen Personen Übelkeit hervorrufen könnten. Details zum Thema Illusionen finden sich im <strong>Regelwerk </strong>auf Seite <strong>259</strong>.","Publikation":"Aventurische Magie 118","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Aromatis Illusionis","spellextensions":{"0":["Gute Illusion 1 ","(FW 8, 1 AP): Die "],"1":["Gute Illusion 2 ","(FW 12, 2 AP): Die "],"2":["Gute Illusion 3 ","(FW 16, 3 AP): Die "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Atemnot.html","properties":{"Wirkungsdauer":"QS in Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"KL/CH/KO (modifiziert um ZK)","Wirkung":"Der Verzauberte leidet augenblicklich unter Atemnot. Er erhält eine Stufe des Zustands Betäubung.","Publikation":"Die Streitenden Königreiche Seite 161","AsP-Kosten":"4 AsP","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Atemnot","spellextensions":{}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Archosphaero.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Zone","Reichweite":"32 Schritt","Probe":"MU/KL/KO","Wirkung":"Aus den Händen des Zaubernden schießt ein Erzball, der in gerader Linie sein Ziel trifft. Das Treffen ist in der Zauberdauer inbegriffen, der Magier muss also keine zusätzliche Aktion aufwenden. Der Erzball explodiert, wenn er sein Ziel oder auf ein großes, festes Hindernis trifft (z. B. eine Wand oder einen Schild). Verlässt der Erzball die Reichweite, ohne ein Ziel zu treffen, so löst er sich auf. Auch die Gefährten des Zauberers können getroffen werden, wenn der Erzball explodiert. Der Radius der Explosion beträgt 5 Schritt. Der Schaden beträgt einen Schritt um das Zentrum der Explosion herum 2W6 + (QS x 3) Trefferpunkte (auch gegen leblose Objekte als Strukturschaden). Pro Schritt Entfernung von diesem Bereich verringert sich der Schaden um 3 TP. Der Erzball trifft automatisch, wenn man ihm nicht aktiv zu entgehen versucht. Dazu muss eine Verteidigung eingesetzt und eine Probe auf <em>Körperbeherrschung</em> <em>(Kampfmanöver) </em>abgelegt werden. Pro QS hat der Held sich 1 Schritt weit weg vom Zentrum der Explosion bewegt. Trifft der Erzball sein Ziel, werden die TP durch den RS des Ziels vermindert. Alle getroffenen Ziele erhalten außerdem bei 1-3 auf 1W6 1 Stufe des Zustands <em>Belastung</em>, der erst nach 5 Minuten wieder endet.","Publikation":"Aventurische Magie 117","AsP-Kosten":"32 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"D","Zauberdauer":"4 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Archosphaero","spellextensions":{"0":["Intensive Explosion ","(FW 8, 4 AP): Es muss nicht gewürfelt werden, ob die Ziele 1 Stufe "],"1":["Schneller Ball ","(FW 12, 8 AP): Die Ziele können schlechter ausweichen. Das Ausweichen ist zusätzlich um 1 erschwert. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Großer Radius ","(FW 16, 12 AP): Der Radius der Explosion beträgt 7 Schritt. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Archofaxius.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"alle","Reichweite":"16 Schritt","Probe":"MU/KL/CH","Wirkung":"Aus den Fingern des Zaubernden schießt ein Erzstrahl, der in gerader Linie sein Ziel trifft. Der Magier muss keine zusätzliche Aktion aufwenden, um nach dem Wirken des Zaubers zu treffen. Das Treffen ist in der Zauberdauer inbegriffen. Das getroffene Ziel erleidet 2W6 + (QS x 2) Trefferpunkte Schaden. Der Erzstrahl zählt als Fernkampfangriff mit einer Schusswaffe und kann entsprechend geblockt werden, und auch ein Ausweichen ist möglich. An Schilden erzeugt er Strukturschaden, wenn er auf sie trifft. Der Strahl trifft automatisch, wenn man sich nicht verteidigt. Trifft der Erzstrahl sein Ziel, werden die TP durch den RS des Ziels vermindert. Das Ziel erhält zudem bei 1-3 auf 1W6 1 Stufe des Zustands <em>Belastung</em>, der erst nach 5 Minuten wieder endet.","Publikation":"Aventurische Magie 117","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden"},"spellclass":"Zauberspruch","name":"Archofaxius","spellextensions":{"0":["Intensiver Strahl ","(FW 8, 3 AP): Es muss nicht gewürfelt werden, ob das Ziel 1 Stufe "],"1":["Windender Strahl ","(FW 12, 6 AP): Das Ziel kann schlechter ausweichen. Das Ausweichen ist zusätzlich um 2 erschwert. Es entstehen zusätzliche Kosten in Höhe von 2 AsP."],"2":["Zwei Ziele ","(FW 16, 9 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Analys.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Hellsicht","Zielkategorie":"Wesen, Objekte","Reichweite":"Berührung","Probe":"KL/KL/IN","Wirkung":"Der Zauberer erhält detaillierte Hinweise, ob das Objekt magisch ist und falls ja, wie seine magische Struktur beschaffen ist. Durch die so gesammelten Informationen kann der Zauberer bei einer Magischen Analyse mehr QS ansammeln als üblich. Jede QS des Analys entspricht der Höhe der maximal ansammelbaren QS der Probe auf Magiekunde (unterschiedliche Anwendungsgebiete) für die Magische Analyse. Hat der Zauberer z. B. 3 QS im Analys erzielt, kann er maximal 3 QS bei der Magischen Analyse erzielen, hat er eine QS von 4, so sind maximal 4 QS bei der Analyse möglich usw. Mehr zur Magischen Analyse siehe Seite 269.","Publikation":"Regelwerk Seite 287","AsP-Kosten":"16 AsP","Steigerungsfaktor":"C","Zauberdauer":"32 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Analys Arkanstruktur","spellextensions":{"0":["Zwei Gegenstände ","(FW 8, 3 AP): Der Zauberer kann bis zu 2 Gegenstände gleichzeitig analysieren."],"1":["Drei Gegenstände ","(FW 12, 6 AP): Der Zauberer kann bis zu 3 Gegenstände gleichzeitig analysieren. Voraussetzung: Zaubererweiterung "],"2":["Vier Gegenstände ","(FW 16, 9 AP): Der Zauberer kann bis zu 4 Gegenstände gleichzeitig analysieren. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Altisonus.html","properties":{"Wirkungsdauer":"QS x 5 Minuten","Merkmal":"Heilung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"MU/CH/KO","Wirkung":"Die Stimme des Zauberers wird deutlich lauter und beeindruckender und vermag jede andere in einem Raum zu übertönen. Die Reichweite seiner Stimme verdoppelt sich.","Publikation":"Aventurische Magie 116","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"4 Aktionen","Verbreitung":"Scharlatane"},"spellclass":"Zauberspruch","name":"Altisonus","spellextensions":{"0":["Reichweite Berührung ","(FW 8, 1 AP): Als Reichweite gilt Berührung. Der Zauberspruch kann nur auf Kulturschaffende übertragen werden."],"1":["Noch weiter ","(FW 12, 2 AP): Die Reichweite der Stimme reicht viermal so weit wie normal. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."],"2":["Donnerstimme ","(FW 16, 3 AP): In einem Radius von 10 Schritt um den Sprecher empfinden Lebewesen die Lautstärke als derart unangenehm, dass sämtliche Fertigkeitsproben für sie um 1 erschwert sind, solange der Zauberer spricht."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_%C3%84ngste_lindern.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Einfluss","Zielkategorie":"Kulturschaffende","Reichweite":"Berührung","Probe":"MU/IN/CH","Wirkung":"je nach QS können mit diesem Zauberspruch Stufen von Furcht augenblicklich abgebaut werden. Die Wirkung ist nicht kumulativ, es gelten die Auswirkungen der höchsten QS.<br/>QS 1: 1 Stufe Furcht<br/>QS 2: 1 Stufe Furcht, +1 MU für 1 Minute<br/>QS 3: 2 Stufen Furcht, +1 MU für 1 Minute<br/>QS 4: 3 Stufen Furcht, +1 MU für 1 Minute<br/>QS 5: 4 Stufen Furcht, +1 MU für 1 Minute<br/>QS 6: 4 Stufen Furcht, +2 MU für 1 Minute","Publikation":"Die Streitenden Königreiche Seite 161","AsP-Kosten":"8 AsP","Steigerungsfaktor":"B","Zauberdauer":"4 Aktionen","Verbreitung":"Hexen"},"spellclass":"Zauberspruch","name":"Ängste lindern","spellextensions":{"0":["Größere Reichweite ","(FW 8, 2 AP): Der Zauber hat die Reichweite 2 Schritt."],"1":["Verteilte Wirkung ","(FW 12, 4 AP): Die Senkung der Zustandsstufe "],"2":["Zwei Ziele ","(FW 16, 6 AP): Der Zauber kann auf bis zu 2 Ziele gleichzeitig gesprochen werden. Das zweite Ziel kostet 4 AsP extra."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Angstausloesen.html","properties":{"Wirkungsdauer":"QS x 2 KR","Merkmal":"Dämonisch","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/CH/GE","Wirkung":"Alle Kulturschaffenden in einem Radius von QS x 2 Schritt um den Zauberer werden von panischer Angst vor ihm erfüllt. Solange sie sich im Wirkungsradius aufhalten, erleiden sie 2 Stufen des Zustands <em>Furcht</em>. Die Opfer müssen den Zauberer nicht sehen, damit der Spruch seine Wirkung entfaltet. Ist ihre SK aber höher als die QS des Zaubers, sind sie von den Auswirkungen nicht betroffen.","Publikation":"Aventurische Magie 116","AsP-Kosten":"16 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"C","Zauberdauer":"4 Aktionen","Verbreitung":"Gildenmagier"},"spellclass":"Zauberspruch","name":"Angst auslösen","spellextensions":{"0":["Zielkategorie Lebewesen ","(FW 8, 3 AP): Die Zielkategorie umfasst Lebewesen."],"1":["Längere Wirkungsdauer ","(FW 12, 6 AP): Die Wirkungsdauer beträgt QS x 3 KR."],"2":["Größerer Radius ","(FW 16, 9 AP): Der Radius beträgt QS x 3 Schritt."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Aeolito.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Elementar","Zielkategorie":"Lebewesen","Reichweite":"16 Schritt","Probe":"KL/CH/KO (modifiziert um ZK)","Wirkung":"Der Aeolito erschafft einen stürmischen Wind, der beim Ziel je nach QS Schaden und den Status <em>Liegend </em>verursacht.<ul><li><strong>QS 1: </strong>1W6 TP</li><li><strong>QS 2: </strong>1W6+2 TP</li><li><strong>QS 3: </strong>1W6+2 TP, <em>Liegend</em></li><li><strong>QS 4: </strong>1W6+4 TP, <em>Liegend</em>, 1 Schritt zurückgeworfen</li><li><strong>QS 5: </strong>1W6+6 TP, <em>Liegend</em>, 2 Schritt zurückgeworfen</li><li><strong>QS 6: </strong>1W6+6 TP, <em>Liegend</em>, 3 Schritt zurückgeworfen</li><li>Rüstungen schützen nicht vor diesem Zauber. Der Zauberspruch trifft sein Ziel automatisch. Der Gegner kann weder ausweichen noch parieren.</li></ul>","Publikation":"Aventurische Magie 115","AsP-Kosten":"8 AsP (Kosten sind nicht modifizierbar)","Steigerungsfaktor":"B","Zauberdauer":"1 Aktion","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Aeolito","spellextensions":{"0":["Zielkategorie Objekt ","(FW 8, 2 AP): Der Zauber ist auch gegen Objekte einsetzbar. Sie erleiden Schaden, erhalten aber nicht den Status "],"1":["Weites Zurückwerfen ","(FW 12, 4 AP): Die Distanz beim Zurückwerfen wird verdoppelt."],"2":["Erz-Zerstörer ","(FW 16, 6 AP): Gegen Ziele des Elements Erz können die TP verdoppelt werden. Es entstehen zusätzliche Kosten in Höhe von 4 AsP."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Alpgestalt.html","properties":{"Wirkungsdauer":"QS Kampfrunden","Merkmal":"Einfluss","Zielkategorie":"Lebewesen","Reichweite":"8 Schritt","Probe":"MU/CH/GE (modifiziert um SK)","Wirkung":"Den Verzauberten befällt Angst und er erhält eine Stufe des Zustands <em>Furcht</em>.","Publikation":"Aventurische Magie 116","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"1 Aktion","Verbreitung":"Druiden, Hexen, Scharlatane"},"spellclass":"Zauberspruch","name":"Alpgestalt","spellextensions":{"0":["Zwei Ziele ","(FW 8, 1 AP): Der Zauber kann bis zu 2 Ziele gleichzeitig treffen. Das zweite Ziel kostet 2 AsP extra."],"1":["Vier Ziele ","(FW 12, 2 AP): Der Zauber kann bis zu 4 Ziele gleichzeitig treffen. Voraussetzung: Zaubererweiterung "],"2":["Acht Ziele ","(FW 16, 3 AP): Der Zauber kann bis zu 8 Ziele gleichzeitig treffen. Jedes Ziel nach dem ersten kostet 2 AsP extra. Voraussetzung: Zaubererweiterung "]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Adlerauge.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Heilung","Zielkategorie":"Wesen","Reichweite":"selbst","Probe":"KL/IN/FF","Wirkung":"Das Talent Sinnesschärfe wird während der Wirkungsdauer um QS +3 des Zaubers erhöht.","Publikation":"Regelwerk Seite 287","AsP-Kosten":"4 AsP (Aktivierung des Zaubers) + 2 AsP pro 5 Minuten","Steigerungsfaktor":"B","Zauberdauer":"2 Aktionen","Verbreitung":"Druiden, Elfen"},"spellclass":"Zauberspruch","name":"Adlerauge","spellextensions":{"0":["Einzelsinn schärfen ","(FW 8, 2 AP): Nur ein Einzelsinn (Sicht, Gehör, Geruch & Geschmack, Tastsinn) ist vom Zauber betroffen. Der Zauberer erhält auf entsprechende Proben einen Bonus von QS +4 auf "],"1":["Spurensuchen ","(FW 12, 4 AP): Bei Proben auf "],"2":["Keine Aufrechterhaltung ","(FW 16, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Abvenenum.html","properties":{"Wirkungsdauer":"sofort","Merkmal":"Objekt","Zielkategorie":"Objekte","Reichweite":"1 Schritt","Probe":"KL/IN/FF","Wirkung":"Bis zu 5 Stein an Getränken und Speisen werden von allen Giften und Krankheitskeimen befreit, deren Stufe die QS nicht übersteigt. Ist bereits jemand durch die Speisen oder Getränke vergiftet worden, wirkt der Abvenenum bei ihm nicht mehr.","Publikation":"Aventurische Magie 114","AsP-Kosten":"4 AsP","Steigerungsfaktor":"A","Zauberdauer":"8 Aktionen","Verbreitung":"Druiden, Elfen, Gildenmagier"},"spellclass":"Zauberspruch","name":"Abvenenum","spellextensions":{"0":["Ausnahme ","(FW 8, 1 AP): Der Zauberer kann entscheiden, dass Alkohol und andere Rauschmittel nicht neutralisiert werden. Er kann die einzelnen Substanzen benennen, die nicht herausgefiltert werden sollen."],"1":["Größerer Umfang ","(FW 12, 2 AP): Der Zauberer kann größere Mengen an Getränken und Speisen verzaubern. Es entstehen zusätzliche Kosten in Höhe von 2 AsP pro weiteren angefangenen 5 Stein."],"2":["Tadelloser Vorkoster ","(FW 16, 3 AP): Für jeweils 4 zusätzliche AsP kann die QS bei einer gelungenen Probe um 1 erhöht werden (bis zu einem Maximum von QS 6)."]}},{"link":"http://www.ulisses-regelwiki.de/index.php/index.php/ZS_Adlerschwinge.html","properties":{"Wirkungsdauer":"aufrechterhaltend","Merkmal":"Verwandlung","Zielkategorie":"Kulturschaffende","Reichweite":"selbst","Probe":"MU/IN/GE","Wirkung":"Der Zauberer verwandelt sich in ein Fluglebewesen seiner Wahl, das jedoch maximal die Größenkategorie klein aufweisen darf. Während der Wirkungsdauer verwendet der Zauberer die körperlichen Werte des Tieres, einschließlich seiner Eigenschaften, Talente und Kampfwerte. Eine niedrige LE wird bei der Verwandlung anteilsmäßig angerechnet, gleiches geschieht bei der Rückverwandlung. Die geistigen Werte des Zauberers bleiben ebenso wie sein Bewusstsein erhalten. Die Verwandlung betrifft nur den Zauberer selbst, nicht seine Kleidung oder Ausrüstung. Es ist keine Verwandlung in ein kulturschaffendes Wesen möglich.","Publikation":"Aventurische Magie 115","AsP-Kosten":"8 AsP (Aktivierung des Zaubers) + 4 AsP pro Stunde","Steigerungsfaktor":"C","Zauberdauer":"8 Aktionen","Verbreitung":"allgemein"},"spellclass":"Zauberspruch","name":"Adlerschwinge","spellextensions":{"0":["Mittlere Tiere ","(FW 8, 3 AP): Der Zauberer kann sich auch in Fluglebewesen der Größenkategorie mittel verwandeln. Es entstehen zusätzliche Kosten in Höhe von 5 AsP."],"1":["Keine Aufrechterhaltung ","(FW 12, 6 AP): Der Zauber gilt nicht als aufrechterhaltend. Die Wirkungsdauer und damit die AsP-Kosten müssen beim Sprechen des Zaubers festgelegt werden."],"2":["Große Tiere ","(FW 16, 9 AP): Der Zauberer kann sich auch in Fluglebewesen der Größenkategorie groß verwandeln. Es entstehen zusätzliche Kosten in Höhe von 6 AsP. Voraussetzung: Zaubererweiterung "]}}]});

